<template>
  <div id="Course">
    <panel-title :crumbs="crumbs"></panel-title>
    <div class="mytheme-container-mg-30">
      <!--<Card class="mg-bottom-20" dis-hover>-->
      <!--<div class="tip-box">-->
      <!--<ul>-->
      <!--<li>-->
      <!--您可以提前上传课件， 创建教室或课程时可以关联课件， 进入直播教室时， 课件会自动关联到教室， 无需再次上传；-->
      <!--</li>-->
      <!--<li>-->
      <!--我们支持多种格式的课件， 如( *-->
      <!--.xls;*.xlsx;*.ppt;*.pptx;*.doc;*.docx;*.txt;*.pdf;*.jpg;*.gif;*.jpeg;*.png;*.bmp;*.mp3;*.mp4)，-->
      <!--但要求单个课件不超过100M；-->
      <!--</li>-->
      <!--</ul>-->
      <!--</div>-->
      <!--</Card>-->
      <Card class="mytheme-noborder mytheme-card-padding noradius-card mytheme-content-minheight" dis-hover>
        <div class="mater-head mytheme-mg-top-30">
          <span class="mytheme-input-label">{{$t('Main.MaterMessanage.InputSearch')}}{{$t('Main.Symbols.Colon')}}</span>
          <Input
            class="input-max-height input-normal-width"
            v-model.trim="InputText"
            :placeholder="$t('Main.MaterMessanage.KeyWords')"
            @on-click="getMaterInfo(current, InputText)"
            @on-enter="getMaterInfo(current, InputText)"
          />
          <Button class="mytheme-mg-left-70" type="primary" @click="getMaterInfo(current, InputText)">搜索</Button>
          <Button class="mytheme-mg-left-70" type="warning" @click="isShowAddFileModal = true;">
            {{$t('Main.MaterMessanage.Upload')}}
          </Button>
        </div>
        <i-table
          class="mytheme-table thead-max-height tbody-min-height noborder-table mytheme-mg-top-30"
          :stripe="true"
          :disabled-hover="true"
          :loading="loading"
          ref="selection"
          :columns="columns1"
          :data="data1"
          :no-data-text="$t('Main.MaterMessanage.NoData')"
        >
        </i-table>
        <pagination
          class="mytheme-mg-top-20"
          v-if="total > 0"
          :total="total"
          :current-page='current'
          :display="display"
          @pagechange="pagechange"
        ></pagination>
      </Card>
    </div>
    <!--删除文件-->
    <Modal class="mytheme-modal" v-model="DeleteModal" :scrollable="true">
      <div class="modal-head blue" slot="header">{{$t('Main.ModalTip')}}</div>
      <div class="modal-body">
        <div>{{$t('Main.AreYouSureWantToDelMater')}}{{$t('Main.Symbols.QuestionMark')}}</div>
      </div>
      <div class="modal-foot" slot="footer">
        <Button class="modal-foot-btn mytheme-btn" type="primary" @click="deleteFile()">
          {{$t('Main.SaveBtn')}}
        </Button>
        <Button class="modal-foot-btn mytheme-btn" type="default" @click="DeleteModal = false;">
          {{$t('Main.CancelBtn')}}
        </Button>
      </div>
    </Modal>
    <!--上传素材-->
    <Modal class="mytheme-modal mytheme-pd-bottom-30" v-model="isShowAddFileModal" :scrollable="true">
      <div class="modal-head" slot="header">上传素材</div>
      <div class="modal-body">
        <RadioGroup style="margin-left: 40px" v-model="modalIndex" @on-change="changeHost(modalIndex)">
          <Radio label=1 style="margin-right: 40px">录播素材</Radio>
          <Radio label=2>直播素材</Radio>
        </RadioGroup>
        <Form class="mytheme-mg-top-30" :label-width="150">
          <FormItem label="选择上传课件：">
            <div class="uploadbox">
              <Upload
                ref="upload"
                multiple
                :show-upload-list="false"
                :data="fileData"
                :action="host"
                name="uploadFile"
                :on-success="uploadSuccess"
                :on-error="uploadefaild"
                :on-format-error="fomatError"
                @click.native="setHeader"
                :headers="head"
                style="display: inline-block; width: 100%; height: 100%;"
                :format="format"
                :accept="accept"
                :max-size="102400">
                <img :src="uploadImg">
              </Upload>
            </div>
            <!--上传文件进度条-->
            <template>
              <Progress
                v-if="el.showProgress"
                v-for="(el, i) in uploadList"
                :percent="el.percentage"
                :key="i"
              ></Progress>
            </template>
          </FormItem>
        </Form>
        <div class="color-hui" style="margin-left: 40px; font-size: 12px;">{{uploadTip}}</div>
      </div>
      <div class="modal-foot" slot="footer">
        <Button class="modal-foot-btn mytheme-btn" type="default" @click="isShowAddFileModal = false;">
          {{$t('Main.CancelBtn')}}
        </Button>
      </div>
    </Modal>
    <!-- 素材预览 -->
    <Modal
      v-model="viewModal"
      class="mytheme-modal"
      width="660">
      <div slot="header">素材预览</div>
      <div class="content">
        <iframe v-if="typeFlag == 1" :src="pathUrl" width="600" frameborder="no" border="0"></iframe>
        <div v-if="typeFlag == 2" class="img-wrap"><img :src="pathUrl" alt=""></div>
        <video v-if="typeFlag == 3" autoplay="autoplay" constrols="controls" width="600" muted="muted" loop="loop" :src="pathUrl">
        </video>
        <audio v-if="typeFlag == 4" controls="controls" autoplay="autoplay" muted="muted" loop="loop" width="600" :src="pathUrl">
        </audio>
        <div v-if="typeFlag == 5" class="noData">该格式暂不支持预览</div>
      </div>
      <div slot="footer">
        <Button class="modal-foot-btn mytheme-btn" type="primary" @click="viewModal = false">确认</Button>
        <Button class="modal-foot-btn mytheme-btn" type="default" @click="viewModal = false">取消</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import pagination from "@/components/pagination";
import Server from "@/service/request";
import Path from "@/service/Path";
import {
  Card,
  Input,
  Table,
  Button,
  Modal,
  Form,
  Row,
  Col,
  Upload,
  Progress,
  Radio,
  RadioGroup,
  FormItem
} from "iview";
import md5 from "md5";
import sha1 from "sha1";
import uploadImg from "@/assets/images/upload.png";
import mediaFormat from "@/assets/js/mediaFomat.js";

export default {
  name: "",
  components: {
    panelTitle,
    Card,
    Input,
    "i-table": Table,
    pagination,
    Button,
    Modal,
    Form,
    Row,
    Col,
    Upload,
    Progress,
    Radio,
    RadioGroup,
    FormItem
  },
  data() {
    return {
      uploadImg,
      crumbs: [
        {
          name: "",
          route: ""
        },
        {
          name: this.$t("Main.MaterMessanage.Title"),
          route: this.$route.query.route
        },
        {
          name: this.$t("Main.MaterMessanage.MaterList"),
          route: this.$route.fullPath
        }
      ],
      DeleteModal: false,
      loading: false,
      total: 0, // 记录总条数
      display: 20, // 每页显示条数
      current: 1, // 当前的页数
      InputText: "",
      teacherid: 0,
      columns1: [
        {
          title: this.$t("Main.MaterMessanage.No"), //编号
          align: "center",
          key: "fileid"
        },
        {
          title: this.$t("Main.MaterMessanage.MaterName"), //资源名称
          align: "center",
          key: "showname"
        },
        {
          title: "用途",
          align: "center",
          render: (h, params) => {
            return h("div", params.row.usetype == 1 ? "录播" : "直播");
          }
        },
        {
          title: `${this.$t("Main.MaterMessanage.Size")}(MB)`, //文件大小
          align: "center",
          render: (h, params) => {
            return h("div", params.row.sizes);
          }
        },
        {
          title: this.$t("Main.MaterMessanage.AddTime"),
          align: "center",
          key: "addtimestr"
        },
        {
          title: '转换状态',
          align: "center",
          // render: (h, params) => {
          //   let that = this;
          //   function renderSex(param) { 
          //     if (param == 0) {
          //       return '转换中';
          //     } else if (param == 1) {
          //       return '成功';
          //     } else if (param == 2) {
          //       return '失败';
          //     }
          //   }
          //   // return h("div", renderSex(params.row.sex));
          // }
          render: (h, params, index) => {
            let _val = "";
            if(params.row.previewpath == ""){
              _val = "失败"
            }else{
              _val = "成功"
            }
            return h('div', {  
              props: {
                },
            }, _val)
          }
        },
        {
          title: this.$t("Main.MaterMessanage.Option"),
          align: "center",
          key: "Make",
          render: (h, params, index) => {
            return h("div", [
              h(
                "div",
                {
                  on: {
                    click: value => {
                      this.openViewModal(params.row.fileid, params.row.previewpath, params.row.showname);
                    }
                  },
                  style: {
                    cursor: "pointer",
                    color: "rgb(255, 185, 50)",
                    display: "inline-block",
                    marginRight: "15px"
                  },
                },
                '预览'
              ),
              h(
                "div",
                {
                  on: {
                    click: value => {
                      this.DeleteModal = true;
                      this.fileId = params.row.fileid;
                    }
                  },
                  style: {
                    cursor: "pointer",
                    color: "red",
                    display: "inline-block"
                  },
                  attrs: {
                    class: "tablebtn red"
                  }
                },
                this.$t("Main.MaterMessanage.DeleteTextBtn")
              )
            ]);
          }
        }
      ],
      data1: [],
      fatherid: 0,
      fileId: 0,
      head: {
        sign: "",
        token: "",
        starttime: ""
      },
      fileData: {},
      upload: {
        fatherid: "",
        allpathnode: "1,0,3"
      },
      uploadb: {
        fatherid: ""
      },
      uploadList: [],
      modalIndex: "1",
      format: mediaFormat.VideoBroadcasting,
      accept: "video/*",
      host:
        window.origin === "http://192.168.0.206:8080" ||
        window.origin === "http://192.168.0.206:8081"|| window.origin === "http://192.168.0.95:8080"
          ? "http://www.lemonedu.com/" + Path.Upload_uploadc
          : "/" + Path.Upload_uploadc,
      filetype: this.$route.query.filetype,
      uploadTip: "（支持上传mp4、WebM、Ogg格式课件，大小不超过100M）",
      isShowAddFileModal: false,
      viewModal: false,
      pathUrl: "",
      typeFlag: 0
    };
  },
  watch: {
    isShowAddFileModal(val) {
      if (!val) {
        this.fileData = this.upload;
      }
    }
  },
  created() {
    if (this.$Utils.isLogin()) {
      this.teacherid = this.$Utils.getUserInfo().uid;
      this.organid = this.$Utils.getUserInfo().organid;
    } else {
      this.$router.replace("/Login");
      return;
    }
    this.fileData.fatherid = this.$route.params.id;
    this.upload.fatherid = this.$route.params.id;
    this.uploadb.fatherid = this.$route.params.id;

    //默认录播课
    this.fileData = this.upload;

    this.crumbs[0].route = this.$route.fullPath;
    this.crumbs[2].name = this.$route.query.prevText;
    this.getMaterInfo();
    this.fatherid = this.$route.params.fileid;
  },
  mounted() {
    this.uploadList = this.$refs.upload.fileList;
  },
  methods: {
    /**
     * 上传文件验证格式错误提示
     */
    fomatError() {
      this.$Message.error("格式不支持");
    },
    /**
     * 切换个人 / 公共资源
     */
    changeHost(param) {
      if (param == 1) {
        //录播
        this.fileData = this.upload;
        this.host =
          window.origin === "http://192.168.0.206:8080" ||
          window.origin === "http://192.168.0.206:8081"|| window.origin === "http://192.168.0.95:8080"
            ? "http://www.lemonedu.com/" + Path.Upload_uploadc
            : "/" + Path.Upload_uploadc;
        this.format = mediaFormat.VideoBroadcasting;
        this.uploadTip = "（支持上传mp4、WebM、Ogg格式课件，大小不超过100M）";
        this.accept = "video/*";
      } else if (param == 2) {
        //直播
        this.fileData = this.uploadb;
        this.host =
          window.origin === "http://192.168.0.206:8080" ||
          window.origin === "http://192.168.0.206:8081"|| window.origin === "http://192.168.0.95:8080"
            ? "http://www.lemonedu.com/" + Path.Upload_Uploadb
            : "/" + Path.Upload_Uploadb;
        this.format = mediaFormat.LiveBroadcast;
        this.uploadTip =
          "（支持上传word、Excel、PPT、pdf、MP3、mp4、jpg等格式课件）";
        this.accept = "";
      }
    },
    /**
     * 设置请求头
     */
    setHeader() {
      var token = JSON.parse(this.$loca.getItem("teacher_info")).token;
      var starttime = Date.parse(new Date());
      this.head = {
        sign: this.getStr(
          this.fileData,
          this.$loca.getItem("teacher_key"),
          starttime,
          token
        ),
        token: token,
        starttime: starttime
      };
    },
    /**
     * md5加密
     * @param {Object} 上传文件的文件id
     * @param {String} 用户登录后获取的key
     * @param {String} 当前时间戳
     * @param {String} 用户登陆后获取的token
     * @return {String} 加密后的字符串
     */
    getStr(arr, key, starttime, token) {
      var tostr = arr ? this.getStrings(arr, "") : "";
      return md5(sha1(md5(tostr) + md5(key) + starttime) + token);
    },
    /**
     * 获取随机字符串
     * @param  {Object} 上传文件的文件id
     * @param  {String} str  空字符串
     * @return {String}      随机字符串
     */
    getStrings(data, str) {
      for (var x in data) {
        if (this.isJson(data[x])) {
          var i = 0;
          for (var k in data[x]) {
            i++;
            break;
          }
          if (i > 0) {
            str += x;
            str = this.getStrings(data[x], str);
          }
        } else if (this.isArray(data[x])) {
          if (data[x].length != 0) {
            str += x;
            str = this.getStrings(data[x], str);
          }
        } else {
          if (
            data[x] !== undefined &&
            data[x] !== null &&
            data[x] !== "" &&
            data[x] !== false &&
            data[x] !== true
          ) {
            str += x.toString() + data[x].toString();
          }
        }
      }
      return str;
    },
    /**
     * 判断是否为数组
     * @param  {Array}  o 数组对象
     * @return {Boolean}  判断数组是否为真
     */
    isArray(o) {
      return Object.prototype.toString.call(o) == "[object Array]";
    },
    /**
     * 判断是否为对象
     * @param  {Object}  obj 对象
     * @return {Boolean}     判断对象是否为真
     */
    isJson(obj) {
      return (
        typeof obj == "object" &&
        Object.prototype.toString.call(obj).toLowerCase() ==
          "[object object]" &&
        !obj.length
      );
    },
    /**
     * 课件上传成功
     * @param  {Object} file 上传成功后返回值
     */
    uploadSuccess(file) {
      if (file.code === 0) {
        this.$Message.success(this.$t("Main.UploadSuccess"));
        this.getMaterInfo();
        this.isShowAddFileModal = false;
        this.fileData = this.upload;
      } else if (file.code === 29007) {
        this.$Message.warning({
          content: "请检查文件名，文件名不得包含特殊符号及空格!",
          duration: 5
        });
      } else {
        this.$Message.warning(this.$t("Main.UploadFaild"));
      }
    },
    // 上传失败
    uploadefaild() {
      this.$Message.error(this.$t("Main.UploadFaild"));
    },
    /**
     * 获取切换后的页码
     * @param  {Number} currentPage 目标页码
     */
    pagechange: function(currentPage) {
      this.current = currentPage;
      this.getMaterInfo(currentPage);
    },
    /**
     * 获取文件列表
     * @param  {Number} [page=1] 当前页码
     */
    getMaterInfo(page = 1) {
      let { InputText, filetype } = this;
      let { id } = this.$route.params;
      this.data1 = [];
      Server.post(
        Path.tch_getFileList,
        {
          showname: InputText,
          fatherid: id,
          pagenum: page,
          filetype: filetype,
          usetype: 0
        },
        res => {
          let { code, data } = res;
          let { pagenum, pagesize, total } = data.pageinfo;
          if (code === 0 && data != "") {
            this.total = total;
            this.display = pagesize;
            this.current = pagenum;
            this.data1 = res.data.data;
          }
          this.loading = false;
        }
      ).catch(err => {
        throw err;
      });
    },
    //删除课件
    deleteFile() {
      Server.post(
        Path.deleteFile,
        {
          fileid: this.fileId,
          fatherid: this.$route.params.id,
          filetype: this.filetype
        },
        res => {
          /// 权限判断
          if (res.code === 20019) {
            // 该文件并非您添加，您不能进行删除操作
            this.$Message.error(
              this.$t("Main.NotHavePermission") +
                this.$t("Main.Symbols.ExclamationMark")
            );
          }
          if (res.code === 0) {
            this.getMaterInfo(this.current);
          }
          this.DeleteModal = false;
        }
      ).catch(err => {
        throw err;
      });
    },
    //素材预览
    openViewModal(viewId, viewPath, viewType){
      this.viewModal = true;
      let _str = viewType;
      if(_str != ""){
        this.pathUrl = viewPath;
        //console.log("id： " + viewId + " path： " + viewPath + " type： " + viewType);
        let index =""
        index = _str.lastIndexOf("\.");
        _str = _str.substring(index+1, _str.length);
        //console.log("index: " + index + " str： " + _str);
        if(_str == "ppt" || _str == "pptx"){
          this.typeFlag = 1;
        }else if( _str == "png" || _str == "jpg" || _str == "gif" || _str == "jpeg" ){
          this.typeFlag = 2;
        }else if(_str == "mp4" || _str == "WebM" || _str == "MP4" || _str == "Ogg" || _str == "flv" || _str == "FLV" || _str == "swf" || _str == "SWF"){
          this.typeFlag = 3;
        }else if(_str == "mp3" || _str == "MP3"){
          this.typeFlag = 4;
        }else{
          this.typeFlag = 5;
        }
      }else{
        this.typeFlag = 5;
      }
    }
  }
};
</script>
<style scoped lang="less">
.uploadbox {
  margin-top: -10px;
  width: 60px;
  height: 60px;
}

.input-label {
  font-size: 18px;
  color: #7a8599;
}

.tip-box {
  padding: 11px 30px 11px 50px;
}

.tip-box ul li {
  line-height: 30px;
  position: relative;
  font-size: 14px;
  color: #525866;
  word-break: break-all;
}

.tip-box ul li::after {
  content: "";
  width: 5px;
  height: 5px;
  border-radius: 50%;
  background-color: #387bff;
  left: -20px;
  top: 11px;
  position: absolute;
}
.img-wrap{
  width: 100%;
  img{
    width: 100%;
    height: 100%;
  }
}
.noData{
  font-size: 16px;
  text-align: center;
}
</style>
<style scoped>
.uploadbox >>> .ivu-upload {
  width: 100% !important;
  height: 100% !important;
}

.uploadbox >>> img {
  width: 100% !important;
  height: 100% !important;
  cursor: pointer;
}
</style>



