<template>
  <div>
    <Card class="noradius-card mytheme-noborder mytheme-content-minheight mytheme-card-padding" dis-hover>
      <div class="evaluation-box">
        <div class="evaluation-item">
          <div class="eval-header-img">
            <img :src="data.imageurl || defaultCover" @error="_imageError">
          </div>
          <div class="eval-content">
            <ul>
              <li>
                <div class="eval-name periodname">{{data.categoryname}}</div>
              </li>
              <li>
                <!--班级：-->
                <div class="eval-class-type">
                  <span class="eval-label">班级名称：</span>
                  <span class="eval-val coursename">{{className}}</span>
                </div>
                <!--课程：-->
                <div class="eval-class-type">
                  <span class="eval-label">课程名称：</span>
                  <span class="eval-val">{{data.coursename}}</span>
                </div>
              </li>
              <li>
                <!--添加时间：-->
                <div class="eval-time">
                  <span class="eval-label">添加时间：</span>
                  <span class="eval-val">{{data.addtime}}</span>
                </div>
                <!--上架状态：-->
                <div class="eval-time orderstatus">
                  <span v-if="data.status==1">已上架</span>
                  <span v-if="data.status==0">未上架</span>
                </div>
                <div class="eval-time">
                  <span class="totalprice">￥{{price}}/课时</span>
                </div>
              </li>
            </ul>
          </div>
        </div>
        <div class="evaluation-overview">
          <div class="eval-label">课程概述：</div>
          <div class="eval-val">
            <div class="generalizeTxt" v-html="data.generalize"></div>
          </div>
        </div>
      </div>
    </Card>
  </div>
</template>

<script>
  import defaultCover from '@/assets/images/defaultCover.png';
  import {Card, Tag} from "iview";

  export default {
    name: "BasicInfo",
    props: ['data'],
    components: {
      Card,
      Tag
    },
    data() {
      return {
        defaultCover,
        totalprice: this.$route.query.totalprice,
        className: this.$route.query.className,
        price: this.$route.query.price,
      }
    },
  }
</script>

<style scoped lang="less">
  @import "~@/assets/less/variable";

  .evaluation-overview {
    display: flex;
    .eval-val {
      flex: 1;
      max-width: 1423px;
    }
  }

  .evaluation-box {
    .eval-label {
      display: table-cell;
      width: 90px;
      color: #4D4D4D;
    }
    .eval-val {
      display: table-cell;
      color: #808080;
      word-break: break-all;
      word-wrap: break-word;
      white-space: pre-wrap;
      font-size: @font-size-normal-s;
      &.coursename {
        color: #4D4D4D;
      }
    }
    .evaluation-item {
      box-sizing: content-box;
      padding: 56px 0;
      .eval-header-img {
        display: inline-block;
        width: 472px;
        height: 278px;
        margin-right: 46px;
        overflow: hidden;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .eval-content {
        display: inline-block;
        vertical-align: top;
        ul {
          li {
            overflow: hidden;
            font-size: @font-size-normal-s;
            line-height: 1;
            .eval-class-type {
              padding-bottom: 36px;
              line-height: 1;
              &:last-child {
                margin-right: 0;
              }
              &.student-number {
                padding-left: 20px;
              }
            }
            .eval-name {
              padding-bottom: 42px;
              font-size: @font-size-normal;
              font-weight: bold;
            }
            .eval-time {
              padding-bottom: 32px;
              .totalprice {
                font-size: @font-size-large;
                color: @color-red;
              }
              &.orderstatus {
                color: @color-orange;
              }
              &:last-child {
                margin-right: 0;
              }
            }
            .inline-block {
              display: inline-block;
            }
          }
        }
      }
    }
  }
</style>
<style scoped>
  .generalizeTxt >>> p img {
    width: 100%;
  }
</style>



