<template>
  <div class="task-detail-type">
    <panelTitle :crumbs="crumbs"></panelTitle>
    <div class="mytheme-container-mg-30">
      <Card class="noradius-card mytheme-noborder mytheme-card-padding" dis-hover>
        <div class="mytheme-mg-top-20">
          <span>输入搜索：</span>
          <Input
            class="input-max-height input-normal-width"
            type="text"
            placeholder="学生姓名"
            v-model="keyword"
            @on-enter="getTaskList(current, keyword)"
          />
          <Button class="mytheme-mg-left-70" type="primary" @click="getTaskList(current, keyword)">搜索</Button>
        </div>
        <Table
          class="mytheme-table noborder-table thead-max-height tbody-min-height mytheme-mg-top-30"
          :columns="colData"
          :disabled-hover="true"
          :data="tabData"
          :stripe="true"
        >
        </Table>
        <pagination
          class="mytheme-mg-top-20"
          v-if="total > 0"
          :total="total"
          :current-page="current"
          :display="display"
          @pagechange="pagechange"
        ></pagination>
      </Card>
    </div>
  </div>
</template>

<script>
  import panelTitle from '@/components/panelTitle';
  import pagination from '@/components/nm_pagination';
  import Server from '@/service/request';
  import Path from '@/service/Path';
  import {Card, Input, Table, Tabs, TabPane, Button} from 'iview';

  export default {
    name: "TaskDetailType",
    components: {
      panelTitle,
      Card,
      Input,
      Table,
      pagination, Tabs, TabPane, Button
    },
    data() {
      return {
        crumbs: [
          {
            name: '',
            route: ''
          },
          {
            name: '',
            route: ''
          },
          {
            name: '未完成',
            route: ''
          }
        ],
        topbarIndex: 0,

        colData: [
          {key: 'studentid', title: '编号'}
        ],
        tabData: [],
        keyword: '',

        display: 20,
        current: 1,
        total: 0,
      }
    },
    created() {
      this.initCrumbs();
    },
    mounted() {
      let {current, keyword} = this;
      this.changeTableColumns(this.topbarIndex);
      this.getTaskList(current, keyword);
    },
    methods: {
      initCrumbs() {
        this.crumbs[0].name = this.$route.query.parentName;
        this.crumbs[0].route = this.$route.query.parentRoute;
        this.crumbs[1].name = this.$route.query.name;
        this.crumbs[1].route = this.$route.query.route;
        this.crumbs[2].route = this.$route.fullPath;
      },
      /**
       * 分页加载
       * @param  {Number} current 页码
       * @return {none}
       */
      pagechange(current) {
      },
      /**
       * 根据条件自定义列数
       * @param  {Number} index topBar下标
       * @return {Array}        表头和表格的列数
       */
      getTableColumns(index) {
        let data = [];
        const tableData = {
          id: {
            key: 'studentid',
            title: '编号',
            align: 'center'
          },
          studentname: {
            key: 'nickname',
            title: '学员名称',
            align: 'center'
          },
          status: {
            render: (h, params) => {
              return h('div', params.row.issubmited ? '是' : '否');
            },
            title: '学员是否提交',
            align: 'center'
          },

          addtime: {
            render: (h, params) => {
              let that = this;
              function renderSubmitTime(param) {
                if(param) {
                  return that.formatStampStr(params.row.submittime, 'yyyy-MM-dd HH:mm:ss');
                } else {
                  return '----';
                }
              }
              return h('div', renderSubmitTime(params.row.submittime));
            },
            title: '提交时间',
            align: 'center'
          },
          readstatus: {
            render: (h, params) => {
              return h('div', params.row.reviewstatus ? '已批阅' : '未批阅');
            },
            title: '批阅状态',
            align: 'center'
          },
          sorce: {
            title: '成绩',
            render: (h, params) => {
              return h('div', {}, params.row.sorce);
            },
            align: 'center'
          },
          view: {
            title: '操作',
            render: (h, params) => {
              return h('a', {
                attrs: {
                  class: (params.row.issubmited == 0 || params.row.reviewstatus == 1) ? 'color-hui' : '',
                },
                on: {
                  'click': () => {
                    if (params.row.issubmited == 0 || params.row.reviewstatus == 1) {//如果未提交，不可批阅
                      return;
                    }
                    this.$router.push({
                      path: '/Examines',
                      query: {
                        route: this.$route.query.parentRoute,
                        level1Name: this.$route.query.parentName,
                        level2Route: this.$route.query.route,
                        level2Name: this.$route.query.name,
                        level3Route: this.$route.fullPath,
                        level3Name: this.crumbs[2].name,
                        curriculumid: this.$route.query.curriculumid,
                        schedulingid: this.$route.query.schedulingid,
                        lessonsid: this.$route.query.lessonsid,
                        studentid: params.row.studentid,
                        studentname: params.row.nickname,
                        periodname: this.$route.query.periodname,
                        isShowSubmitBtn: true,
                        reviewstatus: params.row.reviewstatus,
                      },
                    });
                  }
                }
              }, '批阅');
            },
            align: 'center'
          },
        }
        if (index === 0) {
          data.push(tableData.id, tableData.studentname, tableData.status, tableData.addtime, tableData.readstatus, tableData.view);
        } else {
          data.push(tableData.id, tableData.studentname, tableData.status, tableData.addtime, tableData.readstatus, tableData.reviewtime, tableData.sorce, tableData.view);
        }
        return data;
      },
      formatStampStr(timestamp, format) {
        let date = new Date();
        date.setTime(timestamp * 1000);
        let year = date.getFullYear(),
          month = (date.getMonth() + 1) >= 10 ? date.getMonth() + 1 : `0${date.getMonth() + 1}`,
          day = date.getDate() >= 10 ? date.getDate() : `0${date.getDate()}`,
          hour = date.getHours() >= 10 ? date.getHours() : `0${date.getHours()}`,
          minute = date.getMinutes() >= 10 ? date.getMinutes() : `0${date.getMinutes()}`,
          second = date.getSeconds() >= 10 ? date.getSeconds() : `0${date.getSeconds()}`;
        let formatStr = '';
        switch (format) {
          case 'yyyy-MM-dd':
            formatStr = `${year}-${month}-${day}`;
            break;
          case 'yyyy-MM-dd HH:mm:ss':
            formatStr = `${year}-${month}-${day} ${hour}:${minute}:${second}`;
            break;
          case 'yyyy':
            formatStr = `${year}`;
            break;
          case 'yyyy-MM':
            formatStr = `${year}-${month}`;
            break;
          case 'timestamp':
            formatStr = `${timeStamp}`;
            break;
          default: formatStr = `${year}-${month}-${day}`;
        }
        return formatStr;
      },
      /**
       * 点击topBar切换表格列数
       * @param  {Number} idx tabBar选中下标
       * @return {none}
       */
      changeTableColumns(idx) {
        this.colData = this.getTableColumns(idx);
      },
      /**
       * 查询学生作业明细
       * @param pagenum 目标页码
       * @param keyword 学生姓名关键字
       */
      getTaskList(pagenum, keyword) {
        let {curriculumid, schedulingid, lessonsid, reviewstatus} = this.$route.query;
        Server.post(Path.stuHomeworkList, {
          'pagenum': pagenum,
          'curriculumid': curriculumid,
          'schedulingid': schedulingid,
          'lessonsid': lessonsid,
          'reviewstatus': reviewstatus,
          'studentname': keyword,
        }, (res) => {
          let {code, data} = res;
          if (code === 0) {
            this.tabData = data.data;
            this.total = data.pageinfo.total;
          }
        });
      },
    }
  }
</script>

<style lang="less" scoped>
  .nopadding-card {
    padding: 0 !important !important;
  }

  .topbar {
    font-size: 0;
    li {
      margin: 0 20px 20px 0;
      padding: 0 0 14px;
      display: inline-block;
      text-align: center;
      font-size: 14px;
      cursor: pointer;
      &.active {
        position: relative;
        color: #2D8cF0;
        &:after {
          display: block;
          content: ' ';
          width: 100%;
          height: 2px;
          background: #2D8cF0;
          position: absolute;
          bottom: 0;
          left: 0;
          right: 0;
        }
      }
    }
  }
</style>



