<template>
  <div id="EssayMarking">
    <panel-title :crumbs="crumbs"></panel-title>
    <div class="mytheme-container-mg-30">
      <Tabs class="mytheme-tab"
            type="card"
            :animated="false"
            v-model="topBarIndex"
            @on-click="getCompositionList(current, topBarIndex)">
        <TabPane name='0' label="未批阅"></TabPane>
        <TabPane name='1' label="我的批阅"></TabPane>
      </Tabs>
      <Card class="mytheme-mg-bottom-20 noradius-card mytheme-noborder mytheme-card-padding" dis-hover>
       <div class="mytheme-mg-top-20">
          <span class="mytheme-input-label">学员姓名：</span>
          <Input
            class="input-normal-width input-max-height"
            placeholder="请输入学员姓名"
            v-model="studentname"
            @on-enter="getCompositionList(current,topBarIndex)"
          />
          <Button
            class="mytheme-mg-left-70"
            type="primary"
            @click="getCompositionList(current,topBarIndex)"
          >搜索
          </Button>
        </div>
         <Table
          class="mytheme-table noborder-table thead-max-height tbody-min-height mytheme-mg-top-30"
          :stripe="true"
          :disabled-hover="true"
          :columns="columns"
          :data="datas"
          :no-data-text="$t('Main.TaskManage.NoData')">
        </Table>
        <Pagination
          class="mytheme-mg-top-20"
          v-if="total > 0"
          :total="total"
          :current-page='current'
          :display="display"
          @pagechange="pagechange">
        </Pagination>
      </Card>
    </div>
  </div>
</template>
<script>
  import panelTitle from "@/components/panelTitle";
  import Pagination from '@/components/pagination';
  import {
    Card,
    Input,
    Button,
    Tabs,
    TabPane,
    Table
  } from "iview";
  import Server from '@/service/request';
  import Path from '@/service/Path';

  export default {
    name: "",
    components: {
      panelTitle,
      Card,
      Input,
      Button,
      Tabs,
      TabPane,
      Table,
      Pagination,
    },
    watch: {
      topBarIndex(val){
        this.studentname="";
         this.getCompositionList(this.current,val);
        if(val==0){
           this.crumbs=[{name:'作文批改', route: '/EssayMarking'},{ name:'未批阅', route: '/EssayMarking'}]
        }else{
          this.crumbs=[{name:'作文批改', route: '/EssayMarking'},{ name:'我的批阅', route: '/EssayMarking'}]
        }
      }
    },
    methods: {
      getCompositionList(page = 1, reviewstatus) {
        this.datas = [];
        this.total = 0;
        this.display =20;
        this.current =1;
        Server.post(Path.getCompositionList, {
          reviewstatus:reviewstatus,
          studentname:this.studentname,
          pagenum:page
        }, res => {
          let {code, data} = res;
          if (code == 0 && data.composition.length != 0) {
            this.datas = data.composition;
            this.datas.forEach(el=> {
              if (el.lastname=="") {
                el.lastname="---"
              }
            });
            this.total=data.pageinfo.total;
            this.display=Number(data.pageinfo.pagesize);
            this.current=Number(data.pageinfo.pagenum);
          } else {
            this.datas = [];
          }
        }).catch(err => {
          throw err;
        })
      },
      /**
       * 分页请求
       * @param current 目标页码
       */
      pagechange(current) {
        this.current = current;
        this.getCompositionList(this.current, this.topBarIndex);
      },
    stamp(times) {//时间处理器
      let date = new Date(times * 1000);
      let Y = date.getFullYear() + '-';
      let M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
      let D = (date.getDate() < 10 ? '0' + (date.getDate()) : date.getDate()) + ' ';
      let h = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':';
      let m = (date.getMinutes() <10 ? '0' + date.getMinutes() : date.getMinutes()) + ':';
      let s = (date.getSeconds() <10 ? '0' + date.getSeconds() : date.getSeconds());
      return Y+M+D+h+m+s;
    },
    },

    created() {
      if (this.$Utils.isLogin()) {
        this.teacherid = this.$Utils.getUserInfo().uid;
        this.organid = this.$Utils.getUserInfo().organid;
      } else {
        this.$router.push('/Login');
        return;
      }
      this.getCompositionList(this.current, this.topBarIndex);
    },
    data() {
      return {
        organid: 0,
        teacherid: 0,
        crumbs: [{
          name:'作文批改',
          route: '/EssayMarking'
        },{
          name:'未批阅',
          route: ''
        }],
        topBarIndex: '0',//tab默认值
        studentname:'',//关键字搜索
        columns: [
          {
            title: "编号",
            align: 'center',
            key: 'id'
          }, {
            title: "学员姓名",
            align: 'center',
            key: 'nickname'
          }, {
            title: "作文标题",
            align: 'center',
            key: 'title'
          }, {
            title: "类型",
            align: 'center',
            key: 'type',
            render:(h,params)=>{
                let typeText = "作文";
            if (params.row.type == 1) {
              typeText = "作文";
            } else{
              typeText ="日记";
            }
              return h('div',{},typeText);
            }
          },{
            title: "提交时间",
            align: 'center',
            key: 'addtime',
            render:(h,params)=>{
              return h('div',{},this.stamp(params.row.addtime));
            }
          },{
            title: "上次批改人",
            align: 'center',
            renderHeader:(h, params) => {
             let tableTitel = "";
            if (this.topBarIndex == 0) {
              tableTitel = "上次批改人";
            } else{
              tableTitel ="批阅时间";
            }
              return h('div',{},tableTitel);
          },
           render:(h,params)=>{
                let text = "";
            if (this.topBarIndex == 0) {
              text = params.row.lastname;
            } else{
              text = this.stamp(params.row.reviewtime);
            }
              return h('div',{},text);
            }
          },{
            title: "",
            align: 'center',
            renderHeader:(h, params) => {
             let tableTitel = "上次批改时间";
            if (this.topBarIndex == 0) {
              tableTitel = "上次批改时间";
            } else {
              tableTitel ="评分";
            }
              return h('div',{},tableTitel);
          },
           render:(h,params)=>{
                let text = "";
            if (this.topBarIndex == 0) {
              params.row.lasttime==""?text ="---":text =this.stamp(params.row.lasttime)
            } else{
              text = params.row.reviewscore;
            }
              return h('div',{},text);
            }
          },{
            title: "操作",
            align: 'center',
            render: (h, params, index) => {
              let handleText = "批阅";
              if (this.topBarIndex == 0) {
                handleText = "批阅";
              } else{
                handleText ="查看";
              }
              return h('div', [
                h('a', {
                  style: {
                    color:'#3F9CE8'
                  },
                  on: {
                    'click': () => {
                       if (this.topBarIndex == 0) {
                        Server.post(Path.checksCompositionData, {
                          compositionid:params.row.id
                        }, res => {
                          let {code, data,info} = res;
                          if (code==0) {
                            this.$router.push({
                              path: '/essayDetail',
                              query: {
                                route: this.$route.fullPath,
                                type:this.topBarIndex,
                                compositionid: params.row.id
                              }
                            });
                          } else {
                            this.$Message.error(info);
                          }
                        })
                      } else{
                        this.$router.push({
                          path: '/essayDetail',
                          query: {
                            route: this.$route.fullPath,
                            type:this.topBarIndex,
                            compositionid: params.row.id
                          }
                        });
                      }
                    }
                  },
                 attrs: {
                      class: 'mytheme-table-btn blue',
                    }
                },handleText)
              ])
            }
          }],
          datas:[],
          total: 0,
          display: 20,
          current: 1,
      };
    }
  };
</script>
<style scoped lang="less">
  .ivu-card-head {
    span {
      font-size: 16px;
    }
  }

</style>



