<template>
  <div>
    <!--学生详情-->
    <panelTitle :crumbs="crumbs"></panelTitle>
    <div class="mytheme-container-mg-30">
      <Tabs class="mytheme-tab" type="card" :animated="false">
        <TabPane label="学生信息">
          <BaseInfo :baseinfo="data"></BaseInfo>
        </TabPane>
        <TabPane label="他的订单">
          <Orders :orderlist="orderList" :display="display" :current="current" :total="total" @pagechange="pageChange"></Orders>
        </TabPane>
      </Tabs>
    </div>
  </div>
</template>

<script>
  import panelTitle from "@/components/panelTitle";
  import BaseInfo from '@/container/Mystudents/StudentDetail/BaseInfo';
  import Orders from '@/container/Mystudents/StudentDetail/Orders';
  import Server from '@/service/request';
  import Path from '@/service/Path';
  import {Tabs, TabPane} from "iview";

  export default {
    name: "OverView",
    components: {
      panelTitle, Tabs, TabPane, BaseInfo, Orders,
    },
    data() {
      return {
        crumbs: [
          {name: '', route: ''},
          {name: this.$t('Main.MyStudent.Menu'), route: this.$route.query.route,}, // 我的学生
          {name: this.$t('Main.MyStudent.StudentDetails'), route: this.$route.fullPath,} // 学生详情
        ],
        isfemale: false,
        data: {},
        orderList: [],
        display: 20,
        current: 1,
        total: 0,
      }
    },
    created() {
      if (this.$Utils.isLogin()) {
        this.crumbs[0].route = this.$route.fullPath;
        this.getStuInfo(this.current);
      } else {
        this.$router.replace('/Login');
        return;
      }
    },
    methods: {
      /**
       * 查询学生详情
       * @param page 目标页码
       */
      getStuInfo(page = 1) {
        let stuID = this.$route.query.stuID;
        Server.post(Path.getUserinfo, {
          userid: stuID,
          pagenum: page,
        }, res => {
          let {code, data} = res;
          if (code === 0) {
            this.data = data.data;
            this.total = data.courselist.pageinfo.total;
            this.current = parseInt(data.courselist.pageinfo.pagenum);
            this.orderList = data.courselist.data.reverse();// 订单列表倒序排列
          }
        }).catch(err => {
          throw err;
        })
      },
      /**
       * 分页查询
       * @param currnet 目标页码
       */
      pageChange(currnet) {
        this.getStuInfo(currnet);
      },
    }
  };
</script>

<style scoped lang="less">
  /*-------改版--------*/
  /*student info*/
  .over-view-userinfo {
    position: relative;
    .over-view-userinfo-left {
      padding-top: 30px;
      display: table-cell;
      position: relative;
      width: 270px;
      &:after {
        content: ' ';
        display: block;
        position: absolute;
        top: -16px;
        bottom: -16px;
        right: 0;
        border-right: 1px solid #E6E6E6;
      }
    }
    .over-view-userinfo-right {
      vertical-align: top;
      display: table-cell;
      .right-base-info {
        padding-left: 35px;
        padding-top: 30px;
        font-size: 16px;
        .info-item {
          padding-bottom: 26px;
          label {
            display: table-cell;
            width: 80px;
            color: #525866;
          }
          span {
            display: table-cell;
            width: auto;
            color: #7A8599;
          }
        }
      }
    }
  }

  /*Ta的订单*/
  .panel-title {
    vertical-align: top;
    height: 22px;
    font-weight: 500;
    line-height: 22px;
    color: #5E6266;
    margin: 34px 0 24px 0;
    font-family: 'PingFangSC-Regular';
    font-size: 20px;
    .title-icon {
      vertical-align: top;
      width: 26px;
      height: 28px;
      margin-right: 16px;
    }
    span {
      display: inline-block;
      padding-top: 2px;
    }
  }

  /*Card list*/
  .order-card {
    min-width: 900px;
    margin-bottom: 20px;
    border-radius: 8px;
    .overview-title {
      padding-left: 14px;
      height: 24px;
      font-size: 18px;
      line-height: 24px;
      color: #5E6266;
      font-family: 'PingFangSC-Medium';
      font-weight: 400;
      .order-time {
        color: #5E6266;
        font-size: 14px;
        display: inline-block;
        margin-left: 44px;
        font-weight: 400;
      }
    }
    .order-content {
      overflow: hidden;
      .order-left {
        width: 76%;
        margin: 22px 0 22px 14px;
        overflow: hidden;
        float: left;
        .left-img {
          float: left;
          height: 150px;
          width: 250px;
          margin-right: 26px;
          overflow: hidden;
          img {
            width: 100%;
            height: 100%;
            border-radius: 4px;
          }
        }
        @media screen and (max-width: 1500px) {
          .left-img {
            float: left;
            height: 150px;
            width: 250px;
            margin-right: 16px;
            overflow: hidden;
            img {
              width: 100%;
              height: 100%;
              border-radius: 4px;
            }
          }
        }
        .order-desc {
          float: left;
          line-height: 38px;
          h3 {
            font-size: 16px;
            color: #404040;
            font-family: 'PingFangSC-Medium';
          }
          .desc-coursename {
            font-size: 18px;
            color: #292C33;
            font-family: 'PingFangSC-Regular';
          }
          .desc-classname {
            font-size: 16px;
            color: #525866;
            font-family: 'PingFangSC-Regular';
          }
          ul {
            color: #999999;
          }
          .desc-item {
            float: left;
            margin-right: 40px;
            p {
              font-size: 14px;
              color: #7A8599;
              font-family: 'PingFangSC-Regular';
            }
          }
          @media screen and (max-width: 1500px) {
            .desc-item {
              float: left;
              margin-right: 26px;
              p {
                font-size: 14px;
                color: #7A8599;
                font-family: 'PingFangSC-Regular';
              }
            }
          }
        }
      }
      @media screen and (max-width: 1500px) {
        .order-left {
          width: 80%;
        }
      }
    }
    .order-right {
      color: #7A8599;
      font-size: 16px;
      text-align: center;
      line-height: 216px;
      overflow: hidden;
      p {
        border-left: 1px solid #CFDCE7;
      }
    }
  }

  .color-red {
    color: #F04747;

  }
</style>



