// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import "babel-polyfill";
import Vue from 'vue'
import App from './App'
import router from './router';
import store from '@/store';
import 'iview/dist/styles/iview.css';
import './assets/my-theme/index.less';
import './assets/less/index.less';
import '@/assets/common.css';
import {Switch, Message, Button, Rate, Radio, Input, RadioGroup} from 'iview';
import vueI18n from 'vue-i18n'
import en from './i18n/en';
import zh from './i18n/zh';
import defaultCover from './assets/images/defaultCover.png';
import defaultHead from './assets/images/defaultAvatar.png';
import infiniteScroll from "vue-infinite-scroll";
import {JSEncrypt} from 'jsencrypt';
import utils from './assets/js/utils.js';

Vue.use(infiniteScroll);
Vue.use(vueI18n)

Vue.config.productionTip = false;
Vue.component('i-switch', Switch);
Vue.component('i-button', Button);
Vue.component('i-rate', Rate);
Vue.component('i-radio', Radio);
Vue.component('i-input', Input);
Vue.component('i-radiogroup', RadioGroup);
Vue.prototype.$Message = Message;
Vue.prototype.$loca = window.localStorage;
/* 列表缺省图 */
Vue.prototype._imageError = function (e) {
  e.target.src = defaultCover;
};
/* 默认头像 */
Vue.prototype._defaultHead = function (e) {
  e.target.src = defaultHead;
}
Vue.prototype.$Utils = utils;
// 自动设置语言
const navLang = (navigator.language || navigator.browserLanguage).toLowerCase();
const lang = navLang.indexOf('zh') != -1 ? 'zh-cn' : 'en-us';
const i18n = new vueI18n({
  locale: 'zh-cn',
  messages: {
    'en-us': en,
    'zh-cn': zh
  }
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  i18n,
  router,
  store,
  components: {App},
  template: '<App />'
}).$mount('#app')

/*判断用户信息是否过期，登出*/
if (localStorage.getItem('teacher_info') == null || localStorage.getItem('teacher_info') == '' ||
  localStorage.getItem('teacher_key') == null || localStorage.getItem('teacher_key') == '') {
  localStorage.removeItem('teacher_info');
  localStorage.removeItem('teacher_key');
}



