<template>
  <div id="task-content">
    <Form class="mytheme-form" :label-width="100">
      <FormItem label="作业详情：">
        <br>
        <div class="task-body">
          <TaskContentComponent v-if="homeWorkConent" :reviewData="homeWorkConent" :isdisable="true"></TaskContentComponent>
          <defaultShow v-else :text="'题库暂无数据'"></defaultShow>
        </div>
      </FormItem>
    </Form>
    <div class="btn-group">
      <Button class="btn-margin" type="primary" @click="$emit('addHomeWork')">发布</Button>
      <Button @click="$router.go(-1)">取消</Button>
    </div>
  </div>
</template>

<script>
  import TaskContentComponent from '@/container/TaskManage/TaskContent';
  import defaultShow from '@/components/defaultShow';
  import {Form, FormItem, Row, Col, Button} from 'iview';

  export default {
    name: 'task-content',
    props: ['homeWorkConent'],
    components: {Form, FormItem, Row, Col, Button, TaskContentComponent,defaultShow,},
    data() {
      return {

      }
    },
    methods: {
    },
  }
</script>

<style scoped lang="less">
  @import "~@/assets/less/variable";

  .task-body {
    min-height: 336px;
    margin: 30px 0 0 -90px;
    padding: 20px 30px;
    border: 1px solid #CCCCCC;
    background: #FAFCFC;
  }

  .btn-group {
    margin: 67px 0 0 10px;
  }
</style>



