<template>
  <div class="MyMessage">
    <panel-title :crumbs="crumb"></panel-title>
    <div class="mytheme-container-mg-30">
      <Card class="radius-card mytheme-noborder noradius-card mytheme-card-padding" dis-hover>
        <div class="mytheme-mg-top-20 mytheme-mg-bottom-30" v-if="tabData">
          <Button type="primary" @click="showDelModal">批量删除</Button>
        </div>
        <Table
          class="mytheme-table noborder-table thead-max-height tbody-min-height mytheme-mg-bottom-30"
          :columns="colData"
          :disabled-hover="true"
          :data="tabData"
          :stripe="true"
        >
        </Table>
        <pagination
          v-if="total > 0"
          class="mytheme-mg-top-20"
          :total="total"
          :display="display"
          :current-page="current"
          :loading="loading"
          @pagechange="pagechange"
        >
        </pagination>
      </Card>
    </div>
    <Modal class="mytheme-modal" v-model="isDeleteModal" :closable="false">
      <div class="modal-head" slot="header">温馨提示</div>
      <div class="modal-body">确认删除该消息？</div>
      <div class="modal-foot" slot="footer">
        <Button  type="primary" @click="deleteMsg(checkedIds)">确定</Button>
        <Button class="mytheme-btn" type="default" @click="isDeleteModal= false; resetIds()">取消</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
  import panelTitle from "@/components/panelTitle";
  import {Card, Button, Table, Checkbox, Modal} from 'iview';
  import pagination from '@/components/pagination';
  import Server from '@/service/request';
  import Path from '@/service/Path';

  export default {
    name: "MyMessage",
    data() {
      return {
        crumb: [{name: '我的消息', route: '',}],
        colData: [
          {
            width: 50,
            render: (h, params) => {
              return h('Checkbox', {
                props: {
                  value: params.row.ischeck
                },
                on: {
                  'on-change': () => {
                    this.checkedIds = [];
                    this.tabData[params.index].ischeck = !this.tabData[params.index].ischeck;
                    params.row.ischeck = true;
                    this.tabData.forEach(el => {
                      if (el.ischeck) {
                        this.checkedIds.push(el.id);
                      }
                    });
                  }
                }
              })
            },
            title: ' ',
          },
          {title: '发送时间', key: 'addtime', align: 'center'},
          {title: '内容', key: 'content', align: 'center'},
          {
            title: '操作', key: 'option', align: 'center',
            render: (h, params) => {
              return h('div', [
                h('a', {
                  attrs: {
                    class: 'red'
                  },
                  on: {
                    'click': () => {
                      this.isDeleteModal = true;
                      if (this.checkedIds.length === 0) {
                        this.checkedIds.push(params.row.id);
                      }
                    }
                  }
                }, '删除')
              ])
            }
          }
        ],
        tabData: [],
        total: 0,
        display: 20,
        current: 1,
        isDeleteModal: false,
        checkedIds: [],
        msgDoneArr: [],
        loading: true,
      }
    },
    components: {
      panelTitle,
      Card,
      Button,
      pagination,
      Table,
      Modal,
    },
    created() {
      if (this.$Utils.isLogin()) {
        this.getMsgList(this.current);
      } else {
        this.$router.replace('/Login');
        return;
      }
    },
    watch: {
      /**
       * 监控删除消息Modal，在关闭弹窗时，清空 checkedIds
       */
      isDeleteModal(val) {
        if(!val) {
          this.checkedIds = [];
          this.tabData.forEach(item => {
            item.ischeck = false;
          })
        }
      },
    },
    methods: {
      /**
       * 点击取消按钮，清空选中box
       * @Author   qyh
       * @DateTime 2018-08-07
       * @version  2.0
       */
      resetIds() {
        this.checkedIds = [];
        this.tabData.forEach(el => {
          el.ischeck = false;
        });
      },
      /**
       * 显示删除消息Modal
       */
      showDelModal() {
        if (this.checkedIds.length > 0) {
          this.isDeleteModal = true;
        } else {
          this.$Message.error('请选择要删除的消息');
        }
      },
      /**
       * 分页请求
       * @param page  目标页码
       */
      pagechange(page) {
        this.current = page;
        this.getMsgList(page);
      },
      /**
       * 将消息设为已读
       */
      msgDone() {
        Server.post(Path.setMessage, {
          idarr: id,
        }, (res) => {
          let {code, info} = res;
          if (res) {
            this.$Message.success(info);
            this.getMsgList(this.current);
          }
          if (code === 0) {
            this.$Message.success(info);
            this.getMsgList(this.current);
          } else {
            this.$Message.error(info);
          }
        });
      },
      /**
       * 删除消息
       * @param ids 要删除消息的id数组
       */
      deleteMsg(ids) {
        Server.post(Path.delMsglist, {
          'idarr': ids,
        }, (res) => {
          let {code, info} = res;
          this.getMsgList(this.current);
          if (code === 0) {
            this.$Message.success(info);
            this.isDeleteModal = false;

          } else {
            this.$Message.error(info);
            this.isDeleteModal = false;
          }
        });
      },
      /**
       * 查询消息列表
       * @param page 目标页码
       */
      getMsgList(page = 1) {
        Server.post(Path.msgList, {
            pagenum: page
          }, (res) => {
            let {code, data} = res;
            if (Number(code) === 0) {
              this.tabData = data.data;
              this.tabData.forEach(el => {
                this.$set(el, 'ischeck', false);
              })
            } else if (code == 10151) {//暂无数据
              this.tabData = [];
            }
            this.loading = false;
          }
        ).catch(err => {
          throw err;
        });
      }
    }
  }
</script>

<style scoped>
</style>




