<template>
  <div id="Course">
    <panel-title :crumbs="crumbs"></panel-title>
    <div class="mytheme-container-mg-30">
      <Card class="noradius-card mytheme-noborder mytheme-card-padding mytheme-content-minheight" dis-hover>
        <template>
          <Form class="mytheme-mg-top-20" :label-width="100" inline>
            <span class="mytheme-input-label">{{$t('Main.MyClasses.Coursesname') + $t('Main.Symbols.Colon')}}</span>
            <Input
              class="input-normal-width input-max-height"
              placeholder="请输入课程名称"
              v-model.trim="SearchName"
              @on-enter="getClassList(SearchName, selectedVal, current)"
              @on-click="getClassList(SearchName, selectedVal, current)"
            />
            <span
              class="mytheme-input-label mytheme-pd-left-20"
            >{{$t('Main.MyClasses.Selstate') + $t('Main.Symbols.Colon')}}</span>
            <Select
              class="input-mini-width input-max-height input-max-height"
              v-model="selectedVal"
            >
              <Option v-for="(el, i) in options" :key="i" :value="el.type">{{el.name}}</Option>
            </Select>
            <Button class="mytheme-mg-left-70" type="primary" @click="getClassList(SearchName, selectedVal, current)">搜索</Button>
          </Form>
        </template>
        <template>
          <i-table
            class="mytheme-table noborder-table tbody-min-height thead-max-height mytheme-mg-top-30"
            ref="selection"
            :disabled-hover="true"
            :stripe="true"
            :columns="columns1"
            :data="data1"
            :no-data-text="$t('Main.MaterMessanage.NoData')"
          >
          </i-table>
        </template>
        <template>
          <pagination
            class="mytheme-mg-top-20"
            v-if="total > 0"
            :total="total"
            :current-page='current'
            :display="display"
            @pagechange="pagechange"
          ></pagination>
        </template>
      </Card>
    </div>
  </div>
</template>
<script>
  import panelTitle from "@/components/panelTitle";
  import pagination from "@/components/pagination";
  import {Card, Input, Table, Button, Form, Row, Col, Select, Option, FormItem} from "iview";

  import Server from '@/service/request';
  import Path from "@/service/Path";
  import defaultCover from '@/assets/images/defaultCover.png';

  export default {
    name: "MyClasses",
    components: {
      panelTitle,
      Card,
      Input,
      "i-table": Table,
      pagination,
      Button,
      Form,
      Row,
      Col,
      Select,
      Option,
      FormItem
    },
    data() {
      return {
        defaultCover,
        crumbs: [
          {
            name: this.$t('Main.MyClasses.Menu'),
            route: '/MyClasses'
          }
        ],
        total: 0, // 记录总条数
        display: 20, // 每页显示条数
        current: 1, // 当前的页数
        SearchName: '',
        InputText: "",
        columns1: [
          {
            title: this.$t('Main.MyClasses.CourseNum'),
            align: "center",
            key: 'id',
          },
          {
            title: this.$t('Main.MyClasses.CoursePic'),
            align: "center",
            render: (h, params) => {
              return h('img', {
                attrs: {
                  src: params.row.imageurl,
                },
                on: {
                  'error': () => {
                    this.data1[params.index].imageurl = this.defaultCover;
                  }
                },
                style: {
                  display: 'inline-block!important',
                  width: '72px',
                  height: '54px',
                  margin: '13px 0 7px 0',
                  borderRadius: '4px',
                }
              });
            }
          },
          {
            title: this.$t('Main.MyClasses.Coursesname'),
            align: "center",
            key: 'coursename'
          },
          {
            title: this.$t('Main.MyClasses.ClassName'),
            align: "center",
            key: "gradename"
          },
          {
            title: this.$t('Main.MyClasses.CoursePrice'),
            align: "center",
            render: (h, params) => {
              return h('div', `￥${params.row.price}/课时`);
            }
          },
          {
            title: this.$t('Main.MyClasses.Selstate'),
            align: "center",
            key: 'classstatusStr'
          },
          {
            title: this.$t('Main.MyClasses.Operate'),
            align: "center",
            render: (h, params, index) => {
              return h('div', [
                h('a', {
                  style: {
                    marginRight: '10px'
                  },
                  on: {
                    'click': () => {
                      this.$router.push({
                        path: '/ClassDetail',
                        query: {
                          claID: encodeURIComponent(params.row.id),
                          gradename: encodeURIComponent(params.row.gradename),
                          status: encodeURIComponent(params.row.classstatusStr),
                          price: encodeURIComponent(params.row.price),
                          route: this.$route.fullPath,
                          totalprice: params.row.totalprice,
                          className: params.row.gradename,
                        }
                      });
                    }
                  },
                  attrs: {
                    class: 'blue mytheme-table-text-btn'
                  },
                }, this.$t('Main.MyClasses.View'))
              ])
            }
          }],
        data1: [],
        selectedVal: 7,
        options: [//班级状态
          {name: '全部', type: 7},
          {name: '未招生', type: 0},
          {name: '已招生', type: 1},
          {name: '已满员', type: 3},
          {name: '授课中', type: 4},
          {name: '已结束', type: 5},
          {name: '已超时', type: 6},
        ],
        teacherid: 0,
        loading: true,
      }
    },
    created() {
      if (this.$Utils.isLogin()) {
        this.teacherid = this.$Utils.getUserInfo().uid;
      } else {
        this.$router.replace('/Login');
        return;
      }
      let {SearchName, selectedVal, current} = this;
      this.getClassList(SearchName, selectedVal, current);
    },
    methods: {
      /**
       * 分页请求
       * @param currentPage 目标页码
       */
      pagechange: function (currentPage) {
        this.current = currentPage;
      },
      /**
       * 查询班级列表
       * @param name    班级名称
       * @param status  班级授课状态
       * @param page    页码
       */
      getClassList(name, status, page) {
        let params = {
          teacherid: this.teacherid,
          name: name,
          pagenum: page,
        };
        if (status != undefined && status.toString() !== '' && status != 7) {
          params.classstatus = status;
        }
        Server.post(Path.getSchedulingList, params, (res) => {
          let {code, info, data} = res;
          if (code === 0) {
            this.data1 = data.data;
            this.total = data.pageinfo.total;
          }
          this.loading = false;
        });
      },
    }
  };
</script>
<style scoped lang="less">

  .input-label {
    font-size: 18px;
    color: #7A8599;
  }

  .ivu-table-wrapper {
    overflow: hidden;
  }

  .CheckBox {
    padding: 20px;
  }
</style>



