<template>
  <div class="nm-topbar">
    <ul class="topbar-menu">
      <li class="menu-item">
        <a href="javascript:void(0)">网校首页</a>
      </li>
      <li class="menu-item">
        <a href="javascript:void(0)">客户端下载</a>
      </li>
      <li class="menu-item">
        <a href="javascript:void(0)">神奇海螺</a>
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: "nm-topbar"
  }
</script>

<style scoped lang="less">
  @import "~@/assets/less/variable";
  .nm-topbar {
    height: 30px;
    line-height: 30px;
    background: @color-theme-sub;
    text-align: right;
    .topbar-menu {
      height: 100%;
      font-size: 0;
      .menu-item {
        position: relative;
        padding: 0 22px;
        display: inline-block;
        font-size: @font-size-small-s;
        &:after {
          content: ' ';
          display: block;
          position: absolute;
          top: 0;
          bottom: 0;
          right: 0;
          margin: auto;
          width: 1px;
          height: 12px;
          background: @color-text;
        }
        &:last-child:after {
          display: none;
        }
        a {
          color: @color-text;
        }
      }
    }
  }
</style>



