<template>
  <div>
    <panel-title :crumbs="crumbs"></panel-title>
    <div class="mytheme-container-mg-30">
      <Card class="noradius-card mytheme-noborder mytheme-card-padding" dis-hover>
        <div class="mytheme-mg-top-20">
          <span class="mytheme-input-label">{{$t('Main.TaskManage.InputSearch') + $t('Main.Symbols.Colon')}}</span>
          <Input
            class="input-normal-width input-max-height"
            :placeholder="$t('Main.TaskManage.InputKey')"
            v-model="coursename"
            @on-enter="getHomeWorkList(current,'', '',  coursename, schedulingname)"
          />
          <Button
            class="mytheme-mg-left-70"
            type="primary"
            @click="getHomeWorkList(current, '', '', coursename, schedulingname)"
          >搜索
          </Button>
          <!--布置作业-->
          <router-link :to="{path: '/TaskAdd', query: {name:crumbs[0].name, route: $route.fullPath}}">
            <Button class="mytheme-mg-left-70" type="warning">{{$t('Main.TaskManage.SetTask')}}</Button>
          </router-link>
        </div>
        <i-table
          class="mytheme-table noborder-table thead-max-height tbody-min-height mytheme-mg-top-30"
          ref="selection"
          :stripe="true"
          :disabled-hover="true"
          :columns="columns"
          :data="data"
          :no-data-text="$t('Main.TaskManage.NoData')">
        </i-table>
        <Pagination
          class="mytheme-mg-top-20"
          v-if="total > 0"
          :total="total"
          :current-page='current'
          :display="display"
          @pagechange="pagechange">
        </Pagination>
      </Card>
    </div>
  </div>
</template>

<script>
  import panelTitle from '@/components/panelTitle';
  import Pagination from '@/components/pagination';
  import Server from '@/service/request';
  import Path from '@/service/Path';
  import {Card, Form, Input, Button, Table, Modal, Radio, RadioGroup} from 'iview';

  export default {
    name: "TaskManage",
    components: {
      panelTitle,
      Card,
      Form,
      Input,
      Button,
      "i-table": Table,
      Pagination,
      Modal,
      Radio,
      RadioGroup,
    },
    data() {
      return {
        isShowAddModal: false,
        taskRadio: '1',
        crumbs: [
          {
            name: this.$t('Main.TaskManage.Title'),
            route: '/TaskManage' //作业管理
          }
        ],
        total: 0,
        display: 20,
        current: 1,
        inputTxt: "",
        columns: [
          // {
          //   title: '',
          //   width: 50,
          //   renderHeader: (h, params) => {
          //     return h('div', '');
          //   },
          //   render: (h, params) => {
          //     return h('Checkbox', {
          //       props: {
          //         value: params.row.ischeck
          //       },
          //       on: {
          //         'on-change': () => {
          //           this.checkedIds = [];
          //           this.tabData[params.index].ischeck = !this.tabData[params.index].ischeck;
          //           params.row.ischeck = true;
          //           this.tabData.forEach(el => {
          //             if (el.ischeck) {
          //               this.checkedIds.push(el.id);
          //             }
          //           })
          //         }
          //       }
          //     })
          //   }
          // },
          {
            title: this.$t('Main.TaskManage.Num'),
            align: 'center',
            key: 'id'
          }, {
            title: this.$t('Main.TaskManage.CourseName'),
            align: 'center',
            key: 'coursename'
          }, {
            title: this.$t('Main.TaskManage.ClassName'),
            align: 'center',
            key: 'classname'
          }, {
            title: this.$t('Main.TaskManage.StudentTotal'),
            align: 'center',
            key: 'stunum'
          }, {
            title: this.$t('Main.TaskManage.WorkNum'),
            align: 'center',
            key: 'worknum'
          }, {
            title: this.$t('Main.TaskManage.pubTime'),
            align: 'center',
            render: (h, params) => {
              return h('div', this.$Utils.formatStampStr(params.row.addtime, 'yyyy-MM-dd HH:mm:ss'));
            }
          }, {
            title: this.$t('Main.TaskManage.Operate'),
            align: 'center',
            render: (h, params, index) => {
              return h('div', [
                h('a', {
                  style: {},
                  on: {
                    'click': () => {
                      this.$router.push({
                        path: '/TaskDetail',
                        query: {
                          name: this.$t('Main.TaskManage.Title'),
                          route: this.$route.fullPath,
                          classid: params.row.classid,
                          courseid: params.row.courseid,
                        }
                      });
                    }
                  },
                  attrs: {
                    class: 'blue mytheme-table-text-btn',
                  }
                }, this.$t('Main.TaskManage.View'))
              ])
            }
          }],
        data: [],
        coursename: '',
        schedulingname: '',
      }
    },
    created() {
      let {current, coursename, schedulingname} = this;
      this.getHomeWorkList(current, '', '', coursename, schedulingname);
    },
    watch: {
      coursename(val) {
        this.schedulingname = val;
      }
    },
    methods: {
      /**
       * 查询作业列表
       */
      getHomeWorkList(page = 1, curriculumid = '', schedulingid = '', coursename = '', schedulingname = '') {
        Server.post(Path.scheHomeworkList, {
          'pagenum': page,
          // 'curriculumid': curriculumid,
          // 'schedulingid': schedulingid,
          'coursename': coursename,
          'schedulingname': '',
        }, (res) => {
          let {code, data} = res;
          if (code === 0) {
            this.data = Object.values(data.data).reverse();
            this.total = data.pageinfo.total;
          } else {
            let {current, coursename, schedulingname} = this;
            this.getHomeWorkList1(current, '', '', coursename, schedulingname);
          }
        });

      },
      getHomeWorkList1(page = 1, curriculumid = '', schedulingid = '', coursename = '', schedulingname = '') {
        Server.post(Path.scheHomeworkList, {
          'pagenum': page,
          // 'curriculumid': curriculumid,
          // 'schedulingid': schedulingid,
          'coursename': '',
          'schedulingname': schedulingname,
        }, (res) => {
          let {code, data} = res;
          if (code === 0) {
            this.data = Object.values(data.data).reverse();
            this.total = data.pageinfo.total;
          }
        });
      },
      /**
       * 分页请求
       * @param current 目标页码
       */
      pagechange(current) {
        this.current = current;
        this.getHomeWorkList(current);
      }
    },
  }
</script>

<style lang="less" scoped>
  .modal-body {
    margin: auto;
    padding: 10px 0;
    display: table;
    .modal-body-left {
      vertical-align: top;
      padding-right: 10px;
      display: table-cell;
      font-size: 16px;
    }
    .modal-body-right {
      vertical-align: top;
      margin-top: -4px;
      font-size: 14px;
    }
  }
</style>



