<template>
  <nav>
    <ul class="pagination">
      <!-- 首页 -->
      <li :class="{'disabled': current == 1}"><a href="javascript:;" class="page-main" @click="setCurrent(1)">
        {{$t('Main.PageChange.HomePage')}} </a></li>
      <!-- 上一页 -->
      <li :class="{'disabled': current == 1}"><a href="javascript:;" class="page-main" @click="setCurrent(current - 1)">{{$t('Main.PageChange.Prev')}}</a>
      </li>
      <li v-for="p in grouplist" :class="{'active': current == p.val}">
        <a class="page-item" href="javascript:;" @click="setCurrent(p.val)"> {{ p.text }} </a>
      </li>
      <li :class="{'disabled': current == page}"><a href="javascript:;" class="page-main"
                                                    @click="setCurrent(current + 1)"> {{$t('Main.PageChange.Next')}}</a>
      </li>
      <li :class="{'disabled': current == page}"><a href="javascript:;" class="page-main" @click="setCurrent(page)">
        {{$t('Main.PageChange.BackPage')}} </a></li>
    </ul>
  </nav>
</template>

<script type="es6">
  export default {
    data() {
      return {
        current: this.currentPage
      }
    },
    props: {
      total: {// 数据总条数
        type: Number,
        default: 0
      },
      display: {// 每页显示条数
        type: Number,
        default: 10
      },
      currentPage: {// 当前页码
        type: Number,
        default: 1
      },
      pagegroup: {// 分页条数
        type: Number,
        default: 5,
        coerce: function (v) {
          v = v > 0 ? v : 5;
          return v % 2 === 1 ? v : v + 1;
        }
      }
    },
    computed: {
      page: function () { // 总页数
        return Math.ceil(this.total / this.display);
      },
      grouplist: function () { // 获取分页页码
        var len = this.page, temp = [], list = [], count = Math.floor(this.pagegroup / 2), center = this.current;
        if (len <= this.pagegroup) {
          while (len--) {
            temp.push({text: this.page - len, val: this.page - len});
          }
          ;
          return temp;
        }
        while (len--) {
          temp.push(this.page - len);
        }
        ;
        var idx = temp.indexOf(center);
        (idx < count) && (center = center + count - idx);
        (this.current > this.page - count) && (center = this.page - count);
        temp = temp.splice(center - count - 1, this.pagegroup);
        do {
          var t = temp.shift();
          list.push({
            text: t,
            val: t
          });
        } while (temp.length);
        if (this.page > this.pagegroup) {
          (this.current > count + 1) && list.unshift({text: '...', val: list[0].val - 1});
          (this.current < this.page - count) && list.push({text: '...', val: list[list.length - 1].val + 1});
        }
        return list;
      }
    },
    methods: {
      setCurrent: function (idx) {
        if (this.current != idx && idx > 0 && idx < this.page + 1) {
          this.current = idx;
          this.$emit('pagechange', this.current);
        }
      }
    }
  }
</script>

<style scoped lang="less">
  .pagination {
    overflow: hidden;
    display: table;
    margin: auto;
    /*float: right;*/
    /*width: 100%;*/
    height: 50px;
  }

  .pagination li {
    float: left;
    margin: 6px;
  }

  .pagination li a {
    color: #353b48;
  }

  .pagination li a.page-main {
    color: #959699;
  }

  .pagination li a.page-item {

  }

  .pagination .active a {
    background-color: #387BFF !important;
    color: #fff;
  }

  .pagination li a.page-item:hover {
    background-color: #e3eefa;
  }

  .pagination li a {
    display: block;
    min-width: 44px;
    text-align: center;
    line-height: 30px;
    font-size: 14px;
    text-decoration: none
  }

  .pagination li a.active {
    background: #000;
  }
</style>



