<template>
  <div class="task-examines">
    <panelTitle :crumbs="crumbs"></panelTitle>
    <Card dis-hover class="br8 mytheme-container-mg-30 mytheme-noborder noradius-card mytheme-card-padding">
      <div class="testPaper">
        <h3 class="exercises-name">{{periodname}}</h3>
        <p class="student-name">学生：{{studentName}}</p>
        <!--分数表-->
        <table class="score-table">
          <thead>
          <tr style="height:40px;">
            <th>题号</th>
            <th v-if="choiceQues.subject" v-for="(val,key,index) in getQuesNum(choiceQues.subject)">
              第{{index==0?'一':(index==1?'二':(index==2?'三':'四'))}}题
            </th>
            <th>总分</th>
          </tr>
          </thead>
          <tbody>
          <tr style="height:40px;">
            <td>分数</td>
            <td class="score-number" v-for="val in getQuesNum(choiceQues.subject)">{{totalScore(val, 2)}}</td>
            <td>{{getTotalScore(choiceQues.subject)}}</td>
          </tr>
          </tbody>
        </table>
        <div class="mytheme-mg-bottom-30" v-if="choiceQues.subject">
          <!-- 试卷展示-->
          <TaskContentComponent
            ref="TaskContentComponents"
            :reviewData="choiceQues.subject"
            :isshowEdit="true"
            :isdisable="isHasReaded"
            @errorTip="getErrorTip"
          >
          </TaskContentComponent>
        </div>
        <Button v-if="isShowSubmitBtn" class="mytheme-mg-top-30" type="primary" @click="showMarking()">确定</Button>
      </div>
    </Card>
  </div>
</template>

<script>
  import panelTitle from '@/components/panelTitle';
  import TaskContentComponent from '@/container/TaskManage/TaskContent';
  import Server from '@/service/request';
  import Path from '@/service/Path';
  import {Card, Button, Input, Form, FormItem} from 'iview';

  export default {
    name: "task-examines",
    components: {
      Card,
      panelTitle,
      Button,
      Input,
      Form,
      FormItem,
      TaskContentComponent,
    },
    data() {
      return {
        crumbs: [
          {
            name: '',
            route: ''
          },
          {
            name: '',
            route: ''
          },
          {
            name: '',
            route: ''
          },
          {
            name: '',
            route: ''
          }
        ],
        choiceQues: {
          subject: {},
        },
        isRead: false,
        isShowSubmitBtn: this.$route.query.isShowSubmitBtn,
        isHasReaded: (this.$route.query.reviewstatus === '' || this.$route.query.reviewstatus === undefined) ? true : this.$route.query.reviewstatus,
        studentName: this.$route.query.studentname || this.$route.query.nickname,
        periodname: this.$route.query.periodname || window.localStorage.getItem('HasExamines'),
        compositionVal: '',
        currentErrorTip: '',
      }
    },
    created() {
      this.initCrumbs();
      let {curriculumid, schedulingid, lessonsid, studentid} = this.$route.query;

      this.getStudentTask(curriculumid, schedulingid, lessonsid, studentid);
      if (this.$route.query.type == 'haveSH') {
        this.isRead = true;
        this.SubTitle = '已批阅';
      } else {
        this.isRead == false;
        this.SubTitle = '未批阅';
      }
    },
    methods: {
      /**
       * 获取错误信息
       */
      getErrorTip(msg) {
        this.currentErrorTip = msg;
      },
      initCrumbs() {
        this.crumbs[0].name = this.$route.query.level1Name;
        this.crumbs[0].route = this.$route.query.route;
        this.crumbs[1].name = this.$route.query.level2Name;
        this.crumbs[1].route = this.$route.query.level2Route;
        this.crumbs[2].name = this.$route.query.level3Name
        this.crumbs[2].route = this.$route.query.level3Route;
        this.crumbs[3].name = this.$route.query.level4Name || '未批阅';
        this.crumbs[3].route = this.$route.fullPath;
      },
      /**
       * 查询学生作业详情
       * @param curriculumid 课程id
       * @param schedulingid 班级id
       * @param lessonsid    课时id
       * @param studentid    学生id
       */
      getStudentTask(curriculumid, schedulingid, lessonsid, studentid) {
        Server.post(Path.showExerciselist, {
          curriculumid,
          schedulingid,
          lessonsid: lessonsid,
          studentid: studentid,
        }, (res) => {
          let {code, data} = res;
          if (code === 0) {
            this.choiceQues.subject = data;
          }
        });
      },
      /**
       * 删除数组为空的key
       */
      getQuesNum(obj) {
        for (let k in obj) {
          if (obj[k].length == 0) {
            delete obj[k];
          }
        }
        return obj;
      },
      /**
       * 计算总分
       * @param arr 每一大题的数组
       * @param type 1 2   1：每一大题的总分  2：老师计算给出的分数
       * @return {number}
       */
      totalScore(arr, type) {
        let sum = 0;
        arr.forEach(item => {
          if (type == 1) {
            sum += parseInt(item.score);
          } else {
            sum += parseInt(item.stuscore) || 0;
          }
        });
        return sum;
      },
      /**
       * 格式化提交数据
       * @param arr
       * @return {Array}
       */
      formatSubmitData(arr = []) {
        let itemArr = [];
        arr.forEach(el => {
          itemArr.push({
            workid: el.workid,
            score: el.stuscore || 0,//如果没有学生分数
            comment: el.comment,
          });
        });
        return itemArr;
      },
      /**
       * 批阅作业
       */
      showMarking() {
        let {studentid, lessonsid} = this.$route.query;
        let subject = this.$Utils.objectToArray(this.choiceQues.subject);
        let submitData = {
          'lessonsid': lessonsid,
          'studentid': studentid,
          'a': [],
          'b': [],
          'c': [],
          'd': [],
        };
        subject.forEach(el => {
          switch (el.keys) {
            case '1':
              submitData['a'] = this.formatSubmitData(el.value);
              break;
            case '2':
              submitData['b'] = this.formatSubmitData(el.value);
              break;
            case '3':
              submitData['c'] = this.formatSubmitData(el.value);
              break;
            case '4':
              submitData['d'] = this.formatSubmitData(el.value);
          }
        });
        if (!this.$refs.TaskContentComponents.submitValidate()) {
          this.$Message.error(this.currentErrorTip);
          return;
        }
        Server.post(Path.showMarking, submitData, (res) => {
          let {code, data, info} = res;
          if (code === 0) {
            this.$Message.success(info);
            this.$router.go(-2);
          } else {
            this.$Message.error(info);
          }
        });
      },
      getTotalScore(obj) {
        let arr = this.$Utils.objectToArray(obj), sum = 0;
        arr.forEach(el => {
          el.value.forEach(item => {
            sum += parseInt(item.stuscore);
          })
        });
        return sum;
      },
    }
  }
</script>

<style scoped lang="less">
  @import "~@/assets/less/variable";

  .score-table {
    width: 100%;
    text-align: center;
    font-size: 16px;
    border-collapse: collapse;
    td,
    th {
      border: 1px solid #000000;
    }
    .score-number {
      font-size: @font-size-normal-s;
      color: @color-red;
    }
  }

  .testPaper {
    width: 750px;
    margin: 0 auto;
    .exercises-name {
      padding: 60px 0 52px 0;
      text-align: center;
      font-size: 22px;
    }
    .student-name {
      padding: 0 0 25px 0;
      text-align: right;
      font-size: 14px;
    }
    dt, dd {
      margin: 0 0 0 40px;
    }
    dd {
      line-height: 40px;
    }
    p {
      line-height: 40px;
    }
    .student-composition {
      width: 710px;
      resize: none;
      outline: none;
      line-height: 1.5;
      margin-bottom: 20px;
    }
    .subject-title {
      font-size: @font-size-normal-s;
      font-weight: bold;
    }
    .item-title {
      padding: 10px 0;
      font-size: 14px;
    }
    .options {
      p {
        padding: 0 100px 0 0;
      }
    }
    .answer {
      color: @color-orange;
    }
    .correctanswer {
      color: @color-red;
    }
    .item-image {

    }
    .item-label {
      display: table-cell;
      width: 70px;
      vertical-align: top;
      color: @color-red;
    }
    .item-val {
      display: table-cell;
      vertical-align: top;
      .teacher-examines {
        margin-bottom: 20px;
        width: 640px;
        border-color: @color-red;
        resize: none;
        outline: none;
        line-height: 1.5;
      }
    }
  }
</style>



