<template>
  <div id="default-img">
    <img class="defaultimg" :src="sourceimg || defaultimg" @error="_imageError"/>
  </div>
</template>
<script>
  import defaultimg from '@/assets/images/defaultAvatar.png'

  export default {
    name: "default-img",
    props: {
      sourceimg: {
        default: '',
        type: String
      }
    },
    data() {
      return {
        defaultimg
      }
    }
  }
</script>
<style scoped lang="less">
  #default-img {
    height: 100%;
    img {
      vertical-align: top;
      width: 100%;
      height: 100%;
    }
  }

</style>



