<template>
  <div class="nm_menu" id="nm_menu">
    <Layout>
      <Sider class="sider" hide-trigger>
        <Menu class="menu-ul" width="200" theme="dark">
          <template v-if="courseBol">
          <router-link
            class="ivu-menu-item"
            to="/CalendarSchedule"
            :class="[{'router-link-active':'/'=== getRoute}, {'router-link-active':'/CalendarSchedule'=== getRoute}]"
            tag="li"
            exact> <!--课表-->
            <div class="menu-item-icon schedule"></div>
            <span>{{$t('Main.MyLessonList.Menu')}}</span>
          </router-link>
          <router-link
            class="ivu-menu-item"
            :class="{'router-link-active':'/MyClasses'=== getRoute}"
            to="/MyClasses"
            tag="li"
            exact
          > <!--我的班级-->
            <div class="menu-item-icon course"></div>
            <span>{{$t('Main.MyClasses.Menu')}}</span>
          </router-link>
          <router-link
            class="ivu-menu-item"
            :class="{'router-link-active':'/MaterialList'=== getRoute}"
            to="/MaterialList"
            tag="li"
            exact
            > <!--素材管理-->
            <div class="menu-item-icon material"></div>
            <span>{{$t('Main.MaterMessanage.Menu')}}</span>
          </router-link>
          <!--作业管理-->
          <router-link :class="{'router-link-active':'/TaskManage'=== getRoute}" to="/TaskManage" tag="li" exact
                       class="ivu-menu-item">
            <div class="menu-item-icon homework"></div>
            <span>{{$t('Main.TaskManage.Menu')}}</span>
          </router-link>
          <!--精彩瞬间-->
          <!--<router-link :class="{'router-link-active':'/WonderfulMoment'=== getRoute}" to="/WonderfulMoment" tag="li"-->
                       <!--exact class="ivu-menu-item">-->
            <!--<div class="menu-item-icon material"></div>-->
            <!--<span>{{$t('Main.WonderfulMoment.Menu')}}</span>-->
          <!--</router-link>-->
          <router-link :class="{'router-link-active':'/Mystudents'=== getRoute}" to="/Mystudents" tag="li" exact
                       class="ivu-menu-item"> <!--作业-->
            <div class="menu-item-icon student"></div>
            <span>{{$t('Main.MyStudent.Menu')}}</span>
          </router-link>
          <router-link :class="{'router-link-active':'/MyMessage'=== getRoute}" to="/MyMessage" tag="li" exact
                       class="ivu-menu-item"> <!--我的消息-->
            <div class="menu-item-icon message"></div>
            <span>{{$t('Main.MyMessage.Title')}}</span>
          </router-link>
          </template>
             <!-- 作文批改 -->
           <router-link  v-if="essayBol" :class="{'router-link-active':'/EssayMarking'=== getRoute}" to="/EssayMarking" tag="li" exact
                       class="ivu-menu-item">
            <div class="menu-item-icon composition"></div>
            <span>作文批改</span>
          </router-link>
          <router-link :class="{'router-link-active':'/TeacherEdit'=== getRoute}" to="/TeacherEdit" tag="li" exact
                       class="ivu-menu-item"> <!--设置-->
            <div class="menu-item-icon setting"></div>
            <span>{{$t('Main.Setting.Menu')}}</span>
          </router-link>
        </Menu>
      </Sider>
    </Layout>
  </div>
</template>

<script>
  import {Layout, Sider, Menu, MenuItem} from 'iview';

  export default {
    name: "nm_menu",
    components: {Layout, Sider, Menu, MenuItem},
    computed: {
      getRoute() {
        return this.$Utils.getUrlMenuQuery(this.$route.fullPath, 'route') || this.$route.fullPath;
      }
    },
    created(){
      this.teachertype=JSON.parse(this.$loca.getItem('teacher_info')).teachertype;
     
      this.teachertype.indexOf('1')=='-1'?(this.courseBol=false):(this.courseBol=true);
      this.teachertype.indexOf('2')=='-1'?(this.essayBol=false):(this.essayBol=true);
    },
     data() {
      return {
        teachertype:"",
        courseBol:true,
        essayBol:true
      }
    }
    // watch: {
    //   getRoute(val) {
    //     console.log(val);
    //   }
    // },
  }
</script>

<style scoped lang="less">
  @import "~@/assets/less/variable";

  .nm_menu#nm_menu {
    position: fixed;
    left: 0;
    top: 80px;
    bottom: 0;
    width: 200px;
    z-index: 2;
    .ivu-layout-has-sider {
      height: 100%;
      .sider {
        height: 100%;
        max-height: 960px;
        .menu-ul {
          width: 100%;
          height: 100%;
          border-right: 1px solid #dcdcdc;
          background: @color-highlight-blue;
          .ivu-menu-item {
            padding: 0;
            height: 74px;
            line-height: 74px;
            font-size: @font-size-small;
            .menu-item-icon {
              margin: 0 30px;
              display: inline-block;
              vertical-align: middle;
              margin-top: -1px;
              width: 20px;
              height: 20px;
            }
            // 课表
            .menu-item-icon.schedule {
              background: url(../../../assets/images/icon/icon_timetable_normal@2x.png) no-repeat center;
              background-size: 100% 100%;
            }
            // 我的班级
            .menu-item-icon.course {
              background: url(../../../assets/images/icon/icon_class_normal@2x.png) no-repeat center;
              background-size: 100% 100%;
            }
            // 素材管理
            .menu-item-icon.material {
              background: url(../../../assets/images/icon/icon_folder_normal@2x.png) no-repeat center;
              background-size: 100% 100%;
            }
            // 作业
            .menu-item-icon.homework {
              background: url(../../../assets/images/icon/icon_homework_normal@2x.png) no-repeat center;
              background-size: 100% 100%;
            }
            // 学生
            .menu-item-icon.student {
              background: url(../../../assets/images/icon/icon_student_normal@2x.png) no-repeat center;
              background-size: 100% 100%;
            }
            // 设置
            .menu-item-icon.setting {
              background: url(../../../assets/images/icon/icon_user_normal@2x.png) no-repeat center;
              background-size: 100% 100%;
            }
            // 我的消息
            .menu-item-icon.message {
              background: url(../../../assets/images/icon/icon_message_normal@2x.png) no-repeat center;
              background-size: 100% 100%;
            }
            // 作文批改
            .menu-item-icon.composition {
              background: url(../../../assets/images/icon/icon_composition_normal@2x.png) no-repeat center;
              background-size: 100% 100%;
            }
            &.router-link-active {
              background: #fff;
              color: @color-theme;
              // 课表
              .menu-item-icon.schedule {
                background: url(../../../assets/images/icon/icon_timetable@2x.png) no-repeat center;
                background-size: 100% 100%;
              }
              // 我的班级
              .menu-item-icon.course {
                background: url(../../../assets/images/icon/icon_class@2x.png) no-repeat center;
                background-size: 100% 100%;
              }
              // 素材管理
              .menu-item-icon.material {
                background: url(../../../assets/images/icon/icon_folder@2x.png) no-repeat center;
                background-size: 100% 100%;
              }
              // 作业
              .menu-item-icon.homework {
                background: url(../../../assets/images/icon/icon_homework@2x.png) no-repeat center;
                background-size: 100% 100%;
              }
              // 学生
              .menu-item-icon.student {
                background: url(../../../assets/images/icon/icon_student@2x.png) no-repeat center;
                background-size: 100% 100%;
              }
              // 设置
              .menu-item-icon.setting {
                background: url(../../../assets/images/icon/icon_user@2x.png) no-repeat center;
                background-size: 100% 100%;
              }
              // 我的消息
              .menu-item-icon.message {
                background: url(../../../assets/images/icon/icon_message@2x.png) no-repeat center;
                background-size: 100% 100%;
              }
               // 作文批改
            .menu-item-icon.composition {
              background: url(../../../assets/images/icon/icon_composition@2x.png) no-repeat center;
              background-size: 100% 100%;
            }
            }
          }
        }
      }
    }
  }
</style>



