<template>
  <div id="defaultData">
    <div class="default-img">
      <!--<img src="./assets/images/data_default.png" alt="">-->
      <p class="fontColorr" v-if="text">{{text}}</p>
      <p class="fontColorr" v-else>{{noData}}</p>
    </div>
  </div>
</template>
<script>
export default {
  name: "defaultData",
  props:{
    text:{
      default: '',
      type: String
    }
  },
  data() {
    return {
      noData: this.$t('Main.noData'),
    }
  },
  created() {

  }
}
</script>
<style scoped>
  .default-img{
    text-align: center;
    width: 100%;
    padding: 100px 0;
  }
  .fontColorr{
    color:rgba(164,171,179,1);
    font-size: 16px;
    margin-top: 20px;
  }
</style>



