<template>
  <div id="task-finished-datalist">
    <Card class="noradius-card mytheme-noborder mytheme-card-padding mytheme-content-minheight" dis-hover>
      <div class="mytheme-mg-top-20">
        <span class="mytheme-input-label">输入搜索：</span>
        <Input class="input-normal-width input-max-height" type="text" placeholder="请输入学员姓名"
               @on-enter="getTaskList(current, keyword)"></Input>
        <Button class="mytheme-mg-left-70" type="primary" @click="getTaskList(current, keyword)">搜索</Button>
      </div>
      <Table
        class="mytheme-table noborder-table thead-max-height tbody-min-height mytheme-mg-top-30"
        :columns="colData"
        :disabled-hover="true"
        :data="tabData"
        :stripe="true"
      >
      </Table>
      <pagination class="mytheme-mg-top-30" v-if="total > 0" :total="total" :display="display" :currentPage="current"
                  @pagechange="pageChange"></pagination>
    </Card>
  </div>
</template>
<script>
  import pagination from '@/components/nm_pagination';
  import Server from '@/service/request';
  import Path from '@/service/Path';
  import {Table, Input, Card, Button} from 'iview';

  export default {
    name: "task-finished-datalist",
    components: {
      Table, Input, Card, Button, pagination
    },
    data() {
      return {
        colData: [
          {key: 'studentid', title: '编号', align: 'center'},
          {key: 'nickname', title: '学员姓名', align: 'center'},
          {
            title: '提交时间', align: 'center', render: (h, params) => {
              return h('div', this.$Utils.formatStampStr(params.row.submittime, 'yyyy-MM-dd HH:mm:ss'));
            }
          },
          {
            title: '批阅状态', align: 'center', render: (h, params) => {
              return h('div', params.row.reviewstatus ? '已批阅' : '未批阅');
            },
          },
          {key: 'score', title: '成绩', align: 'center'},
          {
            key: 'option', title: '操作', render: (h, params) => {
              return h('a', {
                on: {
                  'click': () => {
                    this.$router.push({
                      path: '/Examines',
                      query: {
                        level1Name: this.$route.query.parentName,
                        route: this.$route.query.parentRoute,
                        level2Name: this.$route.query.name,
                        level2Route: this.$route.query.route,
                        level3Name: '已批阅',
                        level3Route: this.$route.fullPath,
                        level4Name: '查看详情',
                        curriculumid: this.$route.query.curriculumid,
                        schedulingid: this.$route.query.schedulingid,
                        lessonsid: this.$route.query.lessonsid,
                        studentid: params.row.studentid,
                        isShowSubmitBtn: false,
                        nickname: params.row.nickname,
                      }
                    });
                  }
                }
              }, '查看');
            }, align: 'center'
          }
        ],
        tabData: [],
        display: 20,
        current: 1,
        total: 0,
        keyword: '',
      }
    },
    created() {
      let {current, keyword} = this;
      this.getTaskList(current, keyword);
      // console.log(this.$route.query);
    },
    methods: {
      /**
       * 查询学生作业明细
       * @param pagenum 目标页码
       * @param keyword 学生姓名关键字
       */
      getTaskList(pagenum, keyword) {
        let {curriculumid, schedulingid, lessonsid} = this.$route.query;
        Server.post(Path.stuHomeworkList, {
          'pagenum': pagenum,
          'curriculumid': curriculumid,
          'schedulingid': schedulingid,
          'lessonsid': lessonsid,
          'reviewstatus': 1,
          'studentname': keyword,
        }, (res) => {
          let {code, data} = res;
          if (code === 0) {
            this.tabData = data.data;
            this.total = data.pageinfo.total;
          }
        });
      },
      pageChange(current) {

      },
    },
  }
</script>
<style lang="less" scoped>

</style>



