<template>
  <div class="task-contnet-components">
    <div class="subject" v-for='(item,k,index) in  delEmptyQues(reviewData)' :key="index">
      <p class="subject-title">
        <span>{{index==0?'一':(index==1?'二':(index==2?'三':'四'))}}、</span>
        <span>{{k=='1'?'单选题':(k=='2'?'多选题':(k=='3'?'填空题':'作文题'))}}。</span>
        <span>({{getQuesTotalScore(item,1)}}分)</span>
      </p>
      <dl class="item"
          v-for='(i,indexs) in item'
          :key="i.indexs"
          :class="((k==1||k==2)&&i.answer&&i.answer!=i.correctanswer)?'color-red':''">
        <dt class="item-title">{{indexs+1}}、{{i.name}} （{{i.score}}分)</dt>
        <!--题目图片：-->
        <dd class="item-image" v-if="i.imageurl">
          <div class="item-image-wrapper" v-for='(image, j) in transfer(i.imageurl)' :key="j">
            <img :src="`http://${image}`" @error="_imageError"/>
          </div>
          
        </dd>
        <!--选项：-->
        <dd class="options" v-if="k==1||k==2">
          <p class="inlineblock" v-for="(option, o) in (i.optionname||i.option)" :key="o">{{option}}</p>
        </dd>
        <!--学生答案：-->
        <dd v-if="k!=4">
          <p v-if="i.answer">学生答案：{{i.answer}}</p>
          <p :class="{'correctanswer':i.answer}" v-if="i.correctanswer">正确答案：{{i.correctanswer}}</p>
        </dd>
        <!--学生作文：-->
        <dd v-if="k==4">
          <Input class="student-composition" v-if="!isdisable" :rows="15" type="textarea" v-model="i.answer"/>
          <!-- <div class="student-composition border" v-else>{{i.answer}}</div> -->
          <p :class="{'correctanswer':i.analysis}" v-if="i.analysis">解析：{{i.analysis}}</p>
        </dd>
        <!--老师评阅：-->
        <dd v-if="k == 4 && isshowEdit">
          <Form class="mytheme-mg-top-20">
            <FormItem>
              <div class="item-label">评阅：</div>
              <div class="item-val">
                <Input class="teacher-examines" v-if="!isdisable" :rows="11" type="textarea" v-model="i.comment"/>
                <div class="teacher-examines border" v-else>{{i.comment}}</div>
              </div>
            </FormItem>
          </Form>
        </dd>
        <!--老师评分：-->
        <dd class="" v-if="(k==3||k==4) && isshowEdit">
          <Form :ref="'ruleValidate' + k +indexs" :model="i" :rules="ruleValidate" :key="indexs">
            <FormItem prop="stuscore">
              <div class="item-label">老师评分：</div>
              <div class="item-val" v-if="!isdisable">
                <Input
                  class="input-mini-width"
                  v-if="!isdisable"
                  v-model="i.stuscore"
                  @on-blur="getStudentScore(i)"
                /> 分
              </div>
              <div class="item-val border" v-else>{{i.stuscore}}分</div>
            </FormItem>
          </Form>
        </dd>
      </dl>
    </div>
  </div>
</template>

<script>
import { Input, Form, FormItem } from "iview";
import defaultCover from "@/assets/images/defaultCover.png";

export default {
  name: "index",
  props: ["reviewData", "isshowEdit", "isdisable"],
  components: {
    Input,
    Form,
    FormItem
  },
  data() {
    /**
     * 验证分数是否大于指定分数，阻止提交
     */
    const validateScoreCheck = (rule, value, callback) => {
      if (isNaN(value)) {
        this.errorTip = "请填写正确的分数";
        callback(new Error("请填写正确的分数"));
      }
      if (Number(value) > this.studentScore) {
        this.errorTip = "分数不得大于指定分数!";
        callback(new Error("分数不得大于指定分数!"));
      }
      if (value === "") {
        this.errorTip = "请填写分数";
        callback(new Error("请填写分数"));
      } else {
        callback();
      }
      this.$emit("errorTip", this.errorTip);
    };
    return {
      defaultCover,
      ruleValidate: {
        stuscore: [{ validator: validateScoreCheck, trigger: "blur" }]
      },
      studentScore: 0,
      errorTip: ""
    };
  },
  methods: {
    transfer(img) {
      if (!img) {
        return;
      }
      //图片格式的转换
      return img.split(",");
    },
    /**
     * 获取学生得分
     */
    getStudentScore(obj) {
      this.studentScore = obj.score;
    },
    /**
     * 删除数组为空的大题
     * @param obj 所有题目对象
     * @return Object 删除过后的题目对象
     */
    delEmptyQues(obj) {
      for (let k in obj) {
        if (obj[k].length == 0) {
          delete obj[k];
        }
      }
      return obj;
    },
    /**
     * 计算大题分数
     * @param arr 每个大题数组
     * @returns {number} 大题总分
     */
    getQuesTotalScore(arr) {
      let sum = 0;
      arr.forEach(item => {
        sum += item.score;
      });
      return sum;
    },
    /**
     * 提交前验证是否填写分数
     * @param name
     */
    submitValidate() {
      let ruleValidateArr = this.$Utils.objectToArray(this.$refs);
      let reviewData = this.$Utils.objectToArray(this.reviewData);
      let arr = [];
      ruleValidateArr.forEach((item, index) => {
        let name = item["keys"];
        //循环验证表单
        this.$refs[name][0].validate(valid => {
          if (valid) {
            arr.push(true);
          } else {
            arr.push(false);
          }
        });
      });
      let flag = arr.every(item => {
        return item === true;
      });
      if (flag) {
        return true;
      } else {
        return false;
      }
    }
  }
};
</script>

<style scoped lang="less">
@import "~@/assets/less/variable";

dt,
dd {
  margin: 0 0 0 40px;
}

p {
  line-height: 40px;
}

.subject-title {
  font-size: @font-size-normal-s;
  font-weight: bold;
}

.item-title {
  padding: 10px 0;
  font-size: 16px;
}

.options {
  p {
    padding: 0 30px 0 0;
    font-size: 14px;
  }
}


.item-image {
  font-size: 0;
  .item-image-wrapper {
    display: inline-block;
    width: 150px;
    height: 100px;
    margin: 0 15px 15px 0;
    img {
      display: inline-block;
      width: 100%;
      height: 100%;
    }
  }
}

.item-label {
  display: table-cell;
  width: 90px !important;
  vertical-align: top;
  color: @color-red;
}

.item-val {
  display: table-cell;
  vertical-align: top;
  .teacher-examines {
    &.border {
      border-width: 1px;
      border-style: solid;
      border-color: @color-red;
    }
    width: 620px;
    min-height: 200px;
    height: 100%;
    resize: none;
    outline: none;
    line-height: 1.5;
    textarea.ivu-input {
      height: 100% !important;
    }
  }
}

.student-composition {
  margin-top: 20px;
  width: 100%;
  min-height: 200px;
  &.border {
    border: 1px solid #666666;
  }
}
</style>



