<template>
  <div id="upload-picture">
    <Upload
      class="upload-picture-components"
      name="uploadFile"
      :action="host"
      :data="{allpathnode:tip}"
      :format="['jpg','jpeg','png']"
      :max-size="10240"
      :on-success="onsuccess"
      :show-upload-list="false">
      <slot></slot>
    </Upload>
  </div>
</template>

<script>
import { Upload } from "iview";

export default {
  name: "upload-picture",
  props: {
    // 分类值
    tip: {
      type: Array
    },
    _style: {
      type: Object,
      default() {
        return {
          width: "113px",
          height: "113px"
        };
      }
    }
  },
  data() {
    return {
      host:
        window.origin === "http://192.168.0.206:8080" ||
        window.origin === "http://192.168.0.206:8081"
          ? "http://www.lemonedu.com/teacher/Uploadfile/Upload"
          : "/teacher/Uploadfile/Upload"
    };
  },
  components: {
    Upload
  },
  methods: {
    // 返回上传成功的图片url
    onsuccess(file) {
      if (file.code != 0) {
        //上传失败
        this.$Message.warning(this.$t("Main.UploadFaild"));
      } else {
        //上传成功
        this.$Message.success(this.$t("Main.UploadSuccess"));
        this.$emit("avatarChange", `http://${file.data}`);
      }
    }
  }
};
</script>

<style scoped lang="less">
.upload-picture-components {
  cursor: pointer;
}
</style>



