<template>
  <div id="Course">
    <panel-title :crumbs="crumbs"></panel-title>
    <div class="mytheme-container-mg-30">
      <Card class="mytheme-card-padding mytheme-noborder noradius-card" dis-hover>
        <div class="mytheme-mg-top-20">
          <span class="mytheme-input-label">{{$t('Main.MyStudent.UserName') + $t('Main.Symbols.Colon')}}</span>
          <Input
            class="input-max-height input-normal-width"
            :placeholder="$t('Main.MyStudent.UserName')"
            v-model.trim="SearchName"
            @on-click="getUserList(InputText, SearchName, current)"
            @on-enter="getUserList(InputText, SearchName, current)"
          />
          <Button class="mytheme-mg-left-70" type="primary" @click="getUserList(InputText, SearchName, current)">搜索
          </Button>
          <div class="fr">
            <Button type="warning" @click="openSendMessage()">推送消息</Button>
            <!--<Button type="primary" @click="smsMsgModal=true">发送短信</Button>-->
          </div>
          <div class="clearfloat"></div>
        </div>
        <i-table
          class="mytheme-table noborder-table thead-max-height tbody-min-height mytheme-mg-top-30"
          ref="selection"
          :stripe="true"
          :disabled-hover="true"
          :columns="columns1"
          :data="data1"
          :no-data-text="$t('Main.MyStudent.NoStudentAreBuyingYourCourse')"
          @on-selection-change="getStudentIdArr"
        >
        </i-table>
        <pagination
          class="mytheme-mg-top-20"
          v-if="total > 0"
          :total="total"
          :current-page='current'
          :display="display"
          @pagechange="pagechange"
        >
        </pagination>
      </Card>
    </div>
    <Modal class="mytheme-modal" width="600px" v-model="sockectMsgModal">
      <div class="modal-head" slot="header">推送消息</div>
      <div class="modal-body">
        <Form class="modal-body-form" ref="formValidate" :model="formValidate" :label-width="110" :rules="ruleValidate">
          <FormItem label="推送标题：" prop="title">
            <Input class="input-max-width-l input-max-height" placeholder="请输入标题名称，长度不能超过14个字" :maxlength="14" type="text" v-model="formValidate.title"/>
          </FormItem>
          <FormItem label="推送内容：" prop="content">
            <Input class="input-max-width-l" type="textarea" placeholder="请输入内容，长度不能超过50个字" :maxlength="50" :rows="6" v-model="formValidate.content"/>
            <!-- <div>APP推送标题不能超过14个字，内容不能超过50个字。</div> -->
          </FormItem>
        </Form>
      </div>
      <div class="modal-foot" slot="footer">
        <Button class="modal-foot-btn" type="primary" @click="handleSubmit('formValidate')">确定</Button>
        <Button class="modal-foot-btn mytheme-btn" type="default" @click="sockectMsgModal = false">取消</Button>
      </div>
    </Modal>

    <!--<Modal class="mytheme-modal" width="600px" v-model="smsMsgModal">-->
    <!--<div class="modal-head" slot="header">群发短信</div>-->
    <!--<div class="modal-body">-->
    <!--<Form class="mytheme-form" :label-width="100">-->
    <!--<FormItem label="发送对象：">-->
    <!--<span class="sendnum">共<span class="red">{{getStudentNum()}}</span>个用户</span>-->
    <!--</FormItem>-->
    <!--<FormItem label="短信内容：">-->
    <!--<Input class="input-max-width-l" :maxlength="500" type="textarea" :rows="6"></Input>-->
    <!--<div class="hui">发送时系统会自动在短信前加上【柠檬教育】，请勿重复添加，内容上限不能超过<span class="red">500</span>字。</div>-->
    <!--<div class="sendnum">发送条数：<span class="red">{{getStudentNum()}}</span></div>-->
    <!--</FormItem>-->
    <!--</Form>-->
    <!--</div>-->
    <!--<div class="modal-foot mytheme-mg-bottom-30" slot="footer">-->
    <!--<Button class="modal-foot-btn" type="primary">确定</Button>-->
    <!--<Button class="modal-foot-btn" @click="smsMsgModal=false">取消</Button>-->
    <!--</div>-->
    <!--</Modal>-->
  </div>
</template>
<script>
  import panelTitle from "@/components/panelTitle";
  import pagination from "@/components/pagination";
  import CityList from '@/Api/city.json';
  import {
    Card,
    Input,
    Table,
    Switch,
    Button,
    Form,
    Row,
    Col,
    Select,
    Option,
    FormItem,
    DatePicker,
    Checkbox,
    Modal,
  } from "iview";
  import Server from '@/service/request';
  import Path from '@/service/Path';

  export default {
    name: "",
    components: {
      panelTitle,
      Card,
      Input,
      "i-table": Table,
      pagination,
      "i-switch": Switch,
      Button,
      Form,
      Row,
      Col,
      Select,
      Option,
      FormItem,
      DatePicker,
      Checkbox,
      CityList,
      Modal,
    },
    data() {
      return {
        crumbs: [
          {
            name: this.$t('Main.MyStudent.Menu'),
            route: '/Mystudents'
          }
        ],
        total: 0, // 记录总条数
        display: 20, // 每页显示条数
        current: 1, // 当前的页数
        SearchName: '',
        InputText: "",
        columns1: [
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title: this.$t('Main.MaterMessanage.No'),
            align: "center",
            key: 'id'
          },
          {
            title: this.$t('Main.MyStudent.Name'),
            align: "center",
            key: "nickname"
          },
          {
            title: this.$t('Main.MyStudent.Sex'),
            align: "center",
            render: (h, params) => {
              let that = this;

              function renderSex(param) { //param: 0未知   1男   2女
                if (param == 0) {
                  return '----';
                } else if (param == 1) {
                  return that.$t('Main.sex.man');
                } else if (param == 2) {
                  return that.$t('Main.sex.woman');
                }
              }

              return h("div", renderSex(params.row.sex));
            }
          },
          {
            title: this.$t("Main.MyStudent.Age"),
            align: "center",
            render: (h, params) => {
              function renderAge(param) {
                if (param == '') {
                  return '----';
                } else {
                  return param;
                }
              }
              return h('div', renderAge(params.row.age));
            }
          },
          {
            //地区
            title: this.$t("Main.MyStudent.Area"),
            align: "center",
            render: (h, params) => {
              return h("div", CityList[Number(params.row.country)] || '----');
            }
          },
          {
            title: this.$t("Main.MyStudent.Make"),
            align: "center",
            render: (h, params, index) => {
              return h("div", [
                h(
                  "a",
                  {
                    style: {
                      marginRight: '10px'
                    },
                    on: {
                      'click': () => {
                        this.$router.push({
                          path: '/StudentDetail',
                          query: {
                            stuID: params.row.id,
                            route: this.$route.fullPath,
                          }
                        });
                      }
                    },
                    attrs: {
                      class: 'mytheme-table-btn blue',
                    }
                  }, this.$t('Main.MyStudent.LookInfo'))
              ])
            }
          }
        ],
        data1: [],
        selectedStudentIdArr: [],

        sockectMsgModal: false,
        smsMsgModal: false,
        loading: true,
        formValidate: {
          title: '',
          content: '',
        },

        ruleValidate: {
          title: [
            {required: true, message: '请填写标题', trigger: 'blur', type: 'string',}
          ],
          content: [
            {required: true, message: '请填写内容', trigger: 'blur', type: 'string',},
          ],
        },
      };
    },
    created() {
      if (this.$Utils.isLogin()) {
        this.getUserList();
      } else {
        this.$router.replace('/Login');
        return;
      }
    },
    watch: {
      sockectMsgModal(val) {
        if (!val) {
          this.$refs.formValidate.resetFields();
        }
      },
    },
    methods: {
      openSendMessage() {
        if (this.selectedStudentIdArr.length === 0) {
          this.$Message.error('请选择要推送消息的学生');
          return;
        }
        this.sockectMsgModal = true;
      },
      /**
       * 查询学生列表
       * @param mobil 手机号
       * @param name  昵称
       * @param page  目标页码
       */
      getUserList(mobil = "", name = "", page = 1) {
        Server.post(
          Path.tch_getUserList,
          {
            mobil: mobil,
            nickname: name,
            pagenum: page
          },
          res => {
            let {code, data} = res;
            if (code === 0) {
              this.data1 = [];
              this.total = data.pageinfo.total;
              this.display = data.pageinfo.pagesize;
              this.current = data.pageinfo.pagenum;
              for (let k in data.data) {
                this.data1.push(data.data[k])
              }
            } else {
              this.data1 = [];
            }
            this.loading = false;
          }
        ).catch(err => {
          throw err;
        });
      },
      /**
       * 分页请求
       * @param currentPage 目标页码
       */
      pagechange: function (currentPage) {
        this.current = currentPage;
        this.getUserList(currentPage);
        // ajax请求, 向后台发送 currentPage, 来获取对应的数据
      },
      /**
       * 获取学生数量
       * @return {number}
       */
      getStudentNum() {
        return this.data1.length;
      },
      /**
       * 获取选中的学生id
       */
      getStudentIdArr(e) {
        this.selectedStudentIdArr = e.map(item => {
          return item.id;
        });
      },
      /**
       * 验证提交参数
       */
      handleSubmit(name) {
        let that = this;
        this.$refs[name].validate((valid) => {
          if (valid) {
            that.groupSend();
          }
        })
      },
      /**
       * APP 消息推送
       */
      groupSend() {
        Server.post(Path.noticeAll, {
          'studentarr': this.selectedStudentIdArr,
          'title': this.formValidate.title,
          'content': this.formValidate.content,
        }, (res) => {
          let {code, info} = res;
          if (code === 0) {
            this.$Message.success(info);
            this.sockectMsgModal = false;
          } else {
            this.$Message.error(info);
          }
        });
      },
    }
  }
</script>
<style scoped lang="less">

  .input-label {
    font-size: 18px;
    color: #7A8599;
  }

  .ivu-table-wrapper {
    overflow: hidden;
  }

  .CheckBox {
    padding: 20px;
  }

  .mytheme-modal {
    .modal-body {
      padding: 0 30px;
      .sendnum {
        font-size: 14px;
      }
    }
  }

</style>



