<template>
  <div id="coursedetail-rating">
    <div class="courseinfo-title" v-if="getNowTime(endtime)">
			<img class="icon" src="../assets/icon_evaluate.png">
			<span class="title">{{$t('Main.MyLessonList.StudentRattings')}}</span>
		</div>
		<div class="studentEvaluate" v-if="getNowTime(endtime)">
			<span>{{$t('Main.MyLessonList.StudentRattings')}}</span>
			<!--评论数量-->
			<span class="evaluateCount">（{{total}}）</span>
		</div>
		<div class="ratting-panel mytheme-mg-bottom-20" v-for="(comment, i) in commentList" :key="i" v-if="getNowTime(endtime)">
			<div class="person-avatar">
				<Avatar :src="comment.imageurl" class="avatar" />
			</div>
			<div class="preson-content">
				<div class="content-main">
					<!--学生昵称-->
					<h3>{{comment.nickname}}</h3>
					<!--评分-->
					<Rate show-text allow-half v-model="comment.score" disabled class="rate">
						<span class="rate-srore">{{comment.score}}{{$t('Main.MyLessonList.Star')}}</span>
					</Rate>
				</div>
				<!--评论详情-->
				<p class="rating-content">{{comment.content}}</p>
				<div class="ratting-footer">
					<p>{{$t('Main.MyLessonList.TheClass')+$t('Main.Symbols.Colon')}}{{courseInfo.listPeriodinfo.gradename}}</p>
					<p>{{$t('Main.MyLessonList.Lesson')+$t('Main.Symbols.Colon')}}{{courseInfo.listPeriodinfo.coursename}}</p>
					<p class="ratting-time">{{comment.addtimedate}}</p>
				</div>
			</div>
		</div>
    <!--分页-->
		<pagination v-if="total > 0" class="mytheme-mg-top-20" :total="total" :display="display" :currentPage="current" @pagechange="pageChange"></pagination>
  </div>
</template>
<script>
import Server from "@/service/request";
import requestPath from "@/service/Path";
import pagination from "@/components/pagination";
import { Avatar, Rate } from "iview";
export default {
  name: "coursedetail-rating",
  props: ["courseInfo"],
  components: { pagination, Avatar, Rate },
  data() {
    return {
      endtime: this.$route.query.endtime,
      total: 0,
      current: 1,
      display: 10,
      commentList: []
    };
  },
  created() {
    this.getCourseComment(this.current);
  },
  methods: {
    getNowTime(d2) {
      let date = new Date(),
        year = date.getFullYear(),
        month = date.getMonth() + 1,
        day = date.getDate(),
        hour = date.getHours(),
        minute = date.getMinutes();
      let dateStr = `${year}-${month}-${day} ${hour}:${minute}`;
      if (d2) {
        return (
          new Date(dateStr.replace(/-/g, "/")) > new Date(d2.replace(/-/g, "/"))
        );
      }
    },
    /**
     * 查询评论
     * @param  {Number} current 当前页码
     */
    getCourseComment(current) {
      Server.post(
        requestPath.getperComment,
        {
          date: this.$route.query,
          pagenum: current,
          pagesize: this.display,
          lessonsid: this.$route.query.lessonsid
        },
        res => {
          if (res.code === 0) {
            this.commentList = res.data.data;
            this.current = res.data.pageinfo.pagenum;
            this.total = res.data.pageinfo.total;
            this.display = res.data.pageinfo.pagesize;
          }
        }
      ).catch(err => {
        throw err;
      });
    },
    /**
     * 分页请求
     * @param  {Number} currentPage 当前页码
     */
    pageChange(currentPage) {
      this.getCourseComment(currentPage);
    }
  }
};
</script>
<style lang="less" scoped>
@import "../assets/courseinfo-title.less";
.studentEvaluate {
  background: rgba(242, 246, 245, 1);
  margin-top: 30px;
  border-radius: 4px;
  height: 46px;
  line-height: 46px;
  margin-bottom: 20px;
  padding-left: 20px;
  .evaluateCount {
    font-size: 14px;
    color: #f19235;
  }
}

.ratting-panel {
  padding: 30px 40px;
  border-radius: 10px;
  box-shadow: 0px 0px 10px rgba(140, 157, 158, 0.16);
  .person-avatar {
    float: left;
    height: 100%;
    margin-right: 22px;
    .avatar {
      width: 68px;
      height: 68px;
      border: #469af8 2px solid;
      box-shadow: 0px 0px 5px rgba(140, 157, 158, 0.81);
      border-radius: 50%;
    }
  }
  .preson-content {
    overflow: hidden;
    .content-main {
      h3 {
        float: left;
        font-size: 16px;
        color: rgba(47, 49, 51, 1);
      }
      .rate {
        float: right;
        .rate-srore {
          color: #f5496d;
          font-size: 16px;
          margin-left: -8px !important;
        }
      }
    }
    .rating-content {
      clear: both;
      font-size: 14px;
      color: rgba(94, 98, 102, 1);
      line-height: 30px;
      padding-top: 15px;
      margin-bottom: 18px;
      padding-right: 30px;
    }
    .ratting-footer {
      p {
        display: inline-block;
        font-size: 14px;
        color: #525866;
        &:nth-child(1) {
          margin-right: 89px;
        }
        &.ratting-time {
          float: right;
          font-size: 16px;
          font-family: PingFang-SC-Medium;
          color: rgba(125, 130, 135, 1);
        }
      }
    }
  }
}
</style>



