export const myDate = {
  /**
   * 获取当前时间
   * @param d2 时间字符串
   * @returns {boolean} 是否大于目标时间
   */
  getNowTime(d2) {
    let date = new Date(),
      year = date.getFullYear(),
      month = date.getMonth() + 1,
      day = date.getDate(),
      hour = date.getHours(),
      minute = date.getMinutes();
    let dateStr = `${year}-${month}-${day} ${hour}:${minute}`;
    /**
     * 如果存在参数，返回当前时间是否大于目标时间，否则返回当前时间字符串
     */
    if (d2) {
      return (
        new Date(dateStr.replace(/-/g, "/")) >= new Date(d2.replace(/-/g, "/"))
      );
    } else {
      return dateStr;
    }
  }
};

/**
 * 返回错误码
 * @type {number}
 */
export const ERR_OK = 0;

export const ERR_CODE = {
  ERR_OK: 0,

};




