<template>
  <div id="task-finished-overview">
    <Card class="noradius-card mytheme-card-padding mytheme-noborder mytheme-content-minheight" dis-hover>
      <table class="overview-table">
        <tr>
          <td class="overview-table-label">课程名称</td>
          <td class="overview-table-val">{{courseName}}</td>
        </tr>
        <tr>
          <td class="overview-table-label">班级名称</td>
          <td class="overview-table-val">{{className}}</td>
        </tr>
        <tr>
          <td class="overview-table-label">习题名称</td>
          <td class="overview-table-val">{{exercisesName}}</td>
        </tr>
        <tr>
          <td class="overview-table-label">作业提交率</td>
          <td class="overview-table-val">{{worknum}} / {{stunum}} = {{ConversionPercentage(submitPrecent)}}%</td>
        </tr>
        <tr>
          <td class="overview-table-label">平均成绩</td>
          <td class="overview-table-val">{{aveageScore}}</td>
        </tr>
        <tr>
          <td class="overview-table-label">选择题正确率</td>
          <td class="overview-table-val">
            <ul v-if="correct.length > 0">
              <li v-for="(el, i) in correct" :key="i">
                第{{toChinesNum(el.keys)}}大题
                <ul>
                  <li v-for="(item, k, key) in el.value" :key="k">
                    <span>第{{key+1}}小题：{{item.corrnum}} / {{item.$stunum}} = {{item.percent}}%</span>
                  </li>
                </ul>
              </li>
            </ul>
            <div v-else>无选择题</div>
          </td>
        </tr>
      </table>
    </Card>
  </div>
</template>
<script>
  import Server from '@/service/request';
  import Path from '@/service/Path';
  import {Card, Table, Input} from 'iview';

  export default {
    name: "task-finished-overview",
    components: {
      Card, Table, Input,
    },
    data() {
      return {
        courseName: '',    //课程名称
        className: '',     //班级名称
        exercisesName: '', //习题名称
        submitPrecent: '', //作业提交率
        aveageScore: '',   //平均分
        worknum: '',       //学生提交数量
        stunum: '',        //学生总数
        correct: [],
      }
    },
    created() {
      let {curriculumid, schedulingid, lessonsid} = this.$route.query;
      this.getTotalHomeWork(curriculumid, schedulingid, lessonsid);
    },
    methods: {
      /**
       * 查询作业统计表
       * @param curriculumid 课程id
       * @param schedulingid 班级id
       * @param lessonsid    课时id
       */
      getTotalHomeWork(curriculumid, schedulingid, lessonsid) {
        Server.post(Path.totalHomework, {
          curriculumid,
          schedulingid,
          lessonsid,
        }, (res) => {
          let {code, data} = res;
          if (code === 0) {
            this.courseName = data.curriculumname;
            this.className = data.gradename;
            this.exercisesName = data.periodname;
            this.aveageScore = data.avgscore;
            this.submitPrecent = data.homepercent;
            this.worknum = data.worknum;
            this.stunum = data.stunum;
            this.correct = this.$Utils.objectToArray(data.percent);
            window.localStorage.setItem('HasExamines', data.periodname);///课时名称
          }
        });
      },
      /**
       * 数字转化大写
       * @param num
       * @return {*}
       */
      toChinesNum(num) {
        let changeNum = ['零', '一', '二', '三', '四', '五', '六', '七', '八', '九']; //changeNum[0] = "零"
        let unit = ["", "十", "百", "千", "万"];
        num = parseInt(num);
        let getWan = (temp) => {
          let strArr = temp.toString().split("").reverse();
          let newNum = "";
          for (var i = 0; i < strArr.length; i++) {
            newNum = (i == 0 && strArr[i] == 0 ? "" : (i > 0 && strArr[i] == 0 && strArr[i - 1] == 0 ? "" : changeNum[strArr[i]] + (strArr[i] == 0 ? unit[0] : unit[i]))) + newNum;
          }
          return newNum;
        }
        let overWan = Math.floor(num / 10000);
        let noWan = num % 10000;
        if (noWan.toString().length < 4) noWan = "0" + noWan;
        return overWan ? getWan(overWan) + "万" + getWan(noWan) : getWan(num);
      },
      /**
       * 小数转化百分比
       * @param num
       * @constructor
       */
      ConversionPercentage(num) {
        return num * 100;
      },
    },
  }
</script>
<style lang="less" scoped>
  .overview-title {
    font-size: 16px;
    font-weight: bold;
    text-align: center;
  }

  .overview-table {
    margin-top: 56px;
    max-width: 950px;
    width: 100%;
    border-collapse: collapse;
    border: 1px solid transparent;
    tr {
      &:last-child {
        height: 210px;
      }
    }
    td {
      border: 1px solid #E6E6E6;
      height: 56px;
      text-align: center;
      &.overview-table-label {
        width: 260px;
      }
      &:first-child {
        background: #F7F7F7;
      }
      &:last-child {
        padding: 0 0 0 150px;
        text-align: left;
      }
      ul {
        margin: 0 0 10px 0;
        ul {
          padding: 0 0 0 10px;
        }
      }
    }
  }
</style>



