<template>
  <div id="task-finished">
    <panelTitle :crumbs="crumbs"></panelTitle>
    <div class="mytheme-container-mg-30">
      <Tabs class="mytheme-tab" type="card" :animated="false">
        <TabPane label="作业统计">
          <OverView></OverView>
        </TabPane>
        <TabPane label="筛选查询">
          <DataList></DataList>
        </TabPane>
      </Tabs>
    </div>
  </div>
</template>
<script>
  import panelTitle from '@/components/panelTitle';
  import OverView from './OverView';
  import DataList from './DataList';
  import {Tabs, TabPane} from 'iview';

  export default {
    name: "task-finished",
    components: {
      OverView,
      panelTitle,
      DataList,
      Tabs,
      TabPane,
    },
    data() {
      return {
        crumbs: [
          {name: '', route: ''},
          {name: '', route: ''},
          {name: '已批阅', route: ''}
        ],
      }
    },
    created() {
      this.initCrumbs();
    },
    methods: {
      initCrumbs() {
        this.crumbs[0].name = this.$route.query.parentName;
        this.crumbs[0].route = this.$route.query.parentRoute;
        this.crumbs[1].name = this.$route.query.name;
        this.crumbs[1].route = this.$route.query.route;
        this.crumbs[2].route = this.$route.fullPath;
      }
    }
  }
</script>
<style lang="less" scoped>
</style>



