<template>
  <div id="coursedetail-playback">
    <!-- 课程回放 -->
    <Card class="mytheme-content-minheight noradius-card mytheme-noborder mytheme-card-padding" dis-hover>
      <div class="playback-content" v-if="getNowTime(endtime)">
        <div class="playback-novideo" v-if="playback.length === 0">
          <img :src="icon">
          <p>{{$t('Main.MyLessonList.PlaybackInProduction')}}</p>
        </div>
        <div v-else class="playback-video-container">
          <!--播放区域-->
          <div class="playback-video-ifream">
            <img :src="stop" class="stopimg" @click="play" v-show="imgflag">
            <img :src="bgpic" class="bgpic" v-if="imgflag">
            <iframe id="video" name="video" :src="playpath " width="100%" height="100%"></iframe>
          </div>
          <!--播放列表-->
          <div class="playback-videolist">
            <ul>
              <li
                :class="{'active':index===checkindex }"
                :title="item.duration"
                v-for="(item,index) in playback.video"
                :key="index"
                @click="videoShow(index);
                imgflag = false;"
              >
                <span class="videoname">第{{index+1}}片段</span>
                <span class="videotime">{{item.duration}}</span>
              </li>
            </ul>
          </div>
        </div>
      </div>
      <div class="studentEvaluate" v-if="getNowTime(endtime)">评论列表</div>
      <div class="ratting-panel" v-for="(comment, i) in commentList" :key="i" v-if="getNowTime(endtime)">
        <div class="person-avatar">
          <Avatar :src="comment.imageurl || defaultHeadImg" class="avatar"/>
        </div>
        <div class="preson-content">
          <div class="content-main">
            <!--学生昵称-->
            {{comment.content}}
            <h3>{{comment.nickname}}</h3>
            <!--评分-->
            <Rate allow-half v-model="comment.score" disabled class="rate">
              <!--<span class="rate-srore">{{comment.score}}{{$t('Main.MyLessonList.Star')}}</span>-->
            </Rate>
          </div>
          <!--评论详情-->
          <p class="rating-content">{{comment.content}}</p>
          <div class="ratting-footer">
            <!--<p>{{$t('Main.MyLessonList.TheClass')+$t('Main.Symbols.Colon')}}{{lessonInfo.listPeriodinfo.gradename}}</p>-->
            <!--<p>{{$t('Main.MyLessonList.Lesson')+$t('Main.Symbols.Colon')}}{{lessonInfo.listPeriodinfo.coursename}}</p>-->
            <p class="ratting-time">{{comment.addtimedate}}</p>
          </div>
        </div>
      </div>
      <!--分页-->
      <pagination
        v-if="total > 0"
        class="mytheme-mg-top-20"
        :total="total"
        :display="display"
        :currentPage="current"
        @pagechange="pageChange">
      </pagination>
    </Card>
  </div>
</template>
<script>
  import stop from "@/assets/images/iconstop.png";
  import bgpic from "@/assets/images/bg.png";
  import icon from "@/assets/images/icon.png";
  import defaultHeadImg from '@/assets/images/default.png';
  import StudentRating from '@/container/CalendarSchedule/LessonDetailsEdit';
  import Server from '@/service/request';
  import Path from '@/service/Path';
  import pagination from '@/components/pagination';
  import {Card, Avatar, Rate} from 'iview';

  export default {
    name: "coursedetail-playback",
    props: ['playback', 'lessonInfo'],
    components: {
      Card, Avatar, Rate,
      StudentRating,
      pagination,
    },
    data() {
      return {
        stop,
        bgpic,
        icon,
        defaultHeadImg,
        endtime: this.$route.query.endtime,
        playpath: "",
        checkindex: "",
        imgflag: true,
        total: 0,
        current: 1,
        display: 10,
        commentList: []
      }
    },
    created() {
      this.getCourseComment(this.current);
      console.log(this.$route.query);
    },
    methods: {
      /**
       * 获取当前时间
       */
      getNowTime(d2) {
        let date = new Date(),
          year = date.getFullYear(),
          month = date.getMonth() + 1,
          day = date.getDate(),
          hour = date.getHours(),
          minute = date.getMinutes();
        let dateStr = `${year}-${month}-${day} ${hour}:${minute}`;
        if (d2) {
          return (
            new Date(dateStr.replace(/-/g, "/")) > new Date(d2.replace(/-/g, "/"))
          );
        }
      },
      /**
       * 查询评论
       * @param  {Number} current 当前页码
       */
      getCourseComment(current) {
        Server.post(
          Path.getperComment,
          {
            date: this.$route.query.date,
            pagenum: current,
            pagesize: this.display,
            lessonsid: this.$route.query.lessonsid
          },
          res => {
            if (res.code === 0) {
              this.commentList = res.data.data;
              this.current = res.data.pageinfo.pagenum;
              this.total = res.data.pageinfo.total;
              this.display = res.data.pageinfo.pagesize;
            }
          }
        ).catch(err => {
          throw err;
        });
      },
      /**
       * 分页请求
       * @param  {Number} currentPage 当前页码
       */
      pageChange(currentPage) {
        this.getCourseComment(currentPage);
      },
      play() {
        this.playpath = this.playback.video[0].playpath;
        this.checkindex = 0;
        this.imgflag = !this.imgflag;
      },
      videoShow(index) {
        this.checkindex = index;
        this.playpath = this.playback.video[index].playpath;
      },
    }
  }
</script>
<style lang="less" scoped>
  @import "~@/assets/less/variable";

  .playback-content {
    position: relative;
    margin: 44px 0 0 0;
    max-width: 1396px;
    height: 569px;
    .playback-novideo {
      width: 100%;
      height: 100%;
      font-size: 14px;
      text-align: center;
      background: rgba(243, 243, 243, 1);
      img {
        margin-top: 250px;
      }
    }
    .playback-video-container {
      position: relative;
      width: 100%;
      height: 100%;
      .playback-video-ifream {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 250px;
        .bgpic {
          width: 100%;
          height: 100%;
          position: absolute;
          top: 0;
          left: 0;
          bottom: 0;
          right: 0;
        }
        .stopimg {
          position: absolute;
          top: 0;
          left: 0;
          bottom: 0;
          right: 0;
          margin: auto;
          z-index: 2;
          width: 70px;
          height: 70px;
        }
      }
      .playback-videolist {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        width: 250px;
        background: rgba(243, 243, 243, .5);
        &:before {
          z-index: 2;
          content: ' ';
          position: absolute;
          left: 28px;
          top: 0;
          bottom: 0;
          width: 1px;
          border-left: 1px dashed #CFDCE7;
        }
        ul {
          li {
            position: relative;
            padding: 0 0 0 46px;
            height: 34px;
            line-height: 34px;
            font-size: 14px;
            user-select: none;
            cursor: pointer;
            border-left: 2px solid rgba(243, 243, 243, .5);
            color: #8E9DAA;
            .videoname {
              display: inline-block;
              width: 130px;
              white-space: nowrap;
              text-overflow: ellipsis;
              overflow: hidden;
            }
            .videotime {
              position: absolute;
              right: 17px;
            }
            &:before {
              z-index: 3;
              content: ' ';
              display: block;
              position: absolute;
              top: 0;
              left: 23px;
              bottom: 0;
              margin: auto;
              width: 7px;
              height: 7px;
              background: #CFDCE7;
              border-radius: 7px;
            }
            &.active {
              background: #fff;
              color: #33D7B7;
              border-left: 2px solid #33D7B7;
              &:before {
                background: #33D7B7;
              }
            }
          }
        }
      }
    }
  }
  .studentEvaluate {
    line-height: 1;
    padding: 54px 0 20px 0;
    border-bottom: 1px solid @color-gray-bg;
    font-size: @font-size-normal;
  }

  .ratting-panel {
    display: flex;
    padding: 36px 0;
    border-bottom: 1px solid @color-gray-bg;
    font-size: 0;
    .person-avatar {
      display: table-cell;
      width: 100px;
      height: 100%;
      vertical-align: top;
      .avatar {
        width: 70px;
        height: 70px;
        border-radius: 50%;
      }
    }
    .preson-content {
      display: table-cell;
      flex: 1;
      overflow: hidden;
      .content-main {
        h3 {
          display: inline-block;
          font-size: 16px;
          font-weight: normal;
          line-height: 2;
          color: #4D4D4D;
        }
        .rate {
          vertical-align: top;
          display: inline-block;
          padding: 0 0 0 14px;
          .rate-srore {
            color: #f5496d;
            font-size: 16px;
          }
        }
      }
      .rating-content {
        padding: 10px 0 20px 0;
        font-size: 14px;
        line-height: 30px;
        color: #808080;
        word-break: break-all;
        word-wrap: break-word;
        white-space: pre-wrap;
      }
      .ratting-footer {
        p {
          display: inline-block;
          font-size: 14px;
          color: #525866;
          &:nth-child(1) {
            margin-right: 89px;
          }
          &.ratting-time {
            font-size: 12px;
            color: rgba(125, 130, 135, 1);
          }
        }
      }
    }
  }
</style>



