import http from 'axios';
import qs from 'qs';
import sha1 from 'sha1';
import md5 from 'md5';
import {  Message } from "iview";
import router from '../router/index';

function getStr(arr, key, starttime, token) {
  var tostr = arr?getStrings(arr, ""):"";
  return md5(sha1(md5(tostr) + md5(key) + starttime) + token);
}

function getStrings(data, str) {
  for (var x in data) {
    if (isJson(data[x])) {
      var i=0;
      for (var k in data[x]) {
        i++;
        break;
      }
      if(i>0){
        str += x;
        str = getStrings(data[x], str);
      }
    }else if(isArray(data[x])){
      if(data[x].length!=0){
        str += x;
        str = getStrings(data[x], str);
      }
    } else {
      if(data[x]!==undefined && data[x]!==null && data[x]!==''&& data[x]!==false && data[x] !==true){
        str += x.toString() + data[x].toString();
      }
    }
  }
  return str;
}

function isArray(o) {
  return Object.prototype.toString.call(o) == "[object Array]";
}

function isJson(obj) {
  return (
    typeof obj == "object" &&
    Object.prototype.toString.call(obj).toLowerCase() == "[object object]" &&
    !obj.length
  );
}

// config http
let baseUrl = '';
if (process.env.NODE_ENV === 'development') {
  baseUrl = 'http://www.lemonedu.com/'
  // baseUrl = 'http://ningmeng.talk-cloud.cn'
} else if (process.env.NODE_ENV === 'production') {
  baseUrl = '/'
}
http.defaults.baseURL = baseUrl;

// http.defaults.timeout = 2500
http.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';
// cache-control: "max-age=0, private, must-revalidate"
// http.defaults.transformRequest = [function (data) {
//   return qs.stringify(data);
// }];
// http.defaults.headers.post['Cache-control'] = 'max-age=5'
// Add a request interceptor 解决ie下url带中文参数乱码问题

// 自动设置语言
const navLang = (navigator.language || navigator.browserLanguage).toLowerCase();
const lang = navLang.indexOf('zh') != -1 ? 'zh-cn' : 'en-us';
http.interceptors.request.use(function (config) {
  // Do something before request is sent
  var starttime = Date.parse(new Date());
  config.headers.common['lang'] = lang;
  if (config.method == 'get') {
    config.url = encodeURI(config.url);
  }
  if (localStorage.getItem('teacher_info') == null || localStorage.getItem('teacher_info') == '' ||
    localStorage.getItem('teacher_key') == null || localStorage.getItem('teacher_key') == '') {
    localStorage.removeItem('teacher_info');
    localStorage.removeItem('teacher_key');
    // window.location.href = location.origin + '/teacher/login/login.html';
  } else {
    let token = JSON.parse(window.localStorage.getItem('teacher_info')).token;
    var data = qs.parse(config.data);

    config.headers.common['sign'] = getStr(data, localStorage.getItem("teacher_key"), starttime, token);
    config.headers.common['token'] = String(token);
    config.headers.common['starttime'] = String(starttime);
    // config.headers.common['lang'] = lang;

  }
  return config;
}, function (error) {
  // Do something with request error
  return Promise.reject(error);
});

let tipCount = 0;


// Add a response interceptor
http.interceptors.response.use(function (response) {
  // Do something with response data
  if(response.data.code === -40602) {//您暂无操作权限
    Message.error(response.data.info);
  }
  if (response.data.code === -40666) {//您的账号在其他端口登陆了，请重新登陆
    if(tipCount === 0) {
      Message.error(response.data.info);
      tipCount++;
    }
    window.localStorage.removeItem('teacher_info');
    window.localStorage.removeItem('teacher_key');
    router.push('/Login');
  } else {
    return response.data;
  }
}, function (error) {
  // Do something with response error
  if (error.response) {
    if (error.response.status === 400) {
      Message.error('数据错误！');
    } else if (error.response.status === 401) {
      // define window.app=vm in main.js
      // window.app.$router.push({name: 'Login'});
    } else if (error.response.status === 403) {
      Message.error('您没有操作权限哦！');
    } else if (error.response.status === 404) {
      Message.error('对象不存在！');
    } else if (error.response.status === 500) {
      Message.error('服务器发生了一些错误哦！');
    }
  }
  return Promise.reject(error);
});

export default http;



