<template>
  <div class="calendar_schedule" id="Main">
    <panel-title :crumbs="crumbs"></panel-title>
    <Card dis-hover class="mytheme-container-mg-30 mytheme-pd-bottom-30 noradius-card mytheme-noborder">
      <div class="CalendarBox">
        <div class="Calendar-content">
          <div class="Cal-table-head">
            <Button class="today-btn" @click="getToDayData">{{$t('Main.MyLessonList.Today')}}</Button>
            <div class="Cal-table-head-prev Cal-table-head-btn" @click="getPrevData()"></div>
            <div class="Cal-table-head-currentMonth">{{y}}年{{Zh[m > 12 ? 0 : m - 1]}}</div>
            <div class="Cal-table-head-next Cal-table-head-btn" @click="getNextData()"></div>
          </div>
          <div class="Col-table-content">
            <table class="Cal-table" border="1" cellspacing="0" cellpadding="0">
              <thead>
              <tr>
                <th>一</th>
                <th>二</th>
                <th>三</th>
                <th>四</th>
                <th>五</th>
                <th>六</th>
                <th>日</th>
              </tr>
              </thead>
              <tbody>
              <tr v-for="da,index in data" :key="index">
                <td v-for="d,i in da" :key="i">
                  <div
                    class="cal-item-box"
                    :class="{
                      'activeMonth':d.activeMonth,
                      'pre-month-active':d.prevMonth || d.nextMonth,
                      'active': d.active,
                      'had-class-avtive':d.had,
                      'next-class-avtive':d.dai,
                      'today-prev-active':getNowTime(`${d.timestr}`, 1),
                      'today-next-active':getNowTime(`${d.timestr}`, 0),
                      'nextmonth-class-active':getNowTime(`${d.year}-${d.month}`, 2),
                      }"
                    @click="getDataList(d.year,d.month,d.day,d.active?'active':(d.had?'prev':'next'), d)"
                  >
                    <span class="cal-item-date" v-if="!d.active">{{d.day}}</span>
                    <!-- 共x节课 -->
                    <span class="cal-item-text" v-if="d.active || d.had || d.dai">{{d.num}}{{$t('Main.MyLessonList.ALesson')}}</span>
                  </div>
                </td>
              </tr>
              </tbody>
            </table>
          </div>
        </div>
        <!--<div class="Cal-table-tip" v-if="todoList">-->
          <!--<div class="table-tip-triangle"></div>-->
          <!--<div class="table-tip-content">-->
            <!--<div class="table-tip-content-title">课程安排：</div>-->
            <!--<div class="table-tip-content-coursetime" v-for="(el, i) in todoList" :key="i">-->
              <!--<span class="time">{{el.todayStartTime}}~{{el.todayEndTime}}</span>-->
              <!--<span class="course">{{el.periodname}}</span>-->
            <!--</div>-->
          <!--</div>-->
        <!--</div>-->
      </div>
    </Card>
  </div>
</template>
<script>
  import panelTitle from "@/components/panelTitle";
  import {Card, Button} from "iview";
  import Server from "@/service/request";
  import Path from "@/service/Path";

  import left from "@/assets/images/left.png";
  import right from "@/assets/images/right.png";

  export default {
    name: "Main",
    components: {
      Card,
      Button,
      panelTitle
    },
    data() {
      return {
        nowDate: this.getNowTime(),
        left,
        right,
        data: [],
        y: 0,
        m: 0,
        day: 0,
        En: [
          "January",
          "February",
          "March",
          "April",
          "May",
          "June",
          "July",
          "August",
          "September",
          "October",
          "November",
          "December"
        ],
        Zh: [
          this.$t("Main.January"),
          this.$t("Main.February"),
          this.$t("Main.March"),
          this.$t("Main.April"),
          this.$t("Main.May"),
          this.$t("Main.June"),
          this.$t("Main.July"),
          this.$t("Main.August"),
          this.$t("Main.September"),
          this.$t("Main.October"),
          this.$t("Main.November"),
          this.$t("Main.December")
        ],
        crumbs: [
          {name: this.$t("Main.MyLessonList.Menu"), route: "/CalendarSchedule"} // 我的课表
        ],
        organid: 0,
        teacherid: 0,
        todoList: [], //今日课表

      };
    },
    beforeCreate() {
      if (!this.$Utils.isLogin()) {
        this.$router.replace('/Login');
        return;
      }
    },
    created() {
      if (this.$Utils.isLogin()) {
        this.organid = this.$Utils.getUserInfo().organid;
        this.teacherid = this.$Utils.getUserInfo().uid;
      } else {
        this.$router.replace('/Login');
        return;
      }
      this.getToDayData();
      this.getTodayTodoList();
    },
    methods: {
      /**
       * 获取当前时间，比较当前与目标时间
       * @param d2 时间字符串 yy-mm-dd
       * @param status 0:1 0为是否小于当前条件 1为大于
       */
      getNowTime(d2, status) {
        let date = new Date(),
          year = date.getFullYear(),
          month = date.getMonth() + 1,
          day = date.getDate(),
          hour = date.getHours(),
          minute = date.getMinutes();
        let dateStr = `${year}-${month}-${day} ${hour}:${minute}`;
        let YearMDStr = `${year}-${month}-${day}`;
        let YearMStr = `${year}-0${month}`;
        let YearMSD2Str = `${new Date(d2).getFullYear()}-${new Date(d2).getMonth() + 1}`;

        /**
         * 如果存在参数，返回当前时间是否大于目标时间
         */
        if (d2) {
          if (status == 1) {
            return (
              new Date(YearMDStr.replace(/-/g, "/")) > new Date(d2.replace(/-/g, "/"))
            );
          } else if (status == 0) {
            if (this.$Utils.isBroswer().name == 'mozilla') {
              if (new Date(YearMStr).getTime() < new Date(YearMSD2Str).getTime()) {
                return false;
              }
            }
            //判断是否是当月，如果大于本月，返回false
            if (new Date(YearMStr.replace(/-/g, "/")) < new Date(YearMSD2Str.replace(/-/g, "/"))) {
              return false;
            }
            return (
              new Date(YearMDStr.replace(/-/g, "/")) < new Date(d2.replace(/-/g, "/"))
            );
          } else if (status == 2) {
            if (this.$Utils.isBroswer().name == 'mozilla') {
              return new Date(YearMStr).getTime() < new Date(d2).getTime();
            }
            return (
              new Date(YearMStr.replace(/-/g, "/")) < new Date(d2.replace(/-/g, "/"))
            );
          }
        } else {
          return dateStr;
        }
      },
      /**
       * 获取当日课表
       * @param y 年
       * @param m 月
       * @param d 日
       * @param type
       * @param data
       */
      getDataList(y, m, d, type, data) {
        if (data.num === 0) {
          return;
        }
        this.$router.push({
          path: "/LessonListEdit",
          query: {type: type, date: `${y}-${m}-${d}`, route: this.$route.fullPath,}
        });
      },
      getNextData() {
        let {m} = this;
        this.m += 1;
        if (this.m > 12) {
          this.y += 1;
          this.m = 1;
        }
        this.organCourseList(this.y, this.m, this.day);
      },
      getPrevData() {
        let {m} = this;
        this.m -= 1;
        if (this.m < 1) {
          this.m = 12;
          this.y -= 1;
        }
        this.organCourseList(this.y, this.m, this.day);
      },
      getToDayData() {
        var date = new Date();
        this.y = date.getFullYear();
        this.m = date.getMonth() + 1;
        this.day = date.getDate();
        this.organCourseList(this.y, this.m, this.day);
      },
      organCourseList(year, month, day) {
        var y = year;
        var m = month;
        var day = day;
        Server.post(
          Path.teachCourseList,
          {
            teacherid: this.teacherid,
            organid: this.organid,
            date: `${year}-${month}-1`
          },
          res => {
            let {code, data} = res;
            if (code === 0) {
              this.data = data.map((el, index) => {
                var d = el.map((e, i) => {
                  if (e.month < m) {
                    e.prevMonth = true;
                  } else {
                    e.prevMonth = false;
                  }
                  if (e.month > m) {
                    e.nextMonth = true;
                  } else {
                    e.nextMonth = false;
                  }
                  if (e.month == m) {
                    e.activeMonth = true;
                  }
                  if (
                    e.day == day &&
                    e.year == new Date().getFullYear() &&
                    e.month == new Date().getMonth() + 1
                  ) {
                    e.active = true;
                  } else {
                    e.active = false;
                  }
                  //判断有课状态下，是待上课还是已结课
                  if (e.day < day && e.num > 0) {
                    e.had = true;
                  } else {
                    e.had = false;
                  }
                  if (e.day > day && e.num > 0) {
                    if (Number(e.month) < new Date().getMonth() + 1) {
                      e.had = true;
                    } else {
                      e.dai = true;
                    }
                  } else {
                    e.dai = false;
                  }
                  return e;
                });

                // let last = d.splice(-1)[0];
                // console.log(last);
                // d.unshift(last)
                return d;
              });
            }
          }
        ).catch(err => {
          throw err;
        });
      },

      getTodayTodoList() {
        let date = new Date(),
          year = date.getFullYear(),
          month = (date.getMonth() + 1) > 10 ? date.getMonth() + 1 : `0${date.getMonth() + 1}`,
          day = date.getDate() > 10 ? date.getDate() : `0${date.getDate()}`;
        let newDate = `${year}-${month}-${day}`;

        Server.post(Path.getLessonsByDate, {
            date: newDate,
            pagenum: 1,
            pagesize: 20,
          }, (res) => {
            let {code, data} = res;
            if (code === 0 && data !== null) {
              this.todoList = data.data;

              this.todoList.forEach(el => {
                this.$set(el, 'todayStartTime', this.$Utils.formatDateStr(new Date(el.starttime), 'HH:mm'));
                this.$set(el, 'todayEndTime', this.$Utils.formatDateStr(new Date(el.endtime), 'HH:mm'));
              })

            }
          }
        ).catch(err => {
          throw err;
        });
      },
    }
  };
</script>
<style scoped lang="less">
  @import "~@/assets/less/variable";

  .CalendarBox {
    max-width: 1250px;
    margin: auto;
    .Calendar-content {
      margin-top: 56px;
      .Cal-table-head {
        position: relative;
        height: 58px;
        line-height: 58px;
        background: @color-theme;
        border-top-left-radius: 20px;
        border-top-right-radius: 20px;
        text-align: center;
        color: @color-white;
        .today-btn {
          padding: 0;
          width: 72px;
          height: 26px;
          position: absolute;
          left: 60px;
          top: 16px;
          border: none;
          font-size: @font-size-normal-s;
          background: @color-highlight-blue;
          color: @color-white;
        }
        .Cal-table-head-btn,
        .Cal-table-head-currentMonth {
          display: inline-block;
          cursor: pointer;
        }
        .Cal-table-head-currentMonth {
          font-size: @font-size-large-s;
        }
        .Cal-table-head-btn {
          vertical-align: middle;
          line-height: 22px;
          width: 22px;
          height: 22px;
          margin: 10px;
          &.Cal-table-head-prev {
            position: absolute;
            left: 260px;
            top: 8px;
            background: url(~@/assets/images/icon/icon_paging_l.png) no-repeat center;
            background-size: 100% 100%;
          }
          &.Cal-table-head-next {
            position: absolute;
            right: 260px;
            top: 8px;
            background: url(~@/assets/images/icon/icon_paging_r.png) no-repeat center;
            background-size: 100% 100%;
          }
        }
      }
      .Col-table-content {
        .Cal-table {
          width: 100%;
          text-align: center;
          border-collapse: collapse;
          border: 1px solid transparent;
          box-sizing: border-box;
          tr:first-child th {
            color: @color-theme;
            background: #ECF6FF;
          }
          tr:nth-child(1) {
            td {
              border-top: 1px solid #ECF6FF;
            }
          }
          tr:first-child th:last-child {
            border-right: none;
          }
          tr td:first-child {
            border-left: none;
          }
          tr td:last-child {
            border-right: none;
          }
          tr:last-child td {
            border-bottom: 1px solid #CCCCCC;
          }
          thead tr {
            height: 50px;
            font-size: @font-size-large-s;
            color: @color-gray;
            background: #FCFCFC;
          }
          tbody tr td {
            position: relative;
            height: 84px;
            color: #637e95;
            vertical-align: middle;
            text-align: center;
            position: relative;
            width: 100px;
            border: 1px solid #CCCCCC;
            .cal-item-box {
              width: 100%;
              height: 100%;
              margin: auto;
              &.had-class-avtive {
                cursor: pointer;
              }
              .cal-item-date {
                line-height: 84px;
                top: 6px;
                text-align: center;
                box-sizing: border-box;
                font-size: 42px;
                font-family: MicrosoftYaHei-Bold;
                color: #B8B8B8;
              }
            }
          }
        }
      }
    }
    .Cal-table-tip {
      position: relative;
      margin-top: 30px;
      padding: 0 30px;
      border: 1px dashed #BBBBBB;
      border-radius: 10px;
      .table-tip-content {
        .table-tip-content-title {
          line-height: 40px;
          font-size: @font-size-normal;
          color: #666666;
        }
        .table-tip-content-coursetime {
          line-height: 40px;
          span {
            font-size: @font-size-small;
            &.time {
              display: table-cell;
              width: 100px;
              color: @color-theme;
            }
            &.course {
              display: table-cell;
            }
          }
        }
      }
    }
  }

  .had-class-avtive .cal-item-date,
  .next-class-avtive .cal-item-date {
    color: #B8B8B8;
  }

  .had-class-avtive .cal-item-text,
  .next-class-avtive .cal-item-text,
  .active .cal-item-text {
    font-size: 14px;
  }

  /** 将来的课程 **/
  .next-class-avtive {
    pointer-events: auto;
    cursor: pointer;
    .cal-item-text {
      width: 65px;
      height: 25px;
      line-height: 24px;
      background: @color-orange;
      font-size: 14px;
      color: #fff;
    }
  }

  .nextmonth-class-active {
    background: #F4F8FC;
    .cal-item-date {
      color: #DFE3E6 !important;
    }
  }

  /** 大于今天的日期颜色 **/
  .today-next-active {
    .cal-item-date {
      color: @color-highlight-blue !important;
    }
  }

  /** 今天 **/
  .active {
    position: absolute;
    z-index: 3;
    top: -2px;
    left: -2px;
    right: -2px;
    bottom: -2px;
    border: 2px solid @color-orange;
    cursor: pointer;
    box-sizing: content-box;
    .cal-item-text {
      width: 100%;
      height: 100%;
      left: 0;
      right: 0;
      bottom: 0;
      line-height: 84px;
      font-size: 32px;
      color: @color-orange;
      background: none;
    }
  }

  /** 存在课程的课程文字说明 **/
  .cal-item-text {
    width: 65px;
    height: 25px;
    line-height: 25px;
    position: absolute;
    display: block;
    top: 0;
    right: 0;
    font-size: 14px;
    background: #CCCCCC;
    color: #666565;
  }

  /** 已过去的课程 **/
  .pre-month-active {
    color: #a0abb4;
    pointer-events: none;
    cursor: pointer;
  }

  /** 今天以前 **/
  .today-prev-active {
    background: #F4F8FC;
  }

  /** 今天以前且上个月的 **/
  .pre-month-active.today-prev-active {
    .cal-item-date {
      color: #DFE3E6!important;
    }
  }
</style>



