<template>
  <div id="coursedetail-courseware">
    <!--课件作业-->
    <Card class="noradius-card mytheme-noborder mytheme-content-minheight mytheme-card-padding" dis-hover>
      <!-----------------------------------------课件列表---------------------------------------------->
      <div class="courseware-content">
        <div class="content-label">课件列表：</div>
        <div class="courseinfo-filelist ">
          <ul v-if="ware.data">
            <li v-for="(el, i) in ware.data" :key="i">
              <span class="filelist-item-tt">{{el.showname}}</span>
              <span class="filelist-item-delicon" v-if="!getNowTime(endtime)" @click="getDelId(el)">删除</span>
            </li>
          </ul>
          <div class="content-val" v-else>暂无课件</div>
          <!-----------------------------添加课件---------------------------->
          <Button
            class="addware-btn mytheme-mg-bottom-20 mytheme-mg-top-30"
            type="primary"
            @click="isShowFolderModal = true;"
            v-if="!getNowTime(endtime)">添加
          </Button>
        </div>
      </div>

      <!----------------------------------------作业列表---------------------------------------------->
      <div class="courseware-content">
        <div class="content-label">作业列表：</div>
        <!-- 文件列表 -->
        <div class="courseinfo-filelist">
          <ul class="" v-if="taskList && taskList.length > 0">
            <li v-for="(el, i) in taskList" :key="i">
              <span class="filelist-item-tt">{{el.periodname}}</span>
            </li>
          </ul>
          <div class="content-val" v-else>暂无作业</div>
          <!-- 添加作业 -->
          <router-link
            :to="{
            path: '/TaskAdd?name=作业列表&route=%2FTaskManage',
            query: {
              schedulingid: $route.query.schedulingid,
              lessonid: $route.query.lessonsid,
            },
          }" v-if="taskList && taskList.length == 0">
            <Button class="addware-btn mytheme-mg-top-30" type="primary">添加</Button>
          </router-link>
        </div>
      </div>
      <!------------------------------添加课件Modal------------------------------>
      <Modal class="folder-list-modal mytheme-modal" v-model="isShowFolderModal" width="840" height="500">
        <div class="blue folder-list-modal-header modal-head" slot="header">
          <div class="inlineblock" @click="isShowFolderWrap = true; folderFatherId = 0;" v-if="!isShowFolderWrap">
            <img :src="PrevIcon">
          </div>
          <div class="text-center">{{$t('Main.MyLessonList.AddCourseWare')}}</div>
          <!-- 添加课件 -->
        </div>
        <div class="type-content" ref="MaterScroll">
          <!-- 文件夹列表 -->
          <ButtonGroup class="mytheme-mg-bottom-20" v-if="isShowFolderWrap">
            <Button
              class="folder-list-btn" type="default"
              :class="{'ivu-btn-primary':topBarIndex===0}"
              @click="topBarIndex=0; getLessonPptFolder(1, topBarIndex)">公共资源
            </Button>
            <Button
              class="folder-list-btn" type="default"
              :class="{'ivu-btn-primary':topBarIndex===1}"
              @click="topBarIndex=1; getLessonPptFolder(1, topBarIndex)">个人资源
            </Button>
          </ButtonGroup>
          <div class="folder-wrap" v-if="isShowFolderWrap">
            <ul class="folder-list">
              <li
                class="list-item"
                v-for="(el, i) in folderList"
                :key="i"
                @click="folderFatherId = el.fileid; getLessonFileList(1, topBarIndex);"
                v-if="el.juniorcount > 0"
              >
                <div class="folder-img">
                  <img :src="FolderIcon">
                  <div class="folder-num color-orange">{{el.juniorcount}}{{$t('Main.MyLessonList.Count')}}</div>
                </div>
                <div class="folder-name blue" title="">{{el.showname}}</div>
              </li>
            </ul>
            <!-- 暂无数据 -->
            <div class="defaultshow" v-if="folderList.length === 0">{{$t('Main.noData')}}</div>
          </div>
          <!-- 文件列表 -->
          <div class="filelist-wrap" v-else>
            <div class="loadmore-mod">
              <ul class="file-list" v-if="fileList.length > 0">
                <!--usetype==2 普通课件-->
                <li
                  class="list-item"
                  v-if="el.usetype==2"
                  v-for="(el, i) in fileList"
                  :key="i"
                  @click="el.active = !el.active; reversCheck()">
                  <Checkbox v-model="el.active" @on-change="checkboxChange"></Checkbox>
                  {{el.showname}}
                </li>
              </ul>
            </div>
            <!-- 暂无数据 -->
            <div class="defaultshow" v-if="fileList.length === 0">{{$t('Main.noData')}}</div>
          </div>

        </div>
        <div class="modal-foot" slot="footer">
          <!-- 确认 -->
          <Button class="modal-foot-btn" v-if="!isShowFolderWrap" type="primary" @click="addLessonPpt()">
            {{$t('Main.SaveBtn')}}
          </Button>
          <!--取消-->
          <Button
            class="modal-foot-btn mytheme-btn"
            v-if="!isShowFolderWrap"
            type="default"
            @click="isShowFolderModal = false; isShowFolderWrap = true;">{{$t('Main.CancelBtn')}}
          </Button>
        </div>
      </Modal>

      <!---------------------------------------确认删除Modal---------------------------------------------->
      <Modal class="mytheme-modal" v-model="isShowDelFileModal">
        <div class="modal-head" slot="header">温馨提示</div>
        <div class="modal-body">确认删除此课件吗？</div>
        <div class="modal-foot" slot="footer">
          <Button class="modal-foot-btn" type="primary" @click="deleteLessonFile()">确认</Button>
          <Button class="modal-foot-btn mytheme-btn" type="default" @click="isShowDelFileModal = false;">取消</Button>
        </div>
      </Modal>
    </Card>
  </div>
</template>
<script>
  import addIcon from "@/assets/images/icon_add@2x.png";
  import FolderIcon from "@/assets/images/MaterIcon.png";
  import PrevIcon from "@/assets/images/icon_prev@2x.png";
  import delIcon from "@/assets/images/icon_del@2x.png";
  import {Modal, Button, Checkbox, Icon, Card, Form, FormItem, ButtonGroup} from "iview";
  import Server from "@/service/request";
  import requestPath from "@/service/Path";
  import defaultShow from "@/components/defaultShow";

  export default {
    name: "coursedetail-courseware",
    props: ["ware"],
    components: {Modal, Button, Checkbox, Icon, defaultShow, Card, Form, FormItem, ButtonGroup},
    computed: {
      getParentData() {
        return this.ware;
      }
    },
    data() {
      return {
        addIcon,
        FolderIcon,
        PrevIcon,
        delIcon,
        endtime: this.$route.query.endtime,
        isShowEditBtn: true,
        isShowFolderModal: false,
        isShowFolderWrap: true,
        isShowDelFileModal: false,
        folderList: [],
        fileList: [],
        taskList: [],
        folderFatherId: 0,
        folderPageDisplay: 20,
        folderPageNum: 1,
        folderTotal: 0,

        wareList: [],
        fileArr: [],
        delFileId: "",
        fileType: 1,
        topBarIndex: 0,
        idsArr: [],//添加课件的id数组
      };
    },
    created() {
      this.getLessonPptFolder(1, this.topBarIndex);
      this.showLesssonsHomeList();
    },
    watch: {
      getCurrentWare(ware) {
        this.wareList = ware;
      },
      isShowFolderModal(val) {
        if (!val) {
          this.folderFatherId = 0;       //清空fatherid
          this.isShowFolderWrap = true;  //添加课件再次打开时显示文件夹
        }
      },
    },
    methods: {
      /**
       * 向父组件派发一个自定义事件
       * @param msg 自定义事件名
       */
      emitEvent(msg) {
        this.$emit(msg);
      },

      getNowTime(d2) {
        let date = new Date(),
          year = date.getFullYear(),
          month = date.getMonth() + 1,
          day = date.getDate(),
          hour = date.getHours(),
          minute = date.getMinutes();
        let dateStr = `${year}-${month}-${day} ${hour}:${minute}`;
        if (d2) {
          return (
            new Date(dateStr.replace(/-/g, "/")) >= new Date(d2.replace(/-/g, "/"))
          );
        }
        // if(d2){
        //   return(
        //     new Date().getTime()>new Date(d2.replace(/-/g, "/")).getTime()-1800000
        //   )
        // }
      },
      /**
       * 课件文件夹
       * @param folderPageNum 文件夹分页页码
       */
      getLessonPptFolder(folderPageNum = 1, filetype) {
        let {folderFatherId} = this;
        Server.post(
          requestPath.tch_getFileList,
          {
            showname: "",
            pagenum: folderPageNum,
            fatherid: folderFatherId, //文件夹id
            filetype: filetype,              //资源类型
            usetype: 2,
          },
          res => {
            let {code, data} = res;
            this.folderList = [];
            if (code === 0 && data !== null) {
              if (folderPageNum == 1) {
                this.folderList = [...data.data];
              } else {
                this.folderList = [...this.folderList, ...data.data];
              }
              if (data.data.length < data.pageinfo.pagesize) {
                this.flag = false;
              }
            } else {
              this.flag = false;
            }
          }
        ).catch(err => {
          throw err;
        });
      },
      /**
       * 查询资源列表
       * @param folderPageNum 文件夹页码
       * @param filetype 1：公共资源  2：个人资源
       */
      getLessonFileList(folderPageNum = 1, filetype) {
        let {folderFatherId} = this;
        Server.post(
          requestPath.tch_getFileList,
          {
            showname: "",
            pagenum: folderPageNum,
            fatherid: Number(folderFatherId),
            filetype: Number(filetype),
            usetype: 2,//0：所有类型 1：录播课 2：直播课
          },
          res => {
            let {code, data} = res;
            if (code === 0) {
              this.isShowFolderWrap = false;
              this.folderPageDisplay = data.pageinfo.pagesize;
              this.folderPageNum = data.pageinfo.pagenum;
              this.folderTotal = data.pageinfo.total;
              this.fileList = data.data;

              this.fileList.forEach((el, i) => {
                this.$set(el, "active");
                //设置默认选中装填
                this.getParentData.data.forEach(item => {
                  console.log(item);
                  if (el.fileid == item.fileid) {
                    this.$set(el, "active", true);
                  }
                });
              });
            }
          }
        ).catch(err => {
          throw err;
        });
      },
      //选择要上传的课件
      checkboxChange() {
        let arr = [],
          temp = [];
        let lessonWare = this.getParentData.data;
        let lessonWareIds = [];
        if (lessonWare) {
          lessonWareIds = lessonWare.map(el => {
            return el.fileid;
          });
        }
        this.fileList.forEach((el, i) => {
          if (el.active) {
            arr.push(el.fileid);
            this.fileArr.push(el);
          }
        });
        arr.forEach(el => {
          /**
           * 判断是否选中且没有选择过的，添加到列表中
           * 2018 08 01 20:51
           * qyh
           */
          if (lessonWareIds.indexOf(el) == -1) {
            temp.push(el);
          }
        });
        this.idsArr = temp;
      },
      // 添加课件
      addLessonPpt() {
        if (this.idsArr.length === 0) {//如果没有新添加的课件，不进行请求
          this.isShowFolderModal = false;
          return;
        }
        Server.post(
          requestPath.addWarefile,
          {
            id: this.$route.query.lessonsid, //lessons主键
            fileid: this.idsArr //Filemanage表的主键
          },
          res => {
            let {code, data} = res;
            if (code === 0 && data !== null) {
              this.$emit("addWareSuccess");
              this.isShowFolderModal = false;//添加完课件隐藏课件弹窗
              this.isShowFolderWrap = true;  //再次打开时显示文件夹
            } else {
              this.$Message.info(res.info);
            }
          }
        ).catch(err => {
          throw err;
        });
      },
      //获取要删除的id
      getDelId(el) {
        this.delFileId = el.fileid;
        this.isShowDelFileModal = true;
      },
      //删除课件
      deleteLessonFile() {
        Server.post(
          requestPath.delWarefile,
          {
            id: this.$route.query.lessonsid,
            fileid: [this.delFileId]
          },
          res => {
            if (res.code === 0) {
              this.isShowDelFileModal = false;
              this.$emit("addWareSuccess");
            }
          }
        ).catch(err => {
          throw err;
        });
      },
      /**
       * 反选复选框
       * @param i 选中下标
       */
      reversCheck() {
        this.$forceUpdate();
        this.checkboxChange();
      },
      /**
       * 获取课时详情作业列表
       */
      showLesssonsHomeList() {
        let {curriculumid, schedulingid, periodname} = this.$route.query
        Server.post(requestPath.showLesssonsHomeList, {
          'pagenum': 1,
          curriculumid,
          schedulingid,
          periodname: periodname,
        }, (res) => {
          let {code, data} = res;
          if (code === 0) {
            this.taskList = data.data;
          }
        });
      },
    }
  };
</script>
<style lang="less" scoped>
  @import "../assets/courseinfo-title.less";
  @import "~@/assets/less/variable";

  .folder-list-btn {
    background: transparent;
    &.ivu-btn-primary {
      color: #fff;
      background-color: #3F9CE8;
      border-color: #3F9CE8;
    }
  }

  .defaultshow {
    text-align: center;
    font-size: 16px;
    line-height: 200px;
  }

  .courseware-content {
    margin: 48px 0 0 0;
    .content-label {
      width: 100px;
      display: table-cell;
      font-size: @font-size-normal-s;
      vertical-align: top;
    }
    .content-val {
      font-size: 16px;
      color: rgba(164, 171, 179, 1);
    }
    .courseinfo-filelist {
      display: table-cell;
      max-width: 998px;
      width: 998px;
      li {
        position: relative;
        margin-bottom: 28px;
        padding: 0 14px;
        height: 38px;
        line-height: 38px;
        background: #FAFAFA;
        font-size: 12px;
        border: 1px solid #E6E6E6;
        -ms-text-overflow: ellipsis;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
        .filelist-item-delicon {
          position: absolute;
          right: 16px;
          top: 0;
          bottom: 0;
          margin: auto;
          color: @color-theme;
          cursor: pointer;
        }
        .filelist-item-tt {
          display: inline-block;
          width: 96%;
          margin-right: 70px;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
        }
      }
    }
  }

  .addppt-btn {
    display: block;
    img {
      vertical-align: middle;
      margin-right: 4px;
    }
    span {
      vertical-align: middle;
      font-size: 14px;
    }
  }

  .folder-list-modal {
    position: relative;
    .folder-list-modal-header {
      .inlineblock {
        position: absolute;
        top: 5px;
        left: 8px;
        padding: 10px;
        cursor: pointer;
      }
      img {
        vertical-align: middle;
        margin-right: 6px;
        height: 16px;
        cursor: pointer;
        float: left;
      }
      span {
        vertical-align: middle;
      }
    }
    .folder-wrap {
      max-height: 480px;
      min-height: 200px;
      overflow-y: scroll;
      &::-webkit-scrollbar {
        width: 6px;
        // height: 16px;
        background-color: transparent;
      }
      /*定义滚动条的轨道，内阴影及圆角*/
      &::-webkit-scrollbar-track {
        // -webkit-box-shadow: inset 0 0 6px #D3DFE9;
        border-radius: 10px;
        background-color: transparent;
      }
      /*定义滑块，内阴影及圆角*/
      &::-webkit-scrollbar-thumb {
        height: 20px;
        border-radius: 10px;
        -webkit-box-shadow: inset 0 0 6px #d3dfe9;
        background-color: #d3dfe9;
      }
      .list-item {
        display: inline-block;
        position: relative;
        width: 198px;
        height: 194px;
        cursor: pointer;
        &:hover {
          background: #ECF1FF;
        }
        .folder-img {
          width: 100%;
          height: 120px;
          text-align: center;
          font-size: 16px;
          img {
            margin: 32px auto 0 auto;
            width: 66px;
            height: 66px;
          }
          div {
            margin: 12px auto 0 auto;
            font-size: 12px;
            color: #808080;
          }
        }
        .folder-name {
          padding: 0 10px 0 10px;
          height: 48px;
          line-height: 48px;
          text-align: center;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          font-size: 14px;
          color: #666666;
        }
      }
    }
    .filelist-wrap {
      .file-list {
        padding: 0 20px;
        max-height: 480px;
        min-height: 200px;
        overflow-y: scroll;
        &::-webkit-scrollbar {
          width: 6px;
          // height: 16px;
          background-color: transparent;
        }
        /*定义滚动条的轨道，内阴影及圆角*/
        &::-webkit-scrollbar-track {
          // -webkit-box-shadow: inset 0 0 6px #D3DFE9;
          border-radius: 10px;
          background-color: transparent;
        }
        /*定义滑块，内阴影及圆角*/
        &::-webkit-scrollbar-thumb {
          height: 20px;
          border-radius: 10px;
          -webkit-box-shadow: inset 0 0 6px #d3dfe9;
          background-color: #d3dfe9;
        }
        .list-item {
          height: 40px;
          line-height: 40px;
          font-size: 14px;
          border-bottom: 1px solid #e8edf0;
          cursor: pointer;
          user-select: none;
        }
      }
    }
  }
</style>



