import Vue from 'vue';
import Router from 'vue-router';

//我的课表
import Main from '@/container/CalendarSchedule';
//我的课表-待上课
import LessonListEdit from '@/container/CalendarSchedule/LessonListEdit';
//我的课表-课时详情
import LessonDetailsEdit from '@/container/CalendarSchedule/LessonDetailsEdit';

//我的班级
import MyClasses from '@/container/MyClasses';
//我的班级-班级详情
import ClassDetail from '@/container/MyClasses/ClassDetail';

//素材文件夹列表
import MaterialList from '@/container/MaterialManagement';
//素材文件列表
import MaterialInfo from '@/container/MaterialManagement/MaterListInfoEdit/MaterInfo';

//作业管理
import TaskManage from '@/container/TaskManage';
//作业管理-作业详情列表
import TaskDetail from '@/container/TaskManage/TaskDetail';
//作业管理-布置作业
import TaskAdd from '@/container/TaskManage/TaskAdd';
//作业管理-学员作业明细
import TaskDetailType from '@/container/TaskManage/TaskDetailType';
//作业管理-已批阅
import TaskFinished from '@/container/TaskManage/Finished';
//作业管理-学生作业详情（批阅作业）
import Examines from '@/container/TaskManage/Examines';

//精彩瞬间
import WonderfulMoment from '@/container/WonderfulMoment';
//精彩瞬间-详情
import MomentDetail from '@/container/WonderfulMoment/MomentDetail';

//我的学生
import StudentDetail from '@/container/Mystudents/StudentDetail'
//我的学生-学生详情
import Mystudents from '@/container/Mystudents'

//我的订单
import MyOrders from '@/container/OrdersManagement/OrderList';
//订单详情
import OrderDetails from '@/container/OrdersManagement/OrderDetails';

//我的消息
import MyMessage from '@/container/MyMessage';

//老师资料设置
import TeacherEdit from '@/container/TeacherSetting/TeacherEdit';

//作文批改
import EssayMarking from '@/container/EssayMarking';
//作文批改-查看详情
import essayDetail from '@/container/EssayMarking/essayDetail';

//登录
import Login from '@/container/Login';

//注册
import Register from '@/container/Login/Register';

//layout
import Layout from '@/layout';

//新手引导
import Guidance from '@/container/Guidance';

Vue.use(Router);



const router = new Router({
  routes: [
    {
      path: '/',
      component: Layout,
      redirect: '/Login',
      children: [
        //我的班级
        {
          path: '/MyClasses',
          component: MyClasses,
          name: 'MyClasses'
        },
        //我的班级课程详情
        {
          path: '/ClassDetail',
          component: ClassDetail,
          name: 'ClassDetail'
        },
        //作业管理
        {
          path: '/TaskManage',
          component: TaskManage,
          name: 'TaskManage'
        },
        //作业详情列表
        {
          path: '/TaskDetail',
          component: TaskDetail,
          name: 'TaskDetail'
        },
        //批阅列表
        {
          path: '/TaskDetailType',
          component: TaskDetailType,
          name: 'TaskDetailType'
        },
        //布置作业
        {
          path: '/TaskAdd',
          component: TaskAdd,
          name: 'TaskAdd',
        },
        //批阅作业学生作业详情
        {
          path: '/Examines',
          component: Examines,
          name: 'Examines',
        },
        //已批阅
        {
          path: '/TaskFinished',
          component: TaskFinished,
          name: 'TaskFinished'
        },
        //精彩瞬间
        {
          path: '/WonderfulMoment',
          component: WonderfulMoment,
          name: '/WonderfulMoment'
        },
        //精彩瞬间详情
        {
          path: '/MomentDetail',
          component: MomentDetail,
          name: '/MomentDetail'
        },
        //我的消息
        {
          path: '/MyMessage',
          component: MyMessage
        },
        //我的课表
        {
          path: '/CalendarSchedule',
          component: Main,
          name: 'CalendarSchedule',
        },
        //我的课表（重定向）
        // {
        //   path: '/',
        //   redirect: '/CalendarSchedule'
        // },
        //素材管理文件夹列表
        {
          path: '/MaterialList',
          component: MaterialList
        },
        //素材管理文件列表
        {
          path: '/MaterialInfo/:id',
          component: MaterialInfo,
          name: 'MaterialInfo',
        },
        //我的课表-待上课
        {
          path: '/LessonListEdit',
          component: LessonListEdit,
          name: 'LessonListEdit',
        },
        //课时详情
        {
          path: '/LessonDetail',
          component: LessonDetailsEdit,
          name: 'LessonDetail'
        },
        //我的学生
        {
          path: '/Mystudents',
          component: Mystudents,
          name: 'Mystudents'
        },
        //学生详情
        {
          path: '/StudentDetail',
          component: StudentDetail,
          name: 'StudentDetail'
        },
        //我的设置
        {
          path: '/TeacherEdit',
          component: TeacherEdit,
          name: 'TeacherEdit'
        },
        //我的订单
        {
          path: '/MyOrders',
          component: MyOrders,
          name: 'MyOrders'
        },
        //订单详情
        {
          path: '/OrderDetails',
          component: OrderDetails,
          name: 'OrderDetails'
        },
        //作文批改
        {
          path: '/EssayMarking',
          component: EssayMarking,
          name: 'EssayMarking'
        },
        //作文批改-查看详情
        {
          path: '/essayDetail',
          component: essayDetail,
          name: 'essayDetail'
        }
      ],
    },
    //登录
    {
      path: '/Login',
      component: Login,
      name: 'Login',
    },
    //注册
    {
      path: '/Register',
      component: Register,
      name: 'Register',
    },
    //新手引导
    {
      path: '/Guidance',
      component: Guidance,
      name: 'Guidance'
    },
  ]
})

// 判断权限
// router.beforeEach((to, from, next) => {
//   next();
// })

export default router;



