<template>
  <div id="order-list">
    <panel-title :crumbs="crumbs"></panel-title>
    <div class="mytheme-container-mg-30">
      <Card class="mytheme-noborder noradius-card mytheme-card-padding" dis-hover>
        <div class="mytheme-mg-top-20" ref="datepicker">
          <span class="mytheme-input-label">{{$t('Main.MyOrders.Time')}}{{$t('Main.Symbols.Colon')}}</span>
          <!--选择时间-->
          <Date-picker
            class="input-max-height input-normal-width"
            type="daterange"
            :options="options1"
            placement="bottom-start"
            :placeholder="$t('Main.SelectTime')"
            v-model="searchFrom.time"
            format="yyyy-MM-dd HH:mm:ss"
            @on-change="getTime(searchFrom.time); changeDate()"
            :editable="false">
          </Date-picker>
          <div class="inlineblock">
            <Button
              class="mytheme-mg-left-70"
              @click="activeDateIndex = 0;datestatus = 3;
              getOrderList(orderStatusActive, current); searchFrom.time = ''; timeap = []"
            >
              {{$t('Main.MyOrders.ResetBtn')}}
            </Button>
            <Button
              class="mytheme-mg-left-70"
              type="primary"
              @click="getOrderList(orderStatusActive, current)"
            >
              {{$t('Main.MyOrders.SearchBtn')}}
            </Button>
          </div>
        </div>
        <Table
          class="mytheme-table noborder-table thead-max-height bottom-radius mytheme-mg-top-30"
          :stripe="true"
          :columns="colData"
          :data="tabData"
          :no-data-text="$t('Main.MyOrders.noData')"
        >
        </Table>
        <pagination
          class="mytheme-mg-top-20"
          v-if="total > 0"
          :total="total"
          :current-page='current'
          :display="display"
          @pagechange="pagechange">
        </pagination>
      </Card>
    </div>
  </div>
</template>

<script>
  import {
    Table,
    Button,
    ButtonGroup,
    Card,
    Input,
    Icon,
    DatePicker,
    Row,
    Col,
    Form,
    FormItem
  } from 'iview'
  import panelTitle from '@/components/panelTitle'
  import pagination from '@/components/pagination'
  import Server from '@/service/request'
  import requestPath from '@/service/Path'

  export default {
    name: "order-list",
    components: {
      Table,
      Button,
      ButtonGroup,
      Card,
      panelTitle,
      Input,
      pagination,
      Icon,
      Server,
      requestPath,
      DatePicker,
      Row,
      Col,
      Form,
      FormItem
    },
    data() {
      return {
        crumbs: [{
          name: this.$t('Main.MyOrders.AllOrders'),
          route: '/MyOrders'
        }],
        btnList: [{
          name: this.$t('Main.MyOrders.AllOrders'), //全部订单
          num: 0,
          orderstatus: 21,
          datestatus: 3,
        },],
        colData: [
          //订单编号
          {
            title: this.$t('Main.MyOrders.OrderNum'),
            key: 'ordernum',
            align: 'center'
          },
          //课程名称
          {
            title: this.$t('Main.MyOrders.CourseName'),
            key: 'coursename',
            align: 'center'
          },
          //下单时间
          {
            title: this.$t('Main.MyOrders.OrderTime'),
            key: 'ordertime',
            align: 'center'
          },
          //学生
          {
            title: this.$t('Main.MyOrders.Student'),
            key: 'studentname',
            align: 'center'
          },
          //订单金额
          {
            title: this.$t('Main.MyOrders.Amount'),
            render: (h, params) => {
              return h('div', `${this.$t('Main.Symbols.CurrencySymbol')}${params.row.amount}`)
            },
            align: 'center'
          },
          //支付方式
          {
            title: this.$t('Main.MyOrders.PayType'),
            render: (h, params) => {
              let that = this;

              /**
               * 过滤支付方式
               * @param       {[String]} param 0：其他  1：余额  2：微信  3：支付宝  4：银联
               * @return      {[String]}       render Text
               */
              function _paytype(param) {
                if (param == 0) {
                  return that.$t('Main.MyStudent.Other');
                } else if (param == 1) {
                  return that.$t('Main.MyStudent.Balance');
                } else if (param == 2) {
                  return that.$t('Main.MyStudent.Wx');
                } else if (param == 3) {
                  return that.$t('Main.MyStudent.Alipay');
                } else if (param == 4) {
                  return that.$t('Main.MyStudent.Unionpay');
                }
              }

              return h('div', _paytype(params.row.paytype));
            },
            align: 'center'
          },
          //订单状态
          {
            title: this.$t('Main.MyOrders.OrderStatus'),
            key: 'orderstatusname',
            align: 'center'
          },
          //操作
          {
            title: this.$t('Main.MyOrders.Option'),
            render: (h, params) => {
              return h('a', {
                style: {
                  color: '#F89240'
                },
                on: {
                  'click': () => {
                    this.showDetails(params.row.id);
                  }
                }
              }, this.$t('Main.MyOrders.Review'))
            },
            align: 'center'
          }
        ],
        tabData: [],
        current: 1,
        total: 0,
        display: 20,
        datestatus: 3,
        timeap: [],
        datepickerbtn: [{
          name: this.$t('Main.MyOrders.Today'), //今天
          datestatus: 1
        },
          {
            name: this.$t('Main.MyOrders.ThisWeek'), //本周
            datestatus: 2
          },
          {
            name: this.$t('Main.MyOrders.ThisMonth'), // 本月
            datestatus: 3
          }
        ],
        dateStatusActive: 1, // getOrderList
        activeDateIndex: 0, //本月选中
        orderStatusActive: 21, // getOrderList
        btnActiveIndex: 0, //订单状态选中
        datepickerfirst: '',
        datepickerlast: '',
        teachername: '',
        options3: {
          disabledDate: date => {
            return date.setDate(date.getDate()) && date.setDate(date.getDate()).valueOf() < this.datepickerlast;
          }
        },
        options1: {
          shortcuts: [{
            text: this.$t('Main.AWeek'),
            value() {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              return [start, end];
            },
            onClick: () => {
              this.alerta(1);
            }
          },
            {
              text: this.$t('Main.AMonth'),
              value() {
                const end = new Date();
                const start = new Date();
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                return [start, end];
              },
              onClick: () => {
                this.alerta(2);
              }
            },
            {
              text: this.$t('Main.ThreeMonth'),
              value() {
                const end = new Date();
                const start = new Date();
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                return [start, end];
              },
              onClick: () => {
                this.alerta(3);
              }
            }
          ]
        },
        searchFrom: {
          fromdate: '',
          enddate: '',
          time: ''
        }
      }
    },
    created() {
      if (this.$Utils.isLogin()) {
        this.getOrderList(this.btnList[0].orderstatus, this.current);
      } else {
        this.$router.replace('/Login');
        return;
      }
    },
    methods: {
      changeDate() {
        this.timeap[0] =
          `${this.searchFrom.time[0].getFullYear()}-${this.searchFrom.time[0].getMonth() + 1}-${this.searchFrom.time[0].getDate()} ${this.searchFrom.time[0].getHours()}:${this.searchFrom.time[0].getMinutes()}:${this.searchFrom.time[0].getSeconds()}`;
        this.timeap[1] =
          `${this.searchFrom.time[1].getFullYear()}-${this.searchFrom.time[1].getMonth() + 1}-${this.searchFrom.time[1].getDate()} ${this.searchFrom.time[1].getHours()}:${this.searchFrom.time[1].getMinutes()}:${this.searchFrom.time[1].getSeconds()}`;
        if (this.timeap[0] == this.timeap[1]) {
          this.timeap[1] = `${this.searchFrom.time[1].getFullYear()}-${this.searchFrom.time[1].getMonth() + 1}-${this.searchFrom.time[1].getDate()} 23:59:59`;
        }
      },
      alerta(al) {
        this.datestatus = al;
      },
      pagechange(num) {
        this.getOrderList(this.btnList[0].orderstatus, num);
      },
      //获取时间
      getTime(val) {
        if (val[0] == '' && val[1] == '') {
          this.searchFrom.fromdate = '';
          this.searchFrom.enddate = '';
        } else {
          let d1 = new Date(val[0]);
          let d2 = new Date(val[1]);
          let datetime = d1.getFullYear() + '-' + (d1.getMonth() + 1) + '-' + d1.getDate();
          let datetime2 = d2.getFullYear() + '-' + (d2.getMonth() + 1) + '-' + d2.getDate();
          this.searchFrom.fromdate = datetime;
          this.searchFrom.enddate = datetime2;
        }
      },
      // 查询订单列表
      getOrderList(orderstatus, pagenum) {
        Server.post(requestPath.order_getOrderList, {
          'pagenum': pagenum,
          'orderstatus': orderstatus,
          'timeap': this.timeap
        }, (res) => {
          let data = res.data.data,
            key;
          this.tabData = res.data.data.orderlist;
          this.total = res.data.pageinfo.total;
          this.display = res.data.pageinfo.pagesize;
          this.current = Number(res.data.pageinfo.pagenum);
          this.btnList[0].num = data.statusnum[21].num;
          this.btnList[1].num = data.statusnum[20].num;
          this.btnList[2].num = data.statusnum[0].num;
          this.btnList[3].num = data.statusnum[10].num;
        }).catch(err => {
          throw err;
        });
      },
      // 查看详情
      showDetails(id) {
        this.$router.push({
          path: '/OrderDetails?from=MainOrderManagement',
          query: {
            orderid: id,
            route: this.$route.fullPath,
          }
        });
      },
      // 切换datepicker
      datepickerChange(el) {
        let oneDayLong = 24 * 60 * 60 * 1000;

        if (!el) { //修改时间
          this.activeDateIndex = '';
          return;
        }
        if (el.datestatus === 1) { //今天
          let date = new Date();
          let date2 = new Date();
          date.setHours(0, 0, 0);
          date2.setHours(23, 59, 59);
          this.datepickerfirst = date;
          this.datepickerlast = date2;
        } else if (el.datestatus === 2) { //本周
          let date = new Date();
          let date2 = new Date();
          date.setHours(0, 0, 0);
          date2.setHours(0, 0, 0);
          let nowTime = date.getTime();
          let nowTime2 = date2.getTime();
          let day = date.getDay();
          let day2 = date.getDay();
          let firstDay = new Date(nowTime - (day - 1) * oneDayLong);
          let lastDay = new Date(nowTime2 + (8 - day2) * oneDayLong);
          this.datepickerfirst = firstDay;
          this.datepickerlast = lastDay;
        } else if (el.datestatus === 3) { //本月
          let date = new Date();
          let date2 = new Date();
          let currentMonth = date2.getMonth();
          let nextMonth = ++currentMonth;
          let nextMonthFirstDay = new Date(date2.getFullYear(), nextMonth, 1);
          date.setHours(0, 0, 0);
          let lastDay = nextMonthFirstDay;
          let firstDay = new Date(date.setDate(1));
          this.datepickerfirst = firstDay;
          this.datepickerlast = lastDay;
        }
      }
    },
  }
</script>

<style scoped lang="less">

  .my-card {
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    .input-label {
      font-size: 18px;
      color: #7A8599;
    }
  }

  .datepicker-btn {
    margin-left: 50px;
    padding: 0 0 12px 0;
  }

  .datepicker-btn a {
    padding: 0 34px 0 0;
    font-size: 14px;
    font-family: PingFangSC-Regular;
    color: rgba(86, 94, 102, 1);
  }

  .datepicker-btn a.activedate {
    color: #31C2A6;
  }
</style>



