<template>
  <div class="nm_panne_ltitle">
    <div class="nm_panne_ltitle_border"></div>
    <ul class="bread-crumbs" v-if="crumbs.length">
      <router-link tag="li" v-if="el.name" v-for="(el, i) in crumbs" :key="i" :to="el.route">{{el.name}}</router-link>
    </ul>
  </div>
</template>
<script>
  export default {
    name: "panelTitle",
    props: {
      crumbs: {
        default: function () {
          return [
            {
              name: '',
              route: ''
            }
          ]
        },
        type: Array
      }
    }
  }
</script>
<style scoped lang="less">
  @import "~@/assets/less/variable";

  .nm_panne_ltitle {
    position: relative;
    height: 50px;
    line-height: 50px;
    background: @color-white;
    border-bottom: 1px solid #dcdcdc;
    font-size: 0;
    overflow: hidden;
    .nm_panne_ltitle_border {
      vertical-align: top;
      margin: 15px 12px 15px 30px;
      display: inline-block;
      width: 4px;
      height: 20px;
      background: @color-theme;
    }
    .bread-crumbs {
      display: inline-block;
      height: 50px;
      li {
        position: relative;
        display: inline-block;
        font-size: @font-size-small;
        color: #A4ABB3;
        cursor: pointer;
        &:last-child {
          color: #525866;
        }
        &:after {
          /*margin: 0 6px;*/
          /*background-image: url('images/icon_right.png');*/
          content: '>';
          display: inline-block;
          color: @color-title;
          /*font-weight: bold;*/
          /*width: 20px;*/
          width: 18px;
          height: 20px;
          line-height: 20px;
          text-align: center;
          /*background-size: 73%;*/
          /*background-repeat: no-repeat;*/
          /*background-position-y: 7px;*/
        }
        &:last-of-type::after {
          background-image: url('');
        }
        &:last-of-type:after {
          content: '';
        }
      }
    }
  }
</style>



