<template>
  <div id="coursedetail-studentlist">
    <Card class="noradius-card mytheme-noborder mytheme-card-padding mytheme-content-minheight" dis-hover>
      <!--已结束 学生出勤表-->
      <Table
        v-if="isHasEnded"
        class="student-table mytheme-table noborder-table tbody-min-height"
        :stripe="true"
        :disabled-hover="true"
        :columns="hasEndedColData"
        :data="readTabData"
      >
      </Table>
      <!--未开始 学生列表-->
      <Table
        v-else
        class="mytheme-mg-top-30 student-table mytheme-table noborder-table tbody-min-height"
        :stripe="true"
        :disabled-hover="true"
        :columns="colData"
        :data="readTabData"
      >
      </Table>
      <pagination
        class="mytheme-mg-top-30"
        :total="total"
        :display="display"
        :currentPage="current"
        @pageChange="pageChange"
      >
      </pagination>
    </Card>
    <!-- 学员出勤 -->
    <Modal class="mytheme-modal" v-model="isShowModal" width="800">
      <div class="modal-head" slot="header">学员出勤</div>
      <div class="modal-body">
        <Table class="mytheme-table noborder-table" :columns="readColData" :data="readTabData"></Table>
        <pagination
          class="clearfloat mytheme-mg-top-30 mytheme-mg-bottom-30"
          v-if="readTabData.length > 0"
          :total="readTabPage.total"
          :display="readTabPage.display"
          :current-page="readTabPage.current"
        ></pagination>
      </div>
      <div class="modal-foot" slot="footer">
        <i-button class="modal-foot-btn " type="primary" @click="review(1)">确定</i-button>
        <i-button class="modal-foot-btn mytheme-btn" type="default" @click="isShowModal = false">取消</i-button>
      </div>
    </Modal>

    <!--写点评-->
    <Modal class="mytheme-modal" v-model="isShowRatingModal" :closable="false">
      <div class="modal-head" slot="header">写点评</div>
      <div class="modal-body">
        <div class="body-content">
          <div class="content-title">课时：{{$route.query.periodname}}</div>
          <div class="content-starttime">上课时间：{{$route.query.starttime}}</div>
          <div class="content-student">
            <div class="inline-block">学生：{{ratingModal.studentName}}</div>
            <div class="inline-block mytheme-mg-left-70">评分：
              <Rate v-model="ratingModal.score"></Rate>
            </div>
          </div>
          <div class="content-rating">
            <div>老师点评：</div>
            <Form ref="formValidate" :model="ratingModal" :rules="ruleValidate">
              <FormItem prop="comment">
                <Input type="textarea" :rows="6" :maxlength="200" v-model.trim="ratingModal.comment"/>
                <div class="color-hui fr font-size-12" v-if="ratingModal.comment && ratingModal.comment.length">
                  {{ratingModal.comment.length}}/200
                </div>
                <div class="color-hui fr font-size-12" v-else>0/200</div>
              </FormItem>
            </Form>
          </div>
        </div>
      </div>
      <div class="modal-foot mytheme-mg-bottom-30" slot="footer">
        <i-button class="modal-foot-btn" type="primary" @click="handleSubmit('formValidate');">提交</i-button>
        <i-button class="modal-foot-btn mytheme-btn" type="default" @click="isShowRatingModal = false">取消</i-button>
      </div>
    </Modal>
  </div>
</template>
<script>
  import pagination from "@/components/pagination";
  import Server from "@/service/request";
  import Path from '@/service/Path';

  import {myDate, ERR_OK} from '../utils';
  import city from '@/Api/city';
  import {Table, Modal, Button, Form, FormItem, Rate, Input, Card,} from "iview";

  export default {
    name: "coursedetail-studentlist",
    props: ["data"],
    components: {
      Table,
      pagination,
      Modal,
      Button,
      Form,
      FormItem,
      Rate,
      Input,
      Card,
    },
    data() {
      return {
        colData: [
          {title: "序号", key: "id", align: 'center'},
          {title: "姓名", key: "nickname", align: 'center'},
          {
            title: "性别", align: 'center',
            render: (h, params) => {
              function renderSex(param) {
                if (param == 1) {
                  return '男';
                } else if (parma == 2) {
                  return '女';
                } else {
                  return '保密';
                }
              }

              return h('div', params.row.sex == 1 ? '男' : '女');
            }
          },
          {title: "手机号", key: "mobile", align: 'center'},
          {title: "年龄", key: "age", align: 'center'},
          {
            title: "地区", align: 'center',
            render: (h, params) => {
              function renderCountry(param) {
                if (param) {
                  return city[param];
                } else {
                  return '----';
                }
              }

              return h('div', [
                h('span', {
                  style: {
                    marginRight: (params.row.province && params.row.city) ? '10px' : '0',
                  }
                }, renderCountry(params.row.country)),
                h('span', {
                  style: {
                    marginRight: '10px',
                    display: params.row.province ? 'ineline-block' : 'none',
                  }
                }, city[params.row.province]),
                h('span', {
                  style: {
                    display: params.row.city ? 'ineline-block' : 'none',
                  },
                }, city[params.row.city]),
              ]);
            }
          },
          {
            title: "个性签名",
            align: 'center',
            render: (h, params) => {
              function renderProfile(param) {
                if (param == '') {
                  return '这个人很懒，什么都没有留下...';
                }
              }

              return h('div', params.row.profile || renderProfile(params.row.profile));
            }
          }
        ],
        hasEndedColData: [
          {
            title: "序号", align: 'center',
            render: (h, params) => {
              return h('div', params.index + 1)
            }
          },
          {title: "姓名", key: "nickname", align: 'center'},
          {
            title: "出勤情况", align: 'center',
            render: (h, params) => {
              return h('div', params.row.attendancestatus == 1 ? '是' : '否');
            }
          },
          {
            title: "点评", align: 'center',
            render: (h, params) => {
              /**
               * 判断是否写过点评，如果写过点评，不可编辑
               * @param param 点评内容
               * @returns {string} 点评内容或者写点评
               */
              function renderBtn(param) {
                return (param == '' || param == null) ? '写点评' : param;
              }

              return h('div', {
                attrs: {
                  class: params.row.comment ? '' : 'color-orange',
                },
                style: {
                  cursor: params.row.comment ? 'normal' : 'pointer'
                },
                on: {
                  'click': () => {
                    if (params.row.comment != '' && params.row.comment != null) {
                      return;
                    }
                    this.isShowRatingModal = true;
                    this.tableIndex = params.index;
                    this.ratingModal.studentName = params.row.nickname;
                    this.ratingModal.score = params.row.score;
                    this.ratingModal.comment = params.row.comment;
                  }
                },
              }, renderBtn(params.row.comment))
            }
          },
          {
            title: '评分',
            key: 'score',
            align: 'center'
          },
        ],
        readColData: [
          {
            title: "序号", align: 'center',
            render: (h, params) => {
              return h('div', params.index + 1)
            },
            width: 80,
          },
          {title: "姓名", key: "nickname", align: 'center'},
          {
            title: "确认出勤", align: 'center',
            render: (h, params) => {
              return h('i-switch', {
                props: {
                  value: params.row.attendancestatus == 1,
                },
                on: {
                  'on-change': (event) => {
                    this.readTabData[params.index].attendancestatus = event ? '1' : '0';
                  }
                }
              });
            },
          },
          // {
          //   title: "评分", align: 'center',
          //   render: (h, params) => {
          //     return h('div', [
          //       h('i-rate', {
          //         props: {
          //           allowHalf: false,
          //           value: Number(params.row.score) || 1,
          //         },
          //         on: {
          //           'on-change': (event) => {
          //             this.readTabData[params.index].score = event;
          //           }
          //         },
          //       }),
          //     ]);
          //   }
          // },
          // {
          //   title: "点评", align: 'center',
          //   render: (h, params) => {
          //     return h('div', [
          //       h('i-input', {
          //         props: {
          //           value: params.row.comment,
          //         },
          //         on: {
          //           'on-blur': (event) => {
          //             this.readTabData[params.index].comment = event.target.value;
          //             this.ratingModal.score = params.row.score;
          //             this.ratingModal.comment = params.row.comment;
          //             this.ratingModal.studentName = params.row.nickname;
          //           }
          //         },
          //       }),
          //     ]);
          //   }
          // },
        ],
        readTabData: [],
        tabData: [],  //已结束学生出勤表
        tempData: [], //临时存放学生列表数据
        display: 10,
        total: 0,
        current: 1,
        readTabPage: {
          display: 10,
          total: 0,
          current: 1,
        },
        isHasEnded: false, //是否显示学生出勤表
        isShowModal: false,
        isShowRatingModal: false,
        isShowReadTab: false,
        studentSum: 0,
        startTime: this.$route.query.starttime,
        endTime: this.$route.query.endtime,
        tableIndex: '',
        ratingModal: {
          studentName: '',
          score: 0,
          comment: '',
        },

        ruleValidate: {
          comment: [
            {required: true, message: '请输入点评', trigger: 'blur'}
          ],
        },
      };
    },
    created() {
      this.getOverStudentList(this.current);
      if (!myDate.getNowTime(this.$route.query.endtime)) {
        this.getStudentList(this.current);
      }
    },
    watch: {
      isShowRatingModal(val) {
        if (!val) {
          this.tableIndex = '';
        }
      }
    },
    methods: {
      /**
       * 学生列表
       * @param num 当前页码
       */
      getStudentList(num) {
        Server.post(Path.periodStulist, {
          'toteachtimeid': this.$route.query.toteachtimeid,
          'pagenum': num
        }, (res) => {
          if (res.code === ERR_OK) {
            let arr = [], data = res.data.data;
            if (this.$Utils.isJson(res.data.data)) {
              for (let k in data) {
                arr.push(data[k]);
              }
              this.readTabData = arr;
            } else if (this.$Utils.isArray(data)) {
              this.readTabData = res.data.data;
            }
            this.studentSum = res.data.sum;
            this.readTabPage.total = res.data.pageinfo.total;
            this.readTabData.forEach(el => {
              this.$set(el, 'comment', '');
              this.$set(el, 'score', 0);
              this.$set(el, 'attendancestatus', 1);
            })
          }
        });
      },
      /**
       * 提交点评
       */
      handleSubmit (name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.review(2);
          }
        })
      },
      /**
       * 判断老师是否批阅过学生出勤,
       * @param pagenum 当前页码
       */
      getOverStudentList(pagenum) {
        Server.post(Path.getAttendance, {
          'lessonsid': this.$route.query.lessonsid, 'pagenum': pagenum
        }, (res) => {
          if (res.code === ERR_OK) {
            if (myDate.getNowTime(this.$route.query.endtime)) {//当前时间大于结束时间，显示出勤表
              this.isHasEnded = true;
              if (res.data.allstatus === 0) {//如果没有批阅过，显示批阅弹窗
                this.isShowModal = true;
                this.getStudentList(this.current);
              } else {//有一个批阅过，不显示批阅弹窗
                this.isShowModal = false;
              }
              this.readTabData = res.data.data.data;           //出勤表数据
              this.studentSum = res.data.data.pageinfo.total;  //学生数
              this.total = res.data.data.pageinfo.total;
            } else {//课时未结束，显示所有学生列表
              this.isHasEnded = false;
              this.getStudentList(this.current);
            }
          }
        });
      },
      //学生出勤弹框通过 attendancestatus 判断是否弹出，
      //学生列表的修改出勤状态和点评，每次提交所有学生的状态
      pageChange(current) {
        this.getOverStudentList(current);
      },
      /**
       * 批阅学生出勤
       * @param type 1：批阅  2：点评
       */
      review(type) {
        let arr = [];
        let subArr = [];
        this.readTabData.forEach((el, i) => {
          if(type === 1) {
            arr.push({
              lessonsid: this.$route.query.lessonsid,
              studentid: el.studentid || el.id,
              attendancestatus: el.attendancestatus,
              // score: el.score || 0,
              // comment: el.comment,
            });
          } else if (type === 2) {
            arr.push({
              lessonsid: this.$route.query.lessonsid,
              studentid: el.studentid || el.id,
              //attendancestatus: el.attendancestatus,
              score: el.score,
              comment: el.comment,
            });
          }

        });
        //判断是否第一次提交
        if (this.tableIndex !== '') {
          arr[this.tableIndex].comment = this.ratingModal.comment;
          arr[this.tableIndex].score = this.ratingModal.score;
        }
        if(type === 2){
          if(!this.ratingModal.score){
            this.$Message.error('请填写评分')
            return false
          }
        }
        Server.post(Path.upAttendance, arr, res => {
          let {code, info} = res;
          this.isShowModal = false;
          this.isShowRatingModal = false;
          if (code === ERR_OK) {
            this.$Message.success(info);
            this.$emit('addWareSuccess');
            this.getOverStudentList(this.current);
          } else {
            this.$Message.error(info);
          }
        });
      },
    },
  };
</script>
<style lang="less" scoped>
  @import "../assets/courseinfo-title.less";

  .student-table {
    margin-top: 44px;
    max-width: 1396px;
  }

  .content-title {
    font-size: 16px;
    font-weight: bold;
    color: #333333;
  }

  .content-starttime,
  .content-student,
  .content-rating {
    line-height: 40px;
    font-size: 14px;
    color: #333333;
  }

  .inline-block {
    display: inline-block;
  }
</style>



