<template>
  <div class="nm-header">
    <Layout>
      <Header>
        <div class="layout-logo">
          <img :src="icons.linklogo" class="logo-lemon" alt="柠檬树下大语文">
          <img :src="icons.logoText" class="logo-text">
        </div>
        <div class="layout-nav fr">
          <ul class="layout-nav-list">
            <router-link class="list-item list-item-border dropdown" to="/TeacherEdit" tag="div">
              <!-- 头像 -->
              <div class="item-avatar">
                <defaultHead :sourceimg="baseInfo && baseInfo.imageurl || icons.defaultHeadImg"></defaultHead>
              </div>
              <!-- 用户名 -->
              <span class="nickname" :title="baseInfo && baseInfo.nickname">{{baseInfo && baseInfo.nickname}}</span>
            </router-link>
            <!-- 回到主页 -->
            <li class="list-item list-item-border" title="返回首页" v-if="getTeacherType=='1'||getTeacherType=='1,2'">
              <router-link to="/CalendarSchedule" tag="div">
                <img class="nav-right-icons" :src="icons.icon_home"/><span>首页</span>
              </router-link>
            </li>
            <!-- 我的消息 -->
            <li
              class="list-item list-item-border"
              @click="isShowMessage = true; getMsgList()"
              title="消息"
              v-if="getTeacherType=='1'||getTeacherType=='1,2'">
              <div>
                <img class="nav-right-icons" :src="headerMessageIcon"><span>消息</span>
              </div>
            </li>
            <!-- 退出登录 -->
            <li class="list-item" @click="showModal" title="退出">
              <img class="nav-right-icons" :src="icons.icon_close"><span>退出</span>
            </li>
          </ul>
        </div>
        <div class="message-shade" v-show="isShowMessage" @click="isShowMessage = false; msgDone()"></div>
        <div class="message-box" v-show="isShowMessage" ref="main">
          <div class="message-box-head" v-if="getNoViewedMsgCount !== 0">消息提醒</div>
          <ul class="message-box-body" v-if="messageList && messageList.length > 0">
            <router-link tag="li" to="/MyMessage" v-for="(el, i) in messageList" v-if="el.istoview===0" :key="i">
              <span class="content">{{el.content}}</span>
              <span class="date">{{el.addtime}}</span>
            </router-link>
          </ul>
          <div
            class="message-box-nomessage"
            style="text-align: center; font-size: 14px;"
            v-if="getNoViewedMsgCount == 0"
          >
            {{$t('Main.MaterMessanage.NoData')}}
          </div>
        </div>
        <!-- 退出登录 -->
        <Modal class="mytheme-modal" v-model="isLogoutModal" :mask-closable="false" :scrollable="true">
          <div class="modal-head" slot="header">{{$t('Main.ConfirmExit')}}</div>
          <div class="modal-body text-center">{{$t('Main.AreYouSureWantToQuit')}}</div>
          <div class="modal-foot" slot="footer">
            <!--确认-->
            <Button class="modal-foot-btn small-btn mytheme-btn" type="primary" @click="logOut()">
              {{$t('Main.SaveBtn')}}
            </Button>
            <!--取消-->
            <Button class="mytheme-btn modal-foot-btn small-btn" type="default" @click="isLogoutModal = false">
              {{$t('Main.CancelBtn')}}
            </Button>
          </div>
        </Modal>
      </Header>
    </Layout>
  </div>
</template>

<script>
  import nm_topbar from '../nm_topbar';
  import {Layout, Header, Modal, Button} from 'iview';

  import logo from '@/assets/images/logo_2.png';
  import logoText from '@/assets/images/logo_text.png';
  import linklogo from '@/assets/images/logo.png';
  import icon_message_normal from '@/assets/images/icon_message_header_normal.png';
  import icon_message from '@/assets/images/icon_message_header.png';
  import icon_close from '@/assets/images/icon_close_righttop@2x.png';
  import icon_home from '@/assets/images/icon_home_righttop@2x.png';
  import defaultHeadImg from '@/assets/images/defaultAvatar.png';
  import defaultHead from '@/components/defaultHeader';
  import Path from '@/service/Path';
  import Server from '@/service/request';

  export default {
    name: "nm-header",
    components: {
      nm_topbar,
      Layout,
      Header,
      defaultHead,
      Modal,
      Button,
    },
    data() {
      return {
        icons: {
          logo,
          icon_message_normal,
          icon_message,
          icon_close,
          icon_home,
          defaultHeadImg,
          logoText,
          linklogo,
        },
        headerMessageIcon: icon_message_normal,
        baseInfo: {
          imageurl: '',
          nickname: '',
        },
        isLogoutModal: false,
        isShowMessage: false,
        messageList: [],
        messageDoneArr: [],//已读消息的id数组
      }
    },
    created() {
      if (this.$Utils.isLogin()) {
        this.getTeacherInfo();
        if(this.getTeacherType == '2') return;
        this.getMsgList();
      } else {
        this.$router.replace('/Login');
      }
    },
    watch: {
      //监听头像变化，同时更改header头像
      getHeadImg(imageurl) {
        this.baseInfo.imageurl = imageurl;
      },
      //监听昵称变化
      getNickname(nickname) {
        this.baseInfo.nickname = nickname;
      },
      getNoViewedMsgCount(count) {
        if (count > 0) {
          this.headerMessageIcon = icon_message;
        }
      },
    },
    computed: {
      getHeadImg() {
        return this.$store.store.state.headimg;
      },
      getNickname() {
        return this.$store.store.state.nickname;
      },
      getNoViewedMsgCount() {
        let count = 0;
        this.messageList.forEach(el => {
          if (el.istoview === 0) {
            count++;
          }
        });
        return count;
      },
      getTeacherType() {
        let local = localStorage.getItem('teacher_info'),
          teachertype = JSON.parse(local).teachertype;
        return teachertype ? teachertype : 1;
      },
    },
    methods: {
      showModal() {
        this.isLogoutModal = true;
      },
      getTeacherInfo() {
        let {baseInfo} = this;
        if (this.$loca.getItem('teacher_info') != null || this.$loca.getItem('teacher_info') != '') {
          baseInfo = JSON.parse(this.$loca.getItem('teacher_info'));
          Server.post(Path.teachInfo, {
            teachid: baseInfo.teachername
          }, (res) => {
            if (res.code === 0) {
              this.baseInfo = res.data.baseinfo;
            }
          });
          Server.post(Path.getLogo, {}, (res) => {
            let {
              code,
              data
            } = res;
            if (code === 0) {
              if (data !== null) {
                this.logoInfo = data;
                document.title = '柠檬树下大语文';
                var link = document.querySelector("link[rel*='icon']") || document.createElement('link');
                link.type = 'image/x-icon';
                link.rel = 'shortcut icon';
                link.href = linklogo;
                document.getElementsByTagName('head')[0].appendChild(link);
              }
            } else {
            }
          });
        }
      },
      /**
       * 获取消息列表
       */
      getMsgList() {
        Server.post(Path.msgList, {
          'pagenum': '1'
        }, (res) => {
          let {code, data, info} = res;
          if (code === 0) {
            this.messageList = data.data;
            this.messageList.forEach(el => {//获取列表每条未读消息的id
              if (el.istoview == 0) {
                this.messageDoneArr.push(el.id);
              }
            });
          }
        });
      },
      /**
       * 将消息标记为已读
       * @Author   qygh
       * @DateTime 2018-08-07
       * @version  2.0
       */
      msgDone() {
        if (this.messageDoneArr.length === 0) {//暂无最新消息，取消请求
          return;
        }
        this.headerMessageIcon = icon_message_normal;//标记为已读消息后，更换已读消息图标
        Server.post(Path.setMessage, {
          idarr: this.messageDoneArr,
        }, (res) => {
          let {code, info} = res;
          if (res) {
            this.getMsgList();
          }
          if (code === 0) {
            this.getMsgList();
          }
        });
      },
      /**
       * 退出登录
       */
      logOut: function () {
        let token = JSON.parse(localStorage.getItem('teacher_info')).token;
        this.isshow = false;
        Server.post(Path.exitLogin, {
          token: token
        }, (res) => {
          if (res.code === 0) {
            window.localStorage.removeItem('teacher_info');
            window.localStorage.removeItem('teacher_key');
            this.$router.push('/Login');
          }
        });
      },
    }
  }
</script>

<style scoped lang="less">
  .nm-header {
    position: fixed;
    width: 100%;
    z-index: 9;
    .layout-logo {
      display: inline-block;
      height: 80px;
      line-height: 80px;
      vertical-align: top;
      font-size: 0;
      img {
        height: 56px;
        vertical-align: middle;
        &.logo-text {
          height: 50px;
        }
      }
    }
    .layout-nav-list {
      height: 80px;
      line-height: 80px;
      font-size: 0;
      .list-item {
        position: relative;
        display: inline-block;
        padding: 0 24px;
        font-size: 14px;
        color: #fff;
        cursor: pointer;
        &:last-child:after {
          display: none;
        }
        &:after {
          position: absolute;
          right: 0;
          top: 34px;
          content: ' ';
          display: block;
          width: 1px;
          height: 16px;
          border-left: 1px dashed #fff;
        }
        .nickname {
          vertical-align: top;
          display: inline-block;
          max-width: 120px;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
        .item-avatar {
          display: inline-block;
          margin: 0 22px 0 0;
          width: 40px;
          height: 40px;
          border-radius: 50%;
          overflow: hidden;
          background: #eee;
          vertical-align: middle;
          cursor: pointer;
        }
        .nav-right-icons {
          margin-right: 14px;
          width: 20px;
          height: 20px;
          vertical-align: middle;
        }
      }
    }
    .message-box {
      z-index: 1000;
      position: absolute;
      padding: 0 30px;
      right: 30px;
      top: 80px;
      width: 610px;
      background-color: #fff;
      border-radius: 2px;
      box-shadow: 0px 0px 54px rgba(163, 163, 163, 0.5);
      .message-box-head {
        line-height: 50px;
        font-size: 16px;
        text-align: center;
        border-bottom: 1px solid #EBEBEB;
      }
      .message-box-body {
        font-size: 14px;
        li {
          border-bottom: 1px solid #EBEBEB;
          height: 40px;
          line-height: 40px;
          cursor: pointer;
          .date {
            float: right;
          }
          &:last-child {
            border-bottom: none;
          }
        }
      }
      .message-box-nomessage {
        text-align: center;
        font-size: 14px;
        line-height: 80px;
      }
    }
    .message-shade {
      position: fixed;
      top: 0;
      right: 0;
      left: 0;
      bottom: 0;
      z-index: 999;
    }
  }
</style>



