<template>
  <div id="coursedetail-coursecover">
    <Card class="noradius-card mytheme-noborder mytheme-content-minheight mytheme-card-padding" dis-hover>
      <div class="evaluation-box">
        <div class="evaluation-item">
          <div class="eval-header-img">
            <img :src="data.courseimage || defaultCover" @error="_imageError">
          </div>
          <div class="eval-content">
            <ul>
              <li>
                <div class="eval-name periodname">{{data.periodname}}</div>
              </li>
              <li>
                <!--班级：-->
                <div class="eval-class-type">
                  <span class="eval-label">班级名称：</span>
                  <span class="eval-val coursename">{{data.gradename}}</span>
                </div>
                <!--课程：-->
                <div class="eval-class-type">
                  <span class="eval-label">课程名称：</span>
                  <span class="eval-val">{{data.coursename}}</span>
                </div>
              </li>
              <li>
                <!--开始时间：-->
                <div class="eval-time">
                  <span class="eval-label">{{$t('Main.MyLessonList.StartTime')+$t('Main.Symbols.Colon')}}</span>
                  <span class="eval-val">{{starttime}}</span>
                </div>
                <!--结束时间：-->
                <div class="eval-time">
                  <span class="eval-label">{{$t('Main.MyLessonList.EndTime')+$t('Main.Symbols.Colon')}}</span>
                  <span class="eval-val">{{endtime}}</span>
                </div>
              </li>
            </ul>
          </div>
        </div>
        <div class="evaluation-overview">
          <div class="eval-label lessonGeneral">课程概述：</div>
          <div class="eval-val">
            <div class="generalizeTxt" v-html="data.generalize"></div>
          </div>
        </div>
      </div>
    </Card>
  </div>
</template>
<script>
  import {Card, Row, Col} from 'iview';
  import defaultCover from '@/assets/images/defaultCover.png';

  export default {
    name: "coursedetail-coursecover",
    props: ['data'],
    components: {
      Card, Row, Col
    },
    data() {
      return {
        defaultCover,
        starttime: this.$route.query.starttime,
        endtime: this.$route.query.endtime,
        isload: false,
      }
    },
    mounted() {
      this.isload = true;
    },
  }
</script>
<style lang="less" scoped>
  @import "~@/assets/less/variable";

  .evaluation-overview {
    display: flex;
    .eval-val {
      flex: 1;
      max-width: 1423px;
    }
  }

  .evaluation-box {

    ul {
      .eval-label {
        display: table-cell;
        width: 90px;
        min-width: 90px;
        color: #4D4D4D;
      }
          
      .eval-val {
        display: table-cell;
        color: #808080;
        word-break: break-all;
        word-wrap: break-word;
        white-space: pre-wrap;
        font-size: @font-size-normal-s;
        &.coursename {
          color: #4D4D4D;
        }
      }
    }

    .evaluation-item {
      box-sizing: content-box;
      padding: 56px 0;
      .eval-header-img {
        display: inline-block;
        width: 472px;
        height: 278px;
        margin-right: 46px;
        overflow: hidden;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .eval-content {
        display: inline-block;
        vertical-align: top;
        padding-top: 7px;
        ul {
          li {
            overflow: hidden;
            font-size: @font-size-normal-s;
            line-height: 1;
            .eval-class-type {
              padding-bottom: 36px;
              line-height: 1;
              &:last-child {
                margin-right: 0;
              }
              &.student-number {
                padding-left: 20px;
              }
            }
            .eval-name {
              padding-bottom: 42px;
              font-size: @font-size-normal;
              font-weight: bold;
            }
            .eval-time {
              padding-bottom: 36px;
              &:last-child {
                margin-right: 0;
              }
            }
            .inline-block {
              display: inline-block;
            }
          }
        }
      }
    }
    .generalizeTxt{
      word-wrap:break-word;
      padding-right: 5%;
    }
    .eval-val{
      width: 100%;
    } 
    .lessonGeneral{
      min-width: 90px!important;
    }
  }
</style>
<style scoped>
  .generalizeTxt >>> p img {
    width: 100%;
    vertical-align: top!important;
  }
</style>



