<template>
  <div>
    <panel-title :crumbs="crumbs"></panel-title>
    <div class="mytheme-container-mg-30">
      <Card class="mytheme-card-padding mytheme-noborder noradius-card" dis-hover>
        <div class="mytheme-mg-top-20">
          <span class="input-label mytheme-input-label">{{$t('Main.WonderfulMoment.CourseName') + $t('Main.Symbols.Colon')}}</span>
          <Input
            class="input-normal-width input-max-height"
            :placeholder="$t('Main.WonderfulMoment.InputKey')"
          />
          <span class="input-label mytheme-mg-left-70">{{ $t('Main.WonderfulMoment.ClassName') + $t('Main.Symbols.Colon') }}</span>
          <Input
            class="input-normal-width input-max-height"
            :placeholder="$t('Main.WonderfulMoment.InputKey')"
          />
          <Button class="mytheme-mg-left-70" type="primary">搜索</Button>
        </div>
        <template>
          <i-table
            class="mytheme-table thead-max-height noborder-table bottom-radius thead-max-height mytheme-mg-top-30"
            ref="selection"
            :stripe="true"
            :columns="columns"
            :data="data"
            :no-data-text="$t('Main.WonderfulMoment.NoData')"
          ></i-table>
        </template>
        <template>
          <Pagination
            class="mytheme-mg-top-20"
            :total="total"
            :current-page='current'
            :display="display"
            @pagechange="pageChange">
          </Pagination>
        </template>
      </Card>
    </div>
    <Modal class="mytheme-modal" v-model="isShowModal">
      <div class="modal-head" slot="header">
        <span>{{this.$t('Main.WonderfulMoment.ShareTo')}}</span>
      </div>
      <div class="modal-body">
        <ul class="message">
          <li>
            <span>{{ $t('Main.WonderfulMoment.CourseName') + $t('Main.Symbols.Colon') }}</span>
            <p>小学三年级语文</p>
          </li>
          <li>
            <span>{{ $t('Main.WonderfulMoment.ClassName') + $t('Main.Symbols.Colon') }}</span>
            <p>考前冲刺班</p>
          </li>
          <li>
            <span>{{ $t('Main.WonderfulMoment.PeriodName') + $t('Main.Symbols.Colon') }}</span>
            <p>课时3 古诗词阅读</p>
          </li>
          <li>
            <span>{{ $t('Main.WonderfulMoment.Menu') + $t('Main.Symbols.Colon') }}</span>
            <p>2018-08-20 10:00:00</p>
          </li>
        </ul>
        <i-table
          class="mytheme-table tbody-min-height"
          :columns="modalColData"
          :data="modalTabData"
          @on-selection-change="selectionChange"
          border>
        </i-table>
        <Pagination
          class="clearfloat"
          :total="modalTotal"
          :currentPage='modalCurrent'
          :display="modalDisplay"
          @pagechange="modalPageChange">
        </Pagination>
      </div>
      <div class="modal-foot " slot="footer">
        <!--取消-->
        <Button class="modal-foot-btn" @click="isShowModal = false;">取消</Button>
        <!--确认-->
        <Button class="modal-foot-btn" type="primary" @click="momentConfirm()">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
  import panelTitle from '@/components/panelTitle';
  import Pagination from '@/components/pagination';
  import {
    Card,
    Form,
    Input,
    Button,
    Table,
    Modal,
    Checkbox,
    Radio
  } from 'iview';

  export default {
    name: "WonderfulMoment",
    components: {
      panelTitle,
      Card,
      Form,
      Input,
      Button,
      "i-table": Table,
      Pagination,
      Modal,
      Checkbox,
      Radio,
    },
    data() {
      return {
        crumbs: [{
          name: this.$t('Main.WonderfulMoment.Title'),
          route: '/WonderfulMoment'
        }],
        isShowModal: false,
        total: 0, //记录总条数
        display: 20, //每页显示20条数据
        current: 1, //当前页
        inputTxt: "",

        modalTotal: 0,
        modalDisplay: 7,
        modalCurrent: 1,
        modalSendIds: [],
        modalColData: [{
          type: 'selection',
          render: (h, params) => {
            return h('Checkbox', {
              title: '',
              props: {
                value: params.row.ischeck,
              },
              on: {
                'on-select': () => {
                  this.modalSendIds = [];
                  this.modalTabData.forEach(el => {
                    if (el.ischeck) {
                      this.modalSendIds.push(el.id);
                    }
                  });
                },
              }
            });
          },
          width: 50,
        },
          {
            key: 'sortid',
            title: '序号',
            align: 'center'
          },
          {
            key: 'studentname',
            title: '学生姓名',
            align: 'center'
          },
        ],
        modalTabData: [{
          ischeck: true,
          sortid: 1,
          studentname: '芙蓉王',
        },
          {
            ischeck: false,
            sortid: 2,
            studentname: '阿萨啊',
          },
          {
            ischeck: false,
            sortid: 3,
            studentname: '看卡',
          },
          {
            ischeck: false,
            sortid: 4,
            studentname: '这些',
          },
          {
            ischeck: false,
            sortid: 5,
            studentname: '常出现',
          },
          {
            ischeck: false,
            sortid: 6,
            studentname: '自行车',
          },
          {
            ischeck: false,
            sortid: 7,
            studentname: '燃烧',
          },
          {
            ischeck: false,
            sortid: 8,
            studentname: '燃烧asda ',
          },

        ],
        columns: [{
          title: this.$t('Main.WonderfulMoment.Num'),
          align: 'center',
          key: 'id'
        }, {
          title: this.$t('Main.WonderfulMoment.CourseName'),
          align: 'center',
          key: 'courseName'
        }, {
          title: this.$t('Main.WonderfulMoment.ClassName'),
          align: 'center',
          key: 'className'
        }, {
          title: this.$t('Main.WonderfulMoment.PeriodName'),
          align: 'center',
          key: 'periodName'
        }, {
          title: this.$t('Main.WonderfulMoment.Hours'),
          align: 'center',
          key: 'hours'
        }, {
          title: this.$t('Main.WonderfulMoment.RecordTime'),
          align: 'center',
          key: 'recordTime'
        }, {
          title: this.$t('Main.WonderfulMoment.Operate'),
          align: 'center',
          width: '200',
          render: (h, params) => {
            return h('div', [
              h('a', {
                style: {
                  marginRight: '30px'
                },
                on: {
                  'click': () => {
                    this.$router.push({
                      path: '/MomentDetail',
                      query: {
                        taskID: params.row.Make,
                        route: this.$route.fullPath,
                      }
                    });
                  }
                },
                attrs: {
                  class: 'blue mytheme-table-btn',
                }
              }, this.$t('Main.WonderfulMoment.View')), //查看
              h('a', {
                style: {
                  cursor: 'pointer',
                  color: '#F89240',
                  display: 'inline-block',
                  marginRight: '30px'
                },
                on: {
                  'click': () => {
                    this.isShowModal = true;
                  }
                },
                attrs: {
                  class: 'tablebtn blue',
                }
              }, this.$t('Main.WonderfulMoment.Send')), //发送
              h('a', {
                on: {
                  'click': () => {
                    this.remove(params.index)
                  }
                },
                style: {
                  cursor: 'pointer',
                  display: 'inline-block',
                  marginRight: '10px'
                },
                attrs: {
                  class: 'tablebtn red'
                }
              }, this.$t('Main.WonderfulMoment.Delete')), //删除
            ])
          }
        }],
        data: [{
          "id": '001',
          "courseName": "诗词歌赋鉴赏",
          "className": "考试冲刺班",
          "periodName": "课时3：古诗文阅读",
          "hours": "12分8秒",
          "recordTime": "2018-08-20 18：00：03",
        }, {
          "id": '002',
          "courseName": "诗词歌赋鉴赏",
          "className": "考试冲刺班",
          "periodName": "课时3：古诗文阅读",
          "hours": "12分8秒",
          "recordTime": "2018-08-20 18：00：03",
        }, {
          "id": '003',
          "courseName": "诗词歌赋鉴赏",
          "className": "考试冲刺班",
          "periodName": "课时3：古诗文阅读",
          "hours": "12分8秒",
          "recordTime": "2018-08-20 18：00：03",
        }, {
          "id": '004',
          "courseName": "诗词歌赋鉴赏",
          "className": "考试冲刺班",
          "periodName": "课时3：古诗文阅读",
          "hours": "12分8秒",
          "recordTime": "2018-08-20 18：00：03",
        }, {
          "id": '005',
          "courseName": "诗词歌赋鉴赏",
          "className": "考试冲刺班",
          "periodName": "课时3：古诗文阅读",
          "hours": "12分8秒",
          "recordTime": "2018-08-20 18：00：03",
        }, {
          "id": '006',
          "courseName": "诗词歌赋鉴赏",
          "className": "考试冲刺班",
          "periodName": "课时3：古诗文阅读",
          "hours": "12分8秒",
          "recordTime": "2018-08-20 18：00：03",
        }],
      }
    },
    created() {
      //do something after creating vue instance
      // console.log(this.modalSendIds);
    },
    methods: {
      remove(index) {
        this.data.splice(index, 1);
      },
      pageChange(current) {
      },
      modalPageChange(current) {

      },
      momentConfirm() {

      },
      /**
       * 勾选多选框时获取要发送的id
       * @param  {Array} selectArr 返回选中状态的值
       */
      selectionChange(selectArr) {
        this.modalSendIds = [];
        selectArr.forEach(el => {
          this.modalSendIds.push(el.sortid);
        });
      },
    }
  }
</script>

<style lang="less" scoped>

  .message {
    margin-bottom: 10px;
    li {
      p {
        display: inline-block;
      }
    }
  }
</style>



