<template>
  <div class="detail-baseinfo">
    <!--学生详情-->
    <Card class="mytheme-noborder noradius-card mytheme-content-minheight mytheme-card-padding" dis-hover>
      <table class="mytheme-table">
        <tr>
          <td rowspan="5" width="280">
            <Avata
              :avataImage="baseinfo.imageurl"
              :avataStyle="AvataStyle"
              :nickname="baseinfo.nickname">
            </Avata>
          </td>
          <td class="mytheme-table-bg" width="280">性别</td>
          <td>{{renderSex(baseinfo.sex)}}</td>
        </tr>
        <tr>
          <td class="mytheme-table-bg">手机</td>
          <td>{{baseinfo.mobile}}</td>
        </tr>
        <tr>
          <td class="mytheme-table-bg">生日</td>
          <td>
            <div v-if="baseinfo.birthday">
              {{baseinfo.birthday}}
            </div>
            <div v-else>保密</div>
          </td>
        </tr>
        <tr>
          <td class="mytheme-table-bg">住址</td>
          <td>
            <div v-if="baseinfo.country">
              <span class="city">{{AreaJson[String(baseinfo.country)]}}</span>
              <span class="city">{{AreaJson[String(baseinfo.province)]}}</span>
              <span class="city">{{AreaJson[String(baseinfo.city)]}}</span>
            </div>
            <div v-else>----</div>
          </td>
        </tr>
        <tr>
          <td class="mytheme-table-bg">个性签名</td>
          <td>
            <div v-if="baseinfo.profile">
              {{baseinfo.profile}}
            </div>
            <div v-else>这个人很懒，什么都没有留下...</div>
          </td>
        </tr>
      </table>
    </Card>
  </div>
</template>

<script>
  import AreaJson from '@/Api/city.json';
  import Avata from '@/components/Avata';
  import {Card} from 'iview';

  export default {
    name: "detail-baseinfo",
    props: ['baseinfo'],
    components: {
      Card,
      Avata,
    },
    data() {
      return {
        AvataStyle: {
          width: '113px',
          height: '113px',
          margin: 'auto',
        },
        AreaJson,
      }
    },
    methods: {
      renderSex(sex) {
        if (sex == 0) {
          return '保密';
        } else if (sex == 1) {
          return '男';
        } else if (sex == 2) {
          return '女';
        }
      },
    },
  }
</script>

<style scoped lang="less">
  .mytheme-table {
    margin-top: 56px;
    max-width: 1230px;
    width: 100%;
    border-collapse: collapse;
    border: 1px solid transparent;
    text-align: center;
    tr {
      &:last-child {
        height: 90px;
      }
      td {
        height: 56px;
        border: 1px solid #E6E6E6;
        &.mytheme-table-bg {
          background: #F7F7F7;
        }
        .city {
          padding-right: 10px;
        }
      }
    }
  }
</style>



