import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
    state: {
      step:1,
      headimg: 0,
      nickname: '',
    },
    mutations: {
      selectStep(state,d){
        state.step = d;
      },
      uploadHeadimg(state, d) {
        state.headimg = d;
      },
      updateNickname(state, d) {
        state.nickname = d;
      },
    }
  }
)

export default {
  store
}



