<template>
  <div id="coursedetail-task">
    <div class="courseinfo-title">
			<img class="icon" src="../assets/icon_evaluate.png">
			<span class="title">布置作业</span>
      <span class="btn" v-if="!getNowTime(endtime)">
          <!-- 编辑 -->
          <a class="fr" v-if="isShowEditBtn" @click="isShowEditBtn = false">{{$t('Main.EditBtn')}}</a>
         <!-- 保存 -->
          <a class="fr" v-else @click="isShowEditBtn = true;">{{$t('Main.ArrangMessanage.SaveBtn')}}</a>
      </span>
		</div>
    <!-- 作业列表 -->
    <ul class="task-list" v-if="getNowTime(endtime)">
      <li class="list-item">
        <div class="item-icon"></div>
        <div class="item-title"> 霜叶红于二月花</div>
        <img class="item-delicon" :src="icons.delIcon" v-if="!isShowEditBtn" @click="isShowDelModal=true;">
      </li>
    </ul>
    <!-- 添加作业 -->
    <Button class="fr addtask-btn" v-if="!isShowEditBtn" @click="isShowAddTaskModal = true">
      <img :src="icons.addIcon"><span>添加作业</span>
    </Button>
    <!-- 确定删除作业吗？ -->
    <Modal class="mytheme-modal" v-model="isShowDelModal">
      <div class="modal-head" slot="header">温馨提示</div>
      <div class="modal-body">确定删除作业吗？</div>
      <div class="modal-foot" slot="footer">
        <Button type="text" @click="isShowDelModal=false;">取消</Button>
        <Button type="primary" shape="circle">确定</Button>
      </div>
    </Modal>
    <!-- 您是否要使用模板布置作业 -->
    <Modal class="mytheme-modal" v-model="isShowAddTaskModal">
      <div class="modal-head" slot="header">发布作业</div>
      <div class="modal-body">您是否要使用模板布置作业？</div>
      <div class="modal-foot" slot="footer">
        <Button type="text" @click="isShowAddTaskModal=false;">取消</Button>
        <Button type="primary" shape="circle">确定</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
import delIcon from "@/assets/images/icon_del@2x.png";
import addIcon from "@/assets/images/icon_add@2x.png";
import { Modal, Button } from "iview";
export default {
  name: "coursedetail-task",
  components: { Modal, Button },
  data() {
    return {
      icons: { delIcon, addIcon },
      endtime: this.$route.query.endtime,
      isShowEditBtn: true,  //是否显示编辑按钮
      isShowDelModal: false,//是否显示删除提示
      isShowAddTaskModal: false, //是否显示发布作业Modal

    };
  },
  methods: {
    getNowTime(d2) {
      let date = new Date(),
        year = date.getFullYear(),
        month = date.getMonth() + 1,
        day = date.getDate(),
        hour = date.getHours(),
        minute = date.getMinutes();
      let dateStr = `${year}-${month}-${day} ${hour}:${minute}`;
      if (d2) {
        return (
          new Date(dateStr.replace(/-/g, "/")) > new Date(d2.replace(/-/g, "/"))
        );
      }
    }
  }
};
</script>
<style lang="less" scoped>
@import "../assets/courseinfo-title";
.task-list {
  padding: 30px 0 30px 22px;
  .list-item {
    position: relative;
    margin-bottom: 10px;
    height: 50px;
    line-height: 50px;
    background: rgba(175, 187, 185, 0.1);
    border-radius: 4px;
    font-size: 14px;
    .item-icon {
      vertical-align: middle;
      display: inline-block;
      color: #8e9daa;
      font-size: 18px;
      margin: 0 20px 0 30px;
    }
    .item-title {
      display: inline-block;
    }
    .item-delicon {
      position: absolute;
      right: 30px;
      top: 0;
      bottom: 0;
      margin: auto;
      cursor: pointer;
    }
  }
}
.addtask-btn {
  display: block;
  img {
    vertical-align: middle;
    margin-right: 4px;
  }
  span {
    vertical-align: middle;
    font-size: 14px;
  }
}
</style>



