<template>
  <div class="Avata">
    <div :style="{width: avataStyle.width, height: avataStyle.height, margin: avataStyle.margin}">
      <div class="avata-img">
        <img :src="avataImage || getErrorHeadImg" @error="_defaultHead">
      </div>
    </div>
    <div class="avata-nickname" :style="{marginTop: avataStyle.marginTop}">
      <span :style="{fontSize: avataStyle.fontSize}">{{nickname}}</span>
      <img v-if="showSex === 2" :src="male" class="male">
      <img v-if="showSex === 1" :src="female" class="female">
    </div>
    <div class="avata-logintime">
      <!--最近登陆时间-->
      <div v-if="ShowLoginTime">{{$t('Main.Setting.LoginTime')}}</div>
      <div>{{ShowLoginTime}}</div>
    </div>
  </div>
</template>

<script>
  import male from '@/assets/images/icon_female@2x.png';
  import female from '@/assets/images/icon_male@2x.png';

  export default {
    name: "Avata",
    props: {
      avataImage: {
        type: String,
        default: '',
      },
      //昵称
      nickname: {
        type: String,
        default() {
          return ''
        },
      },
      //是否显示性别
      showSex: {
        type: Number,
        default: 0,
      },
      //是否显示登陆时间
      ShowLoginTime: {
        type: String,
        default() {
          return ''
        },
      },
      //头像样式
      avataStyle: {
        type: Object,
        default() {
          return {
            width: '113px',
            height: '113px',
            margin: 'auto'
          }
        }
      },
    },
    data() {
      return {
        male,
        female,
      }
    },
    computed: {
      getErrorHeadImg() {
        return this._imageError;
      }
    }
  }
</script>

<style scoped lang="less">
  .Avata {
    text-align: center;
    .avata-img {
      width: 100%;
      height: 100%;
      border-radius: 50%;
      overflow: hidden;
      img {
        border-radius: 50%;
        width: 100%;
        height: 100%;
      }
    }
    .avata-nickname {
      text-align: center;
      margin-top: 20px;
      font-size: 18px;
      color: rgba(94, 98, 102, 1);
      img {
        vertical-align: middle;
        width: 20px;
        height: 20px;
      }
    }
    .avata-logintime {
      margin-top: 28px;
      text-align: center;
      color: #F89240;
    }
  }
</style>



