<template>
  <div id="UserLessonList">
    <panel-title :crumbs="crumbs"></panel-title>
    <Card
      class="mytheme-container-mg-30 mytheme-noborder noradius-card mytheme-card-padding"
      dis-hover
      v-if="mydata">
      <div class="fixTitle">
        <div class="cardTitle">{{formatDate}}</div>
      </div>
      <Row>
        <Col span="24">
          <div class="evaluation-box">
            <div class="evaluation-item" v-for="d in mydata" :key="d.id">
              <div class="eval-header-img">
                <img :src="d.courseimage || defaultCover" @error="_imageError">
              </div>
              <div class="eval-content">
                <ul>
                  <li>
                    <div class="eval-name">{{d.periodname}}</div>
                  </li>
                  <li>
                    <div class="eval-time">
                      {{$t('Main.MyLessonList.StartTime')}}{{$t('Main.Symbols.Colon')}}{{d.starttime}}
                    </div>
                    <div class="eval-time">
                      {{$t('Main.MyLessonList.EndTime')}}{{$t('Main.Symbols.Colon')}}{{d.endtime}}
                    </div>
                  </li>
                  <li>
                    <!--<div class="eval-class-type">{{d.coursename}}</div>-->
                    <div class="eval-class-type inline-block">
                      <div class="inlineblock coursename-label">课程名称：</div>
                      <div class="inlineblock coursename-val">{{d.gradename}}</div>
                    </div>
                    <div class="eval-class-type student-number inline-block">
                      {{$t('Main.MyLessonList.Student')}}
                      {{$t('Main.Symbols.Colon')}}
                      {{$t('Main.MyLessonList.ATotalOf')}}
                      {{d.sum || 0}}{{$t('Main.MyLessonList.People')}}
                    </div>
                  </li>
                </ul>
              </div>
              <div class="eval-btngroup">
                <!--进教室-->
                <Button
                  class="enter-btn"
                  v-if="d.classstatus == 1"
                  type="success"
                  @click="enterRoom(organid, d.id)">
                  {{$t('Main.MyLessonList.EnterClassroom')}}
                </Button>
                <!--未开始-->
                <Button
                  class="status-btn"
                  v-if="d.classstatus == 2"
                  type="success">
                  {{$t('Main.MyLessonList.NotStarted')}}
                </Button>
                <!--查看-->
                <router-link :to="{path:'LessonDetail',
                        query:{
                          toteachtimeid: d.id,
                          starttime: d.starttime,
                          endtime: d.endtime,
                          date: date,
                          id: d.lessonsid,
                          lessonsid: d.lessonsid,
                          isChangeFile: d.classstatus,
                          parentRoute: $route.query.route,
                          name: crumbs[1].name,
                          route: $route.fullPath,
                          type: d.type,
                          periodname: d.periodname,
                          schedulingid: d.schedulingid,
                          curriculumid: d.curriculumid,
                          }}"
                >
                  <Button class="view-btn" type="primary">查看详情</Button>
                </router-link>
              </div>
            </div>
          </div>
          <pagination
            v-if="total > 0"
            :total="total"
            :display="display"
            :current-page='current'
            @pagechange="pagechange">
          </pagination>
        </Col>
      </Row>
    </Card>
    <Card v-else>
      <!-- 暂无课程 -->
      <defaultShowCard :text="$t('Main.MyLessonList.NoLesson')"></defaultShowCard>
    </Card>
  </div>
</template>
<script>
  import panelTitle from "@/components/panelTitle";
  import pagination from "@/components/nm_pagination";
  import {Card, Row, Col, Button} from "iview";
  import Server from "@/service/request";
  import Path from "@/service/Path";
  import defaultCover from "@/assets/images/defaultCover.png";
  import defaultShowCard from "@/components/defaultShow";

  export default {
    name: "UserLessonList",
    components: {
      panelTitle,
      Card,
      Row,
      Col,
      pagination,
      Button,
      defaultShowCard
    },
    created() {
      if (this.$Utils.isLogin()) {
        this.organid = this.$Utils.getUserInfo().organid;
        this.teacherid = this.$Utils.getUserInfo().uid;
        this.getLessonData();
        if (this.type === "prev") {
          this.crumbs[this.crumbs.length - 1].name = this.$t("Main.MyLessonList.Ended");
        } else if(this.type === "active"){
          this.crumbs[this.crumbs.length - 1].name = this.$t("Main.MyLessonList.ForaClass");
        } else if(this.type === "next"){
          this.crumbs[this.crumbs.length - 1].name = '待上课';
        }
        this.crumbs[this.crumbs.length - 1].route = this.$route.fullPath;
      } else {
        this.$router.replace('/Login');
        return;
      }

    },
    data() {
      return {
        defaultCover,
        teacherid: 0,
        organid: 0,
        total: 0,
        current: 1,
        display: 10,
        date: this.$route.query.date || "",
        type: this.$route.query.type || "",
        _true: true,
        _false: false,
        mydata: [],
        crumbs: [
          {
            name: this.$t("Main.MyLessonList.Menu"),
            route: "/CalendarSchedule"
          },
          {
            name: this.$t("Main.MyLessonList.ForaClass"),
            route: ""
          }
        ]
      };
    },
    methods: {
      formatclass(type) {
        if (type === 1) {
          return this.$t("Main.ArrangMessanage.OneTo.Title"); // 一对一
        } else if (type === 2) {
          return this.$t("Main.ArrangMessanage.Mini.Title"); // 小班课
        } else if (type === 3) {
          return this.$t("Main.ArrangMessanage.Big.Title"); // 直播课
        }
      },
      //进教室
      enterRoom(organid, toteachid) {
        var a = document.createElement("a");
        a.setAttribute(`target`, `_blank`);
        a.setAttribute(
          `href`,
          `/teacher/enterClassMiddle/index.html?toteachid=${toteachid}`
        );
        a.click();
      },
      pagechange(currentPage) {
        this.getLessonData(currentPage);
        // ajax请求, 向后台发送 currentPage, 来获取对应的数据
      },
      getLessonData(page = 1) {
        Server.post(
          Path.getLessonsByDate,
          {
            date: this.date,
            pagenum: page,
            pagesize: this.display
          },
          res => {
            let {code, data} = res;
            if (code === 0 && data !== null) {
              this.mydata = [];
              this.mydata = data.data;
              this.total = data.pageinfo.total;
              this.current = data.pageinfo.pagenum;
              this.display = data.pageinfo.pagesize;
            }
          }
        ).catch(err => {
          throw err;
        });
      }
    },
    computed: {
      formatDate: function () {
        var date = this.date.split("-");
        return (
          date[0] +
          this.$t("Main.Year") +
          date[1] +
          this.$t("Main.Month") +
          date[2] +
          this.$t("Main.Day")
        );
      }
    }
  };
</script>
<style scoped lang="less">
  @import "~@/assets/less/variable";

  .fixTitle {
    .cardTitle {
      padding: 2px 0 16px 0;
      width: 100%;
      border-bottom: 1px solid @color-gray-bg;
      font-size: @font-size-large-s;
      color: @color-title;
    }
  }

  .evaluation-item {
    position: relative;
    border-bottom: 1px solid @color-gray-bg;
    box-sizing: content-box;
    padding: 40px 0;
    .eval-header-img {
      display: inline-block;
      width: 240px;
      height: 138px;
      margin-right: 46px;
      border-radius: 6px;
      overflow: hidden;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .eval-content {
      display: inline-block;
      vertical-align: top;
      ul {
        li {
          overflow: hidden;
          .eval-class-type {
            vertical-align: top;
            padding-top: 8px;
            font-size: @font-size-small;
            line-height: 1;
            color: @color-text;
            &:last-child {
              margin-right: 0;
            }
            &.student-number {
              padding-left: 20px;
            }
          }
          .coursename-label {
            width: 70px;
            display: table-cell;
          }
          .coursename-val {
            width: 200px;
            display: table-cell;
            word-break: break-all;
            word-wrap: break-word;
            white-space: pre-wrap;
          }
          .eval-name {
            padding-bottom: 22px;
            line-height: 1;
            font-size: @font-size-normal;
            color: #4D4D4D;
          }
          .eval-time {
            padding-bottom: 18px;
            color: #808080;
            line-height: 1;
            font-size: @font-size-small;
            &:last-child {
              margin-right: 0;
            }
          }
          .inline-block {
            display: inline-block;
          }
        }
      }
    }
    .eval-btngroup {
      padding: 20px 113px 0 0;
      @media screen and (max-width: 1366px) {
        padding: 20px 50px 0 0;
      }
      float: right;
      .view-btn,
      .status-btn,
      .enter-btn {
        display: block;
        width: 80px;
        height: 30px;
        padding: 0;
        margin: 0 0 34px 0;
        font-size: @font-size-small;
        border: 1px solid @color-theme;
        background: @color-white;
        color: @color-theme;
      }
      .view-btn {
        top: 60px;
      }
      .status-btn {
        top: 60px;
      }
      .enter-btn {
        top: 60px;
      }
    }
    &:last-child {
      border-bottom: none;
    }
  }
</style>



