<template>
  <div>
    <template>
      <Card dis-hover class='mytheme-noborder noradius-card mytheme-content-minheight'>
        <div class="classhour-title" v-if="data && data.length && data.length > 0">
          <span>共{{getUnitNum}}个单元</span>
          <span class="nbsp">/</span>
          <span>{{getUnitItemNum}}个课时</span>
        </div>
        <div class="course-hour-content" v-if="data && data.length && data.length > 0">
          <div class="class-time-box-item" v-for="data,index in data" :key="data.id">
            <dl>
              <dt>
                <div class="time-title-icon">
                  <div class="time-title-icon-q"></div>
                </div>
                <div class="item-title-number">第{{index+1}}单元：</div>
                <div class="edit-btn-box" v-if="data.list">共{{data.list.length}}课时</div>
                <div class="item-title-text" :title="data.unitname">{{data.unitname}}</div>
              </dt>
              <dd v-for="d,i in data.list" :key="d.id">
                <div class="inlineblock" :title="d.periodname">
                  课时{{i+1}}：{{d.periodname}}
                </div>
                <div class="inlineblock">{{d.timestr}}</div>
              </dd>
            </dl>
          </div>
        </div>
        <defaultShow v-else></defaultShow>
        
      </Card>
    </template>
  </div>
</template>

<script>
  import defaultShow from '@/components/defaultShow';
  import {Card, Input, Modal, Form, Row, Col} from "iview";

  export default {
    name: "CourseHour",
    components: {
      Card,
      Input,
      Modal,
      Form,
      Row,
      Col,
      defaultShow,
    },
    props: {
      data: {
        type: Array,
        default() {
          return []
        }
      }
    },
    computed: {
      getUnitNum() {
        return this.data.length;
      },
      getUnitItemNum() {
        let count = 0;
        this.data.forEach(el => {
          if (el.list) {
            count += el.list.length;
          }
        });
        return count;
      },
    },
  }
</script>

<style lang="less" scoped>

  .classhour-title {
    padding: 20px 0 35px 0;
    margin-left: 30px;
    font-size: 0;
    span {
      &.nbsp {
        padding: 0 10px;
      }
      font-size: 20px;
      display: inline-block;
    }
  }

  .course-hour-content {
    position: relative;
    &:before {
      content: ' ';
      position: absolute;
      top: 20px;
      left: 42px;
      width: 1px;
      height: 100%;
      background: #63AEEB;
      display: block;
    }

  }

  .class-time-box-item {
    padding-left: 60px;
    margin-left: 42px;
    dl {
      padding: 0 0 10px 0;
      dt,
      dd {
        border-width: 1px;
        border-style: solid;
        margin-bottom: 20px;
        padding: 0 24px;
        position: relative;
        div {
          &.inlineblock {
            display: inline-block;
            width: 360px;
            text-overflow: ellipsis;
            overflow: hidden;
            white-space: nowrap;
          }
          &.item-title-number {
            display: inline-block;
            vertical-align: top;
          }
          &.item-title-text {
            display: inline-block;
            max-width: 400px;
            text-overflow: ellipsis;
            overflow: hidden;
            white-space: nowrap;
          }
        }
      }
      dt {
        max-width: 1188px;
        background: #F2F2F2;
        border-color: #E6E6E6;
        border-radius: 2px;
        line-height: 50px;
        height: 50px;
        .time-title-icon {
          position: absolute;
          width: 24px;
          height: 24px;
          background: rgba(99, 174, 235, .5);
          left: -72px;
          top: 13px;
          border-radius: 50%;
          .time-title-icon-q {
            width: 14px;
            height: 14px;
            border-radius: 50%;
            background-color: #63AEEB;
            position: absolute;
            left: 0;
            top: 0;
            right: 0;
            bottom: 0;
            margin: auto;
          }
        }
        .edit-btn-box {
          float: right;
          margin-right: 15px;
          .time-edit-item {
            float: left;
            cursor: pointer;
            margin-right: 10px;
          }
        }
      }
      dd {
        margin: 0 0 22px 50px;
        max-width: 1138px;
        height: 44px;
        line-height: 44px;
        -webkit-border-radius: 2px;
        -moz-border-radius: 2px;
        border-radius: 2px;
        border-color: #E6E6E6;
        background: #FAFAFA;
      }
    }
  }
</style>



