<template>
  <div>
    <panelTitle :crumbs="crumbs"></panelTitle>
    <div class="mytheme-container-mg-30">
      <Tabs class="mytheme-tab" type="card" :animated="false">
        <TabPane label="课程信息">
          <basic-info :data="courseDetail"></basic-info>
        </TabPane>
        <TabPane label="课时信息">
          <course-hour :data="courseDetail.list"></course-hour>
        </TabPane>
        <TabPane label="学生详情">
          <student-info></student-info>
        </TabPane>
      </Tabs>
    </div>
  </div>
</template>

<script>
  import panelTitle from "@/components/panelTitle";
  import BasicInfo from "@/container/MyClasses/ClassDetail/BasicInfo";
  import CourseHour from "@/container/MyClasses/ClassDetail/CourseHour";
  import StudentInfo from '@/container/MyCLasses/ClassDetail/StudentInfo';

  import Server from '@/service/request';
  import Path from '@/service/Path';
  import {Tabs, TabPane,} from "iview";

  export default {
    name: "ClassDetail",
    components: {
      panelTitle,
      StudentInfo,
      CourseHour,
      BasicInfo,
      Tabs,
      TabPane,
    },
    data() {
      return {
        crumbs: [
          {name: '我的班级', route: this.$route.query.route}, // 我的班级
          {name: '班级详情', route: ''}            // 课程详情
        ],
        courseDetail: {},
      }
    },
    created() {
      if (this.$Utils.isLogin()) {
        this.initCrumbs();
        this.getCourseDetails();
      } else {
        this.$router.replace('/Login');
        return;
      }
    },
    methods: {
      initCrumbs() {
        this.crumbs[1].route = this.$route.fullPath;
      },
      getCourseDetails() {
        Server.post(Path.showSchedulCurriinf, {
          id: this.$route.query.claID,
        }, (res) => {
          let {code, info, data} = res;
          if (code === 0) {
            this.courseDetail = data;
          }
        });
      },

    },
  }
</script>

<style lang="less" scoped>

</style>



