<template>
	<div id="register">
		<div class="register-box-pc">
			<div class="center">
				<div class="login">
					<div class="login_title">老师注册</div>
					<div class="login_content">
            <div class="inputbox login-area">
              <AreaCode class="select-value" :editCode='86' @selectValue='selectValue'></AreaCode>
              <div class="input-wrap">
                <input type="text" onfocus="this.type='tel'" autocomplete="off" placeholder="请输入手机号" id="number" minlength="6" maxlength="15" v-model.trim="mobile">
              </div>
            </div>
						<div class="inputbox">
							<div class="input-icon password"></div>
							<div class="input-wrap">
								<input type="text" onfocus="this.type='password'" placeholder="请输入密码" id="password" autocomplete="off" v-model.trim="password">
							</div>
						</div>
						<div class="inputbox">
							<div class="input-icon code"></div>
							<div class="input-wrap sendcode">
								<input type="text" placeholder="请输入验证码" id="code" autocomplete="off" v-model.trim="SMSCode">
							</div>
							<div class="input-btn">
                <!-- 发送验证码 -->
								<button ref="sendcodebtn" id="sendcodebtn" @click="sendCode()">{{sendBtnText}}</button>
							</div>
						</div>
						<div class="submitbox">
							<button id="submit" @click="register()">注册</button>
						</div>
						<div class="ohterbox">
							已有帐号<a href="./login.html">去登录</a>
						</div>
					</div>
				</div>
			</div>
			<div class='fotter'>Copyright © 门课网 京ICP备17018423号-2</div>
		</div>
	</div>
</template>
<script type="text/javascript">
import Server from "@/service/request";
import Path from "@/service/Path";
import * as codeReg from "@/container/Login/codeReg.js";
import AreaCode from "@/components/areaCode";
import "@/assets/areaCode.css";

export default {
  name: "register",
  components: {
    AreaCode
  },
  data() {
    return {
      publickey: "",
      count: 59,
      prephone: 86,
      organid: "",
      mobile: "",
      password: "",
      sendBtnText: "发送验证码",
      clickAble: true,
      SMSCode: ""
    };
  },
  created() {
    //如果直接打开注册页，账号的organid为1，解绑状态账号，无操作权限
    this.organid = this.GetRequest(window.location.href)["organid"] || 1;
  },
  methods: {
    selectValue(prephone) {
      this.prephone = prephone;
    },
    checkMobile() {
      if (!this.mobile.replace(/\s+/g, "")) {
        //手机号不得为空
        this.$Message.error("请输入手机号");
        return false;
      }
      if (
        isNaN(this.mobile.replace(/\s+/g, "")) ||
        this.mobile.replace(/\s+/g, "").length < 6
      ) {
        //长度必须大于等于6，中国手机号必须为11位
        this.$Message.error("请输入正确的手机号格式");
        return false;
      }
    },
    checkPwd() {
      if (!this.password) {
        //密码不得为空
        this.$Message.error("请输入密码");
        return false;
      }
    },
    checkSMSCode() {
      if (!this.SMSCode) {
        //验证码不得为空
        this.$Message.error("请输入验证码");
        return false;
      }
    },
    register() {},
    /**
     * 发送验证码
     * @Author   qyh
     * @DateTime 2018-08-03
     * @version  2.0
     */
    sendCode() {
      if (!this.clickAble) {
        //点击后button为不可点击
        return;
      }
			if (!this.mobile.replace(/\s+/g, "")) {
        //手机号不得为空
        this.$Message.error("请输入手机号");
        return false;
      }
			if (
        isNaN(this.mobile.replace(/\s+/g, "")) ||
        this.mobile.replace(/\s+/g, "").length < 6
      ) {
        //长度必须大于等于6，中国手机号必须为11位
        this.$Message.error("请输入正确的手机号格式");
        return false;
      }
      this.sendReTime();
      this.clickAble = false;
      Server.post(
        Path.sendUpdatePassMsgNlog,
        {
          mobile: this.mobile.replace(/\s+/g, ""),
          prphone: this.prephone
        }, (res)=> {
          let {code, data, info} = res;
          if(code === 0) {
            this.$Message.success(info);
          }else{
            this.$Message.error(info);
          }
        });
      },
      //公钥加密
      wrap_encrypt(content) {
        let encryptobj = new JSEncrypt();
        encryptobj.setPublicKey(this.publickey);

        let result = "";
        let step = 117;
        let strlen = content.length;
        let count = Math.ceil(strlen / step);
        for (let i = 0; i < count; i++) {
          let chunk = content.substring(step * i, step * (1 + i));
          result += encryptobj.encrypt(chunk);
        }
        return result;
      },
      //生成随机字符串
      randomString(len, charSet) {
        charSet = charSet || "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
        let randomString = "";
        for (let i = 0; i < len; i++) {
          let randomPoz = Math.floor(Math.random() * charSet.length);
          randomString += charSet.substring(randomPoz, randomPoz + 1);
        }
        return randomString;
      },
      //获取公钥
      getKey() {
        Server.post(Path.getPublicKey, {}, (res) => {
          let {code, key, info} = res;
          if (code === 0) {
            this.publickey = key;
          } else {
            this.$Message.error(info);
          }
        }).catch(err => {
          throw err;
        });
      },
      /**
       * @Author   qyh
       * @DateTime 2018-08-03
       * @version  2.0
       * @param    {DomObject}   obj
       */
      sendReTime() {
        if(this.mobile === 0) {
          this.clickAble = true;
          this.sendBtnText  = '发送验证码';
          this.count = 59;
          return;
        } else {
          this.clickAble = false;
          this.sendBtnText = this.count;
          this.count--;
        }
        setTimeout(() => {
          this.sendReTime();
        }, 1000);
      },

		},
	}
</script>
<style lang="less" scoped>
    .login-area .select-control {
      padding-left: 0;
      width: 50px!important;
    }
	  input:focus {
      outline: none;
    }

    .register-box-pc {
      user-select: none;
    }

    .register-box-pc .fotter {
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 80px;
      line-height: 80px;
      text-align: center;
      font-size: 12px;
      color: #333;
    }

    .register-box-pc .center {
      width: 828px;
      height: 496px;
      background: url('../assets/login4@2x.png') no-repeat;
      margin: auto;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
    }

    .register-box-pc .login_title {
      color: #646464;
      font-size: 16px;
      margin-bottom: 32px;
      margin-left: 17px;
      font-weight: bold;
    }

    .register-box-pc .login {
      width: 268px;
      position: absolute;
      top: 88px;
      left: 92px;
    }

    .register-box-pc .intl-tel-input.separate-dial-code.allow-dropdown.iti-sdc-3 .selected-flag {
      width: 50px;
    }

    .register-box-pc .flag-container {
      height: 39px;
      text-align: center;
    }

    .register-box-pc .selected-flag .iti-flag {
      display: none;
    }

    .register-box-pc .intl-tel-input.separate-dial-code .selected-flag {
      background: none;
      height: 39px;

    }

    .register-box-pc .selected-flag:focus {
      outline: none;
    }

    .register-box-pc .selected-flag:hover {
      background: none;
    }

    .register-box-pc .intl-tel-input.separate-dial-code.allow-dropdown.iti-sdc-3 input, .intl-tel-input.separate-dial-code.allow-dropdown.iti-sdc-3 input[type=text],
    .intl-tel-input.separate-dial-code.allow-dropdown.iti-sdc-3 input[type=tel] {
      padding-left: 50px;
    }

    .register-box-pc .intl-tel-input.separate-dial-code .selected-dial-code {
      padding-left: 0;
      text-align: center;
      font-size: 12px;
      color: #646464;
    }

    .register-box-pc .iti-arrow {
      display: none;
    }

    .register-box-pc .intl-tel-input {
      margin-bottom: 13px;
    }

    .register-box-pc .inputbox {
      position: relative;
      margin-bottom: 13px;
      width: 258px;
      height: 39px;
      background: #F7F8FB;
      border-radius: 39px;
    }

    .register-box-pc .input-icon {
      display: inline-block;
      width: 12px;
      height: 14px;
      margin: 14px 0 0 34px;
    }

    .register-box-pc .password {
      background: url(../assets/icon_pwd@2x.png) no-repeat center;
      background-size: 100% 100%;
    }

    .register-box-pc .code {
      background: url(../assets/icon_code@2x.png) no-repeat center;
      background-size: 100% 100%;
    }

    .register-box-pc .input-wrap {
      position: absolute;
      left: 70px;
      right: 0;
      top: 0;
      bottom: 0;
      display: inline-block;
    }

    .register-box-pc .input-wrap input {
      width: 100%;
      height: 100%;
      border: none;
      background: none;
    }

    .register-box-pc .input-wrap.sendcode {
      right: 91px;
    }

    .register-box-pc .input-btn {
      position: absolute;
      right: 0;
      display: inline-block;
      width: 90px;
      height: 39px;
      overflow: hidden;
    }

    .register-box-pc .input-btn button {
      width: 100%;
      height: 100%;
      background: #688FF0;
      border-radius: 39px;
      border: none;
      font-size: 12px;
      color: #fff;
    }

    .register-box-pc .input-btn button:focus {
      outline: none;
    }

    .register-box-pc .submitbox {
      margin-top: 42px;
    }

    .register-box-pc .submitbox button {
      margin-left: 10px;
      width: 130px;
      height: 39px;
      border-radius: 39px;
      border: none;
      background: #688EF1;
      box-shadow: 0 6px 13px rgba(104, 142, 241, .3);
      color: #fff;
    }

    .register-box-pc .submitbox button:focus {
      outline: none;
    }

    .register-box-pc .ohterbox {
      margin-left: 30px;
      margin-top: 20px;
      font-size: 12px;
    }

    .register-box-pc .ohterbox a {
      color: #688FF0;
    }

    .register-box-pc #sendcodebtn,
    .register-box-pc #submit {
      cursor: pointer;
    }
</style>
<style>
  /*修改登录页手机号前缀样式 2018 08 03 qyh*/
  .select-control {
    height: 39px;
    margin-right: 0;
    padding: 0!important;
  }
  .ivu-select-selection {
    border: none;
  }
  .login-area .select-control {
    width: 70px!important;
    border: none;
  }
  .ivu-select-selection {
    height: 39px!important;
    background: transparent;
  }
  .ivu-select,
  .ivu-select-selected-value {
    height: 39px!important;
    line-height: 39px!important;
    font-size: 12px!important;
  }
  .ivu-select-selected-value {
    /*text-align: center;*/
  }
</style>



