<template>
  <div id="login-header">
    <div class="header-content">
      <div class="header-logo">
        <a href="/">
          <img src="../../assets/logo.png">
        </a>
      </div>
      <div class="header-title">教师登录</div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "login-header"
  }
</script>

<style scoped lang="less">
  @color-blue: #3F9BE7;

  #login-header {
    height: 120px;
    background: #fff;
    font-size: 0;
    .header-content {
      margin: 0 auto;
      width: 1190px;
      div {
        display: inline-block;
        &.header-logo {
          height: 60px;
          margin: 35px 0 0 0;
          img {
            height: 100%;
          }
        }
        &.header-title {
          vertical-align: top;
          margin: 40px 0 0 59px;
          padding: 7px 0 0 41px;
          height: 49px;
          font-size: 28px;
          border-left: 2px solid @color-blue;
          color: @color-blue;
          letter-spacing: 2px;
        }
      }
    }
  }
</style>



