<template>
  <div id="essayDetail">
    <panel-title :crumbs="crumbs"></panel-title>
    <Card style="padding:0 10%" class="mytheme-container-mg-30 noradius-card mytheme-noborder" dis-hover>
        <Row>
            <p class="title">{{CompositionData.title}}</p>
        </Row>
        <Row>
            <div class="right">
                <p>学生：{{CompositionData.nickname}}</p>
                <p>日期：{{stamp(CompositionData.addtime)}}</p>
            </div>
        </Row>
        <Row class="between">
            <div style="flex:1;margin-right:20px">{{CompositionData.content}}</div>
            <div>
                <Carousel
                    style="width:476px;"
                    :height="410"
                    v-model="value3"
                    :autoplay="setting.autoplay"
                    :autoplay-speed="setting.autoplaySpeed"
                    :dots="setting.dots"
                    :radius-dot="setting.radiusDot"
                    :trigger="setting.trigger"
                    :arrow="setting.arrow">
                    <CarouselItem v-for="(i,index) in transfer(CompositionData.imgurl || '')" :key="index" style="position: relative;">
                        <!-- <img :src="+i" style="width:100%;height:100%;" alt=""> -->
                        <img :src="'http://'+i" style="width:100%;height:100%;" alt="">
                        <div class="see" @click="handleView(i)">
                             <img :src="Zoom" alt="">
                        </div>
                    </CarouselItem>
                </Carousel>
            </div>
            <!-- {{transfer(CompositionData.imgurl)}} -->
        </Row>
        <Form>
            <FormItem label='老师评语：' style="width:100%;margin-top:30px;">
                <Select v-model="formData.reviewscore" style="width:200px;z-index:999 !important;">
                    <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
                </Select>
                <span v-if="$route.query.type==0" class="sign">{{text_||0}}/1000字</span>
                <span v-else  class="sign">{{stamp(teacherData.reviewtime)}}</span>
            </FormItem>
            <div class="input-box form-confrol form-confrol2" style="height: initial;" v-if='isShowEditor'>
              <wangeditor :getLength='getLength' :content='formData.commentcontent' :catchData="catchData" ></wangeditor>
            </div>
            <Row style="text-align: center;" class="mytheme-container-mg-30">
                <Button type="primary" @click="reviewComposition()">
                  <span v-if="$route.query.type==0">提交</span>
                  <span v-else>保存</span>
                </Button>
            </Row>
        </Form>
    </Card>
     <Card v-if="$route.query.type==1" class="mytheme-container-mg-30 noradius-card mytheme-noborder" dis-hover>
            <fieldset class="formHead">
                <legend style="padding: 0 30px;">学生评价</legend>
            </fieldset>
         <Form style="padding:0 205px" v-if="ishave==true">
            <FormItem label='评分：' style="width:100%;">
                 <Rate allow-half disabled v-model="studentData.reviewscore" class="rate"/>
                 <span style="color:#FF2F2F;margin:0 20px;" >{{studentData.hierarchy}}</span>
                 <span style="color:#808080;font-size:16px;">{{stamp(studentData.reviewtime)}}</span>
            </FormItem>
            <!-- <FormItem label='' style="width:100%;padding-left:50px;" class="lable">
                <span>评价细心</span>
                <span>批改严谨</span>
                <span>评价很有帮助</span>
            </FormItem> -->
            <FormItem label='学生评价：' style="width:100%;"></FormItem>
            <FormItem label='' style="width:100%;padding-left:70px;">{{studentData.commentcontent}}</FormItem>
        </Form>
         <p v-if="ishave==false" style="text-align: center;color:#808080;font-size:16px;padding:30px 0;">暂无数据</p>
    </Card>
     <!-- 图片放大区 -->
    <Modal v-model="visible">
        <div slot="header">图片预览</div>
        <!-- <img :src="+imageEssay" style="width: 100%"> -->
        <img :src="'http://'+imageEssay" style="width: 100%">
        <div slot="footer"></div>
    </Modal>
  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import Zoom from "./img/fangdajing.png";
import {
  Card,
  Button,
  Row,
  Col,
  Carousel,
  CarouselItem,
  Form,
  FormItem,
  Select,
  Option,
  Modal,
  Rate
} from "iview";
import wangeditor from "@/components/wangEditor";
import Server from "@/service/request";
import Path from "@/service/Path";

export default {
  name: "",
  components: {
    panelTitle,
    Card,
    Button,
    Row,
    Col,
    Carousel,
    CarouselItem,
    Form,
    FormItem,
    Select,
    Option,
    wangeditor,
    Modal,
    Rate
  },
  watch: {},
  beforeRouteLeave(to, from, next) {
    //当退出页面时将状态给改回来
    // 导航离开该组件的对应路由时调用
    // 可以访问组件实例 `this`
    if (this.$route.query.type == 0 && this.isSubmit == false) {
      this.compositionRegresses();
    }
    next();
  },
  methods: {
    getLength(text) {
      //获取富文本编辑内容的长度
      this.text_ = text;
    },
    getContent(data){
      let str = data
      str = str.replace(/<p>/igm,"");
      str = str.replace(/<\/p>/igm, "");
      str = str.replace(/<br>/igm, "");
      str = str.replace(/[&nbsp;]/igm, "");
      str = str.replace(/\s+/gim, "");
      return str;
    },
    // getContentLength(data){
    //   let str1 = data;
    //   str1 = str1.replace(/(\n)/g, "");
    //   str1 = str1.replace(/(\t)/g, "");
    //   str1 = str1.replace(/(\r)/g, "");
    //   str1 = str1.replace(/<\/?[^>]*>/g, "");
    //   str1 = str1.replace(/\s*/g, "");
    //   return str1;
    // },
    // 获取富文本编辑器内容
    catchData(value) {
      this.formData.commentcontent = value; //在这里接受子组件传过来的参数，赋值给data里的参数
      this.content = this.getContent(value);
      //console.log(this.content);
    },
    getCompositionData() {
      //获取要批阅的数据/查看数据

      let port = Path.getCompositionData;
      this.$route.query.type == 0
        ? (port = Path.getCompositionData)
        : (port = Path.seeReviewComposition);

      Server.post(
        port,
        {
          compositionid: this.$route.query.compositionid
        },
        res => {
          let { code, data } = res;
          if (code == 0) {
            //数据获取成功
            if (this.$route.query.type == 0) {
              //未批阅
              this.CompositionData = data[0];
              this.isShowEditor = true;
            } else {
              //已批阅
              this.CompositionData = data.composition[0];

              if (data.teacher.length) {
                //老师数据是否存在
                this.teacherData = data.teacher[0];
                this.formData.commentcontent = data.teacher[0].commentcontent;
                this.formData.reviewscore = data.teacher[0].reviewscore;
                this.getLength(data.teacher[0].commentcontent);
                this.isShowEditor = true; //编辑器的显示
              }
              if (data.student.length) {
                //学生评论数据是否存在
                this.ishave = true;
                this.studentData = data.student[0];
                this.studentData.reviewscore = Number(
                  this.studentData.reviewscore
                );
                switch (this.studentData.reviewscore) {
                  case 1:
                    this.studentData.hierarchy = "非常不满意";
                    break;
                  case 2:
                    this.studentData.hierarchy = "不满意，比较差";
                    break;
                  case 3:
                    this.studentData.hierarchy = "一般";
                    break;
                  case 4:
                    this.studentData.hierarchy = "比较满意";
                    break;
                  case 5:
                    this.studentData.hierarchy = "非常满意，无可挑剔";
                    break;
                }
              }
            }
          } else {
            this.CompositionData = {};
          }
        }
      ).catch(err => {
        throw err;
      });
    },
    transfer(img) {
      //图片格式的转换
      return img.split("~");
    },
    //查看图片
    handleView(name) {
      this.imageEssay = name;
      this.visible = true;
    },
    stamp(times) {
      //时间处理器
      let date = new Date(times * 1000);
      let Y = date.getFullYear() + "-";
      let M =
        (date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1) + "-";
      let D =
        (date.getDate() < 10 ? "0" + date.getDate() : date.getDate()) + " ";
      let h =
        (date.getHours() < 10 ? "0" + date.getHours() : date.getHours()) + ":";
      let m =
        (date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes()) +
        ":";
      let s =
        date.getSeconds() < 10 ? "0" + date.getSeconds() : date.getSeconds();
      return Y + M + D + h + m + s;
    },
    compositionRegresses() {
      //改变状态
      Server.post(
        Path.compositionRegresses,
        {
          compositionid: this.$route.query.compositionid
        },
        res => {
          this.getCompositionData();
          // console.log(res.info);
        }
      );
    },
    
    reviewComposition() {
      //console.log(2,this.content);
      //提交/编辑批阅信息
      let submitPath = Path.UpdateReviewComposition;
      if (this.$route.query.type == 0) {
        submitPath = Path.reviewComposition;
      } else {
        submitPath = Path.UpdateReviewComposition;
      }
      if (this.formData.reviewscore == "") {
        this.$Message.error("请输入老师评分");
        return false;
      }
      // if (this.text_ == 0) {
      //   this.$Message.error("请输入评语");
      //   return false;
      // }
      if(this.content == ""){
        this.$Message.error("请输入评语！");
        return;
      }
      if (this.text_ >= 1000) {
        this.$Message.error("评语长度过长！");
        return false;
      }
      Server.post(
        submitPath,
        {
          compositionid: this.$route.query.compositionid,
          reviewscore: this.formData.reviewscore,
          commentcontent: this.formData.commentcontent
        },
        res => {
          if (res.code == 0) {
            this.isSubmit = true;
            this.$router.push("/EssayMarking");
          }
        }
      );
    }
  },
  // mounted() {
  //   window.onbeforeunload = function() {
  //     //  这是用来设定一个时间, 时间到了, 就会执行一个指定的 method。
  //     setTimeout(onunloadcancel,1);
  //     return "真的离开?";
  //   };
  //   window.onunloadcancel = function() {
  //     this.compositionRegresses();
  //   };
  // },
  created() {
    if (this.$Utils.isLogin()) {
      //判断是否登录
      this.teacherid = this.$Utils.getUserInfo().uid;
      this.organid = this.$Utils.getUserInfo().organid;
    } else {
      this.$router.push("/Login");
      return;
    }
    this.crumbs = [
      //面包屑
      {
        name: "作文批改",
        route: "/EssayMarking"
      },
      {
        name: this.$route.query.type == 0 ? "未批阅" : "我的批阅",
        route: "/EssayMarking"
      },
      {
        name: "查看详情",
        route: ""
      }
    ];
    if (this.$route.query.type == 0) {
      this.compositionRegresses();
    } else {
      this.getCompositionData();
    }
  },
  data() {
    return {
      text_: "",
      organid: 0,
      teacherid: 0,
      crumbs: [
        {
          name: "作文批改",
          route: "/EssayMarking"
        },
        {
          name: "未批阅",
          route: "/EssayMarking"
        },
        {
          name: "查看详情",
          route: ""
        }
      ],
      CompositionData: {}, //作文信息
      studentData: {}, //学生信息
      teacherData: {}, //老师信息
      Zoom,
      imageEssay: "", //查看图片
      value3: 0,
      setting: {
        autoplay: false,
        autoplaySpeed: 2000,
        dots: "inside",
        radiusDot: true,
        trigger: "click",
        arrow: "never"
      },
      cityList: [
        {
          value: "A++",
          label: "A++"
        },
        {
          value: "A+",
          label: "A+"
        },
        {
          value: "A",
          label: "A"
        },
        {
          value: "B++",
          label: "B++"
        },
        {
          value: "B+",
          label: "B+"
        },
        {
          value: "B",
          label: "B"
        },
        {
          value: "C++",
          label: "C++"
        },
        {
          value: "C+",
          label: "C+"
        },
        {
          value: "C",
          label: "C"
        }
      ],
      formData: {
        reviewscore: "",
        commentcontent: ""
      },
      isShowEditor: false, //默认隐藏wangedit
      isSubmit: false, //判断是否提交
      visible: false, //图片是否放大展示
      ishave: false, //是否有学生评论
      content: ''
    };
  }
};
</script>
<style scoped lang="less">
.title {
  margin-top: 45px;
  text-align: center;
  font-size: 24px;
  font-family: MicrosoftYaHei;
  font-weight: 400;
  color: #333;
  line-height: 42px;
}
.right {
  float: right;
  font-size: 16px;
  font-family: MicrosoftYaHei;
  font-weight: 400;
  color: #333;
  line-height: 42px;
}
.between {
  display: flex;
}
.see {
  position: absolute;
  right: 0;
  bottom: 0;
  z-index: 1000;
  width: 43px;
  height: 43px;
  text-align: center;
  background: rgba(128, 128, 128, 1);
  opacity: 0.5;
  img {
    margin-top: 5px;
    cursor: pointer;
    width: 33px;
    height: 33px;
  }
}
.sign {
  float: right;
  line-height: 34px;
  font-size: 16px;
  color: #808080;
}
.formHead {
  text-align: center;
  font-size: 20px;
  font-family: MicrosoftYaHei;
  font-weight: 400;
  color: #4d4d4d;
  line-height: 42px;
  border: 0;
  border-top: dashed 1px #cccccc;
}
.lable {
  span {
    padding: 2px 10px;
    margin-right: 20px;
    border: 1px solid #ccc;
    border-radius: 10px;
    font-family: MicrosoftYaHei;
    color: #808080;
    // line-height:30px;
  }
}
</style>



