<template>
  <div>
    <panel-title :crumbs="crumbs"></panel-title>
    <Card class="mytheme-container-mg-30 mytheme-noborder noradius-card" dis-hover>
      <ul class="message">
        <li>
          <span>{{ $t('Main.WonderfulMoment.CourseName') + $t('Main.Symbols.Colon') }}</span>
          <p></p>
        </li>
        <li>
          <span>{{ $t('Main.WonderfulMoment.ClassName') + $t('Main.Symbols.Colon') }}</span>
          <p></p>
        </li>
        <li>
          <span>{{ $t('Main.WonderfulMoment.PeriodName') + $t('Main.Symbols.Colon') }}</span>
          <p></p>
        </li>
        <li>
          <span>{{ $t('Main.WonderfulMoment.Menu') + $t('Main.Symbols.Colon') }}</span>
          <p></p>
        </li>
      </ul>
    </Card>
  </div>
</template>

<script>
import panelTitle from '@/components/panelTitle';
import {Card} from 'iview';
export default {
  name: "MomentDetail",
  components: {
    panelTitle,
    Card
  },
  data() {
    return {
      crumbs: [{
        name: this.$t('Main.WonderfulMoment.Title'),
        route: '/WonderfulMoment'
      },{
        name: this.$t('Main.WonderfulMoment.MomentDetail'),
        route: this.$route.fullPath,
      }]
    }
  }
}
</script>

<style lang="less" scoped>
  .tip{
    font-size:14px;
  }
  .title {
    color: #5e6266;
    font-size: 18px;
    font-weight: normal;
  }
  .photo {
    overflow: hidden;
    width: 180px;
    height: 135px;
  }
  .message {
    padding: 46px 30px 0 0px;
    font-size: 16px;
  }
  .message li {
    display:flex;
    margin-bottom: 23px;
    line-height: 40px;
  }
  .message li > span {
    width: 200px !important;
    text-align: right;
    line-height: 40px;
    color: #7A8599;
  }
  .message li * {
    display: inline-block;
    vertical-align: top;
  }
</style>



