<template>
  <div class="login-content" :style="{height: contentHeight.height, minHeight: '539px',}">
    <div class="content-center">
      <div class="content-form">
        <div class="input-box">
          <div class="input-icon userid"></div>
          <div class="input-wrap">
            <input
              type="text"
              id="username"
              v-model.trim="username"
              placeholder="请输入手机号"
              onkeyup="this.value=this.value.replace(/[\u4e00-\u9fa5]/g,'')"
              autocomplete="off"
              autofocus="autofocus"
              @keyup.enter="login()"
            />
          </div>
        </div>
        <div class="input-box">
          <div class="input-icon password"></div>
          <div class="input-wrap">
            <input
              type="text"
              onfocus="this.type='password'"
              id="password"
              class="password"
              v-model.trim="password"
              placeholder="请输入密码"
              autocomplete="off" @keyup.enter="login()"
            />
          </div>
        </div>
        <div class="input-box">
          <button class="btn" id="Login" @click="login()">登&nbsp;&nbsp;&nbsp;&nbsp;录</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Server from "@/service/request";
import Path from "@/service/Path";

export default {
  name: "login-content",
  components: {},
  data() {
    return {
      publickey: "",
      prephone: 86,
      username: "",
      password: "",
      organId: "",
      contentHeight: {
        height: document.documentElement.clientHeight - 120 - 289 + "px"
      }
    };
  },
  created() {
    this.getKey();
    /**
     * 如果已经是登录状态，直接跳转到首页
     */
    if (this.$Utils.isLogin()) {
      let teachertype = JSON.parse(this.$loca.getItem("teacher_info"))
        .teachertype;
      if (teachertype.indexOf("1") == "-1") {
        this.$router.push("/EssayMarking");
      } else {
        this.$router.push("/CalendarSchedule");
      }
    }
  },
  mounted() {},
  methods: {
    /**
     * 获取手机号前缀
     * @Author   qyh
     * @DateTime 2018-08-03
     * @version  2.0
     * @param    {String}   prephone  prephone areaCode组件Emit出手机号前缀
     */
    selectValue(prephone) {
      this.prephone = prephone;
    },
    /**
     * 去注册，本页面跳转
     * @Author   qyh
     * @DateTime 2018-08-03
     * @version  2.0
     */
    goRegister() {
      window.location.href = location.origin + "/teacher/login/register.html";
    },
    /**
     * 忘记密码，新开页面
     * @Author   qyh
     * @DateTime 2018-08-03
     * @version  2.0
     */
    ForgetPwd() {
      window.open(location.origin + "/web/#/forgetpwd?platform=teacher");
    },
    login() {
      if (!this.username) {
        this.$Message.error("请输入手机号");
        return;
      }
      if (!this.password) {
        // 请填写登录密码
        this.$Message.error(this.$t("Main.Login.tip19"));
        return;
      }

      let key = this.randomString(10);
      let { prephone, username, password, organId } = this;
      let postData = this.wrap_encrypt(
        JSON.stringify({
          username: username,
          password: password,
          type: 1,
          source: "web",
          key: key,
          organid: organId,
          prephone: prephone
        })
      );

      Server.post(Path.login, { data: postData }, res => {
        let { code, data } = res;
        if (code === 0) {
          window.localStorage.setItem("teacher_key", key);
          window.localStorage.setItem("teacher_info", JSON.stringify(data));
          let teachertype = JSON.parse(this.$loca.getItem("teacher_info"))
            .teachertype;
          if (teachertype.indexOf("1") == "-1") {
            this.$router.push("/EssayMarking");
          } else {
            this.$router.push("/CalendarSchedule");
          }
        } else {
          this.$Message.error(res.info);
        }
      });
    },

    wrap_encrypt(content) {
      let encryptobj = new JSEncrypt();
      encryptobj.setPublicKey(this.publickey);

      let result = "";
      let step = 117;
      let strlen = content.length;
      let count = Math.ceil(strlen / step);
      for (let i = 0; i < count; i++) {
        let chunk = content.substring(step * i, step * (1 + i));
        result += encryptobj.encrypt(chunk);
      }
      return result;
    },
    //生成随机字符串
    randomString(len, charSet) {
      charSet =
        charSet ||
        "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
      let randomString = "";
      for (let i = 0; i < len; i++) {
        let randomPoz = Math.floor(Math.random() * charSet.length);
        randomString += charSet.substring(randomPoz, randomPoz + 1);
      }
      return randomString;
    },
    //获取公钥
    getKey() {
      Server.post(Path.getPublicKey, {}, res => {
        let { code, key, info } = res;
        if (code === 0) {
          this.publickey = key;
        } else {
          this.$Message.error(info);
        }
      }).catch(err => {
        throw err;
      });
    }
  }
};
</script>

<style scoped lang="less">
.login-content {
  background: url(../../assets/login_bg.png) repeat-x;
  .content-center {
    position: relative;
    width: 1190px;
    height: 100%;
    margin-left: auto;
    margin-right: auto;
    .content-form {
      float: right;
      margin: 60px 0 0 0;
      width: 390px;
      height: 460px;
      background: #fff;
      border-radius: 4px;
      box-shadow: 6px 0 16px hsla(240, 1%, 45%, 0.22);
      .input-box {
        position: relative;
        margin: 0 auto 49px auto;
        height: 48px;
        width: 330px;
        border: 1px solid #d9d9d9;
        &:first-child {
          margin-top: 109px;
        }
        &:last-child {
          border: none;
          margin-bottom: 0;
          margin-top: 61px;
        }
        .input-icon {
          display: inline-block;
          margin: 12px 0 0 16px;
          width: 22px;
          height: 22px;
          background-size: 100% 100%;
          &.userid {
            background: url(../../assets/icon_userid@2x.png) no-repeat center;
          }
          &.password {
            background: url(../../assets/icon_password@2x.png) no-repeat center;
          }
        }
        .input-wrap {
          position: absolute;
          left: 50px;
          right: 0;
          height: 100%;
          display: inline-block;
          input {
            width: 100%;
            height: 100%;
            border: none;
            font-size: 14px;
            color: #666;
          }
        }
        button {
          width: 100%;
          height: 42px;
          font-size: 16px;
          border: none;
          border-radius: 4px;
          background: #3f9ce8;
          color: #fff;
          cursor: pointer;
          &:focus {
            outline: none;
          }
          &:hover {
            background: #0080e8;
          }
        }
      }
    }
  }
}

input {
  border: none;
  &:focus {
    outline: none;
  }
}

.select-control {
  height: 39px;
  margin-right: 0;
  padding: 0 !important;
}

.ivu-select-selection {
  border: none;
}

.login-area .select-control {
  width: 70px !important;
  border: none;
}

.ivu-select-selection {
  height: 39px !important;
  background: transparent;
}

.ivu-select,
.ivu-select-selected-value {
  height: 39px !important;
  line-height: 39px !important;
  font-size: 12px !important;
}

.ivu-select-selected-value {
  text-align: center;
}

.select-control {
  padding-left: 0 !important;
}

.ivu-select-selection {
  padding-left: 0 !important;
  border: none !important;
  height: 39px;
  background-color: transparent !important;
  background: transparent !important;
}

a {
  text-decoration: none;
}

input::-webkit-input-placeholder {
  /*color: RGBA(204, 204, 204, 1);*/
  font-size: 14px;
  color: #666;
}

input:-moz-placeholder {
  /*color: RGBA(204, 204, 204, 1);*/
  font-size: 14px;
  color: #666;
}

input::-moz-placeholder {
  /*color: RGBA(204, 204, 204, 1);*/
  font-size: 14px;
  color: #666;
}

input:-ms-input-placeholder {
  /*color: RGBA(204, 204, 204, 1);*/
  font-size: 14px;
  color: #666;
}
</style>



