<template>
  <div id="ClassType">
    <panel-title :crumbs="crumbs"></panel-title>
    <div class="mytheme-container-mg-30">
      <Tabs class="mytheme-tab"
            type="card"
            :animated="false"
            v-model="topBarIndex"
            @on-click="getMaterData(1, topBarIndex)">
        <TabPane name='1' label="个人资源"></TabPane>
        <TabPane name='0' label="公共资源"></TabPane>
      </Tabs>
      <Card class="mytheme-mg-bottom-20 noradius-card mytheme-noborder folder-padding" dis-hover>
        <ul class="mater-box">
          <li @click="MaterModal = true;">
            <div class="mater-item">
              <div class="mater-img">
                <img :src="icon_addfolder"/>
                <div></div>
              </div>
              <div class="mater-name">新建文件夹</div>
            </div>
          </li>
          <li
            v-for="(el, index) in materData"
            :key="index"
            @mouseover="el.active = true"
            @mouseout="el.active = false"
          >
            <div
              class="mater-delete"
              v-if="el.active"
              @click="deleteModal(el.fileid,el.juniorcount)">
              <img src="./assets/img/delete.png">
           </div>
            <router-link
              :to="{
              path: '/MaterialInfo/'+ el.fileid,
              query: {prevText: el.showname, filetype: topBarIndex, route: $route.fullPath,}
            }">
              <div class="mater-item">
                <div class="mater-img">
               <img :src="MaterIcon"/>
             <div>（{{el.juniorcount}}{{$t('Main.MyLessonList.Count')}}）</div>
                </div>
                <div class="mater-name" :title="el.showname">{{el.showname}}</div>
              </div>
            </router-link>
          </li>
        </ul>
        <!-- 暂无文件夹 -->
        <!-- <defaultShow v-else :text="$t('Main.MaterMessanage.NoFolder')"></defaultShow> -->
      </Card>
    </div>

    <!--新建文件夹-->
    <Modal class="mytheme-modal" v-model="MaterModal" :scrollable="true">
      <div slot="header" class="modal-head">{{$t('Main.MaterMessanage.CreateFolder')}}</div>
      <div class="type-content modal-body">
        <Form class="mytheme-mg-top-20">
          <Row>
            <Col span="20" offset="2">
              <div class="input-box">
                <Form class="mytheme-form" ref="formValidate" :model="formValidate" :rules="ruleValidate"
                      :label-width="90" label-position="left">
                  <FormItem label="名称：" prop="materName">
                    <Input
                      class="input-max-height input-max-width-l"
                      v-model.trim="formValidate.materName"
                      @keydown.enter.native="handleSubmit('formValidate')"
                    />
                  </FormItem>
                </Form>
              </div>
            </Col>
          </Row>
        </Form>
      </div>
      <div class="modal-foot" slot="footer">
        <Button
          class="modal-foot-btn"
          :disabled="isDisabled"
          type="primary"
          @click="handleSubmit('formValidate');"
        >{{$t('Main.SaveBtn')}}
        </Button>
        <Button
          class="modal-foot-btn mytheme-btn"
          type="default"
          @click="cancel();
          MaterModal = false;"
        >{{$t('Main.CancelBtn')}}
        </Button>
      </div>
    </Modal>
    <!-- 删除 -->
    <Modal class="mytheme-modal" v-model="isShowDelModal" :scrollable="true">
      <div class="modal-head" slot="header">{{$t('Main.ModalTip')}}</div>
      <Form class="modal-body">
        <Row>
          <Col>
            <div class="input-box">{{deleteFileText}}</div>
          </Col>
        </Row>
      </Form>
      <div class="modal-foot" slot="footer">
        <Button
          class="modal-foot-btn mytheme-btn"
          type="primary"
          @click="deleteFile(); isShowDelModal= false;"
        >{{$t('Main.SaveBtn')}}
        </Button>
        <Button class="modal-foot-btn mytheme-btn" type="default" @click="isShowDelModal = false">{{$t('Main.CancelBtn')}}</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import MaterIcon from "./assets/img/MaterIcon.png";
import icon_addfolder from "@/assets/images/icon/icon_addfolder@2x.png";
import {
  Card,
  Input,
  Button,
  Modal,
  Form,
  FormItem,
  Row,
  Col,
  Tabs,
  TabPane
} from "iview";
import Server from "@/service/request";
import Path from "@/service/Path";
import defaultShow from "@/components/defaultShow";

export default {
  name: "",
  components: {
    panelTitle,
    Card,
    Input,
    Button,
    Modal,
    Form,
    FormItem,
    Col,
    Row,
    defaultShow,
    Tabs,
    TabPane
  },
  watch: {
    MaterModal(val) {
      this.handleReset("formValidate");
    }
  },
  methods: {
    deleteModal(id, num) {
      if (num > 0) {
        this.deleteFileText =
          this.$t("Main.AreYouSureWantToDelFolder") +
          this.$t("Main.Symbols.QuestionMark");
      } else {
        this.deleteFileText =
          this.$t("Main.AreYouSureWantToDel") +
          this.$t("Main.Symbols.QuestionMark");
      }
      this.fileId = id;
      this.isShowDelModal = true;
    },
    // 删除文件夹
    deleteFile() {
      let { fileId } = this;
      Server.post(
        Path.deleteFile,
        {
          fileid: fileId,
          fatherid: 0,
          filetype: this.topBarIndex
        },
        res => {
          if (res.code == 0) {
            this.getMaterData(1, this.topBarIndex);
            this.page = 1;
            this.isShowDelModal = false;
            this.materData = this.materData.filter((el, index) => {
              return el.fileid != fileid;
            });
          } else if (res.code === 20019) {
            this.$Message.error(
              this.$t("Main.MaterMessanage.ErrorFolderTip") +
                this.$t("Main.Symbols.ExclamationMark") +
                this.$t("Main.Symbols.ExclamationMark")
            );
            this.isShowDelModal = false;
          }
        }
      ).catch(err => {
        throw err;
      });
    },
    handleSubmit(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          this.isDisabled = true;
          this.ok();
        }
      });
    },
    handleReset(name) {
      this.$refs[name].resetFields();
    },
    //创建文件夹
    ok() {
      let { organid } = this;
      Server.post(
        Path.fileAdd,
        {
          showname: this.formValidate.materName,
          pagenum: 1,
          filetype: this.topBarIndex,
          usetype: 0,
        },
        res => {
          let { code, data } = res;
          this.MaterModal = false;
          if (code === 0) {
            this.isDisabled = false;
            this.materData = [];
            this.getMaterData(1, this.topBarIndex);
            this.MaterModal = false;
            this.formValidate.materName = "";
          }
          this.formValidate.materName = "";
        }
      ).catch(err => {
        throw err;
      });
    },
    cancel() {
      this.formValidate.materName = "";
    },
    getMaterData(page = 1, filetype) {
      this.materData = [];
      Server.post(
        Path.tch_getFileList,
        {
          filetype: filetype,
          pagenum: page,
          fatherid: 0,
          usetype: 0
        },
        res => {
          let { code, data } = res;
          if (code === 0 && data.data.length != 0) {
            var mater = data.data.map((el, index) => {
              el.active = false;
              return el;
            });
            if (page == 1) {
              this.materData = [...mater];
            } else {
              this.materData = [...this.materData, ...mater];
            }
            this.busy = false;
          } else {
            this.busy = true;
          }
        }
      ).catch(err => {
        throw err;
      });
    }
  },

  created() {
    if (this.$Utils.isLogin()) {
      this.teacherid = this.$Utils.getUserInfo().uid;
      this.organid = this.$Utils.getUserInfo().organid;
    } else {
      this.$router.push("/Login");
      return;
    }
    this.getMaterData(1, this.topBarIndex);
  },
  data() {
    return {
      MaterIcon,
      icon_addfolder,
      MaterModal: false,
      formValidate: {
        materName: ""
      },
      materData: [],
      loading: true,
      crumbs: [
        {
          name: this.$t("Main.MaterMessanage.Title"),
          route: "/MaterialList"
        }
      ],
      fileId: 0,
      isShowDelModal: false,
      deleteFileText: "",
      organid: 0,
      teacherid: 0,
      isDisabled: false,
      filetype: 0,
      topBarIndex: "1",
      ruleValidate: {
        materName: [
          { required: true, message: "文件夹名不得为空", trigger: "blur" }
        ]
      }
    };
  }
};
</script>
<style scoped lang="less">
.mater-box {
  padding: 20px;
  overflow: hidden;
  li {
    position: relative;
    width: 198px;
    height: 194px;
    float: left;
    &:hover {
      background: #ecf1ff;
    }
    .mater-item {
      position: relative;
      cursor: pointer;
      .mater-name {
        padding: 0 10px 0 10px;
        height: 48px;
        line-height: 48px;
        text-align: center;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        font-size: 14px;
        color: #666666;
      }
      .mater-img {
        width: 100%;
        height: 120px;
        text-align: center;
        font-size: 16px;
        img {
          margin: 32px auto 0 auto;
          width: 66px;
          height: 66px;
        }
        div {
          margin: 12px auto 0 auto;
          font-size: 12px;
          color: #808080;
        }
      }
    }
    .mater-delete {
      position: absolute;
      cursor: pointer;
      right: 5px;
      top: 5px;
      z-index: 3;
    }
  }
}

.ivu-card-head {
  span {
    font-size: 16px;
  }
}
</style>



