<template>
  <div class="detail-orders">
    <!--他的订单-->
    <Card class="mytheme-noborder noradius-card mytheme-card-padding" dis-hover>
      <div class="orders-content">
        <!--仅显示已支付的 orderstatus !== 0-->
        <div class="orders-item" v-if="el.orderstatus===20" v-for="(el, i) in orderlist" :key="i">
          <div class="item-head">
            <div class="head-ordernum">订单编号：{{el.ordernum}}</div>
            <div class="head-ordertime fr">下单时间：{{el.ordertime}}</div>
          </div>
          <div class="item-content">
            <div class="content-orderinfo">
              <div class="orderinfo-image">
                <img :src="el.imageurl" @error="_imageError">
              </div>
              <div class="orderinfo-baseinfo">
                <div class="baseinfo-title">{{el.coursename}}</div>
                <div class="baseinfo-item">
                  <span class="baseinfo-item-label">班级名称：</span>
                  <span class="baseinfo-item-val">{{el.classname}}</span>
                </div>
                <div class="baseinfo-item">
                  <span class="baseinfo-item-label">老师名称：</span>
                  <span class="baseinfo-item-val">{{el.teachername || el.nickname}}</span>
                </div>
                <div class="baseinfo-item">
                  <span class="baseinfo-item-label">支付方式：</span>
                  <span class="baseinfo-item-val">{{transferOrderPayment(el.paytype)}}</span>
                </div>
              </div>
            </div>
            <ul>
              <li>
                <p class="orderprice-label">课程总价：</p>
                <p class="orderprice-val">￥{{el.originprice}}</p>
              </li>
              <li>
                <p class="orderprice-label">优惠金额：</p>
                <p class="orderprice-val">￥{{el.discount}}</p>
              </li>
              <li>
                <p class="orderprice-label">订单价格：</p>
                <p class="orderprice-val">￥{{el.amount}}</p>
              </li>
            </ul>
          </div>
        </div>
      </div>
      <pagination :display="display" :currentPage="current" :total="total" @pagechange="emitEvent"></pagination>
    </Card>
  </div>
</template>

<script>
  import pagination from '@/components/nm_pagination';
  import {Card, Row, Col,} from 'iview';

  export default {
    name: "detail-orders",
    props: ['orderlist', 'display', 'current', 'total'],
    components: {
      Card, Row, Col, pagination,
    },
    methods: {
      transferOrderPayment(type) {
        let typeStr = '';
        if (type == 0) {
          typeStr = '其他';
        } else if (type == 1) {
          typeStr = '余额';
        } else if (type == 2) {
          typeStr = '微信';
        } else if (type == 3) {
          typeStr = '支付宝';
        } else if (type == 4) {
          typeStr = '银联';
        }
        return typeStr;
      },
      emitEvent(page) {
        this.$emit('pagechange', page);
      },
    },
  }
</script>

<style scoped lang="less">
  @import "~@/assets/less/mixin";
  @import "~@/assets/less/variable";

  .detail-orders {
    .orders-content {
      margin-top: 56px;
      .orders-item {
        margin-bottom: 50px;
        border: 1px solid #BFBFBF;
        -webkit-box-sizing: content-box;
        -moz-box-sizing: content-box;
        box-sizing: content-box;
        .item-head {
          padding: 0 30px;
          height: 50px;
          line-height: 50px;
          background: #F2F2F2;
          div {
            display: inline-block;
            &.head-ordertime {

            }
          }
        }
        .item-content {
          display: flex;
          .content-orderinfo {
            display: table-cell;
            padding: 35px 0 35px 30px;
            width: 640px;
            .orderinfo-image {
              display: inline-block;
              margin-right: 20px;
              width: 220px;
              height: 130px;
              border-radius: 4px;
              overflow: hidden;
              img {
                width: 100%;
                height: 100%;
              }
            }
            .orderinfo-baseinfo {
              display: inline-block;
              vertical-align: top;
              .baseinfo-title {
                font-size: @font-size-normal-s;
              }
              .baseinfo-item {
                padding-top: 15px;
                .baseinfo-item-label {

                }
                .orderprice-val {

                }
              }
            }
          }
          ul {
            display: flex;
            flex: 1;
            height: 206px;
            li {
              display: inline-block;
              flex: 1;
              height: 100%;
              border-left: 1px solid #BFBFBF;
              text-align: center;
              .orderprice-label {
                padding: 62px 0 40px 0;
                color: @color-text;
              }
              .orderprice-val {
                text-indent: -1em;
                color: @color-red;
              }
            }
          }
        }
      }
    }
  }

</style>



