<template>
  <div class="task-add">
    <panelTitle :crumbs="crumbs"></panelTitle>
    <Card
      class="mytheme-card mytheme-container-mg-30 mytheme-noborder noradius-card mytheme-card-padding"
      dis-hover>
      <taskBase
        ref="taskBaseComponent"
        :schedulingidArr="schedulingidArr"
        :lessidArr="lessidArr"
        :sum="classNum"
        @getLessonid="getLessonArr"
        @getHomeWorkContent="getHomeWorkContent"
        @selectDate="selectDate"
        @emptyLessonId="emptyLessonId"
      >
      </taskBase>
      <taskContent
        v-if="isShowTaskContent"
        :homeWorkConent="homeWorkConent"
        @addHomeWork="addHomeWork"
      >
      </taskContent>
    </Card>
  </div>
</template>
<script>
  import panelTitle from '@/components/panelTitle';
  import Server from '@/service/request';
  import Path from '@/service/Path';
  import taskBase from './TaskBase';
  import taskContent from './TaskContent';
  import {Card, Form, FormItem, Input, Select, Option, DatePicker, Button, Row, Col} from 'iview';

  export default {
    name: "task-add",
    components: {
      panelTitle,
      Card,
      Form,
      FormItem,
      Input,
      Select,
      Option,
      DatePicker,
      Button,
      taskBase,
      taskContent,
      Row,
      Col
    },
    data() {
      return {
        crumbs: [{name: '', route: '/',}, {name: '作业模板', route: ''}],
        homeWorkConent: [], //作业内容
        schedulingidArr: [],//班级id下拉列表
        lessidArr: [],      //课程id下拉列表
        classNum: 0,        //班级人数
        starttime: '',      //作业开始时间
        endtime: '',        //作业结束时间
        schedulingid: 0,    //班级id
        lessonid: 0,        //课程id
        isShowTaskContent: false,//是否显示作业内容
      }
    },
    created() {
      this.initCrumbs();
      this.getSchedulingArr();
    },
    watch: {
      lessonid(b) {
        if(b) {
          this.isShowTaskContent = true;
        } else {
          this.isShowTaskContent = false;
        }
      },
    },
    methods: {
      emptyLessonId() {
        this.lessonid = 0;
      },

      /**
       * 初始化面包屑
       */
      initCrumbs() {
        this.crumbs[0].name = this.$route.query.name;
        this.crumbs[0].route = this.$route.query.route;
        this.crumbs[1].route = this.$route.fullPath;
      },

      /**
       * 获取班级 id 下拉列表
       */
      getSchedulingArr() {
        Server.post(Path.getChoicelist, {
          schedulingid: '',
          lessonid: '',
          periodid: '',
        }, (res) => {
          let {code, data} = res;
          if (code === 0) {
            this.schedulingidArr = this.$Utils.objectToArray(data);
            this.asyncFn(this.schedulingidArr).then(res => {
              this.$refs.taskBaseComponent.routeFrom(); //调用子组件方法
            });
          }
        });
      },
      /**
       * 获取课程列表，获取班级id
       * @param data 班级id
       */
      getLessonArr(data) {
        this.lessidArr = [];
        let schedulingid = data, lessonid = '';
        this.schedulingid = data;
        Server.post(Path.getChoicelist, {
          schedulingid,
          lessonid: lessonid,
          periodid: '',
        }, (res) => {
          let {code, data} = res;
          if (code === 0) {
            this.lessidArr = this.$Utils.objectToArray(data);
            this.asyncFn(this.lessidArr).then(res => {
              this.$refs.taskBaseComponent.initSelectLesson(); //调用子组件方法
            })
          }
        });
      },
      /**
       * 获取作业内容，获取课程id
       * @param schedulingid 班级id
       * @param lessonid     课程id
       */
      getHomeWorkContent(schedulingid, lessonid, periodid) {
        this.lessonid = lessonid;
        Server.post(Path.getChoicelist, {
          schedulingid,
          lessonid: lessonid||'',
          periodid: periodid,
        }, (res) => {
          let {code, data} = res;
          if (code === 0) {
            this.homeWorkConent = data.exercise;
            this.classNum = data.sum;
          }
        });
      },
      /**
       * 异步函数
       */
      asyncFn(ready) {
        return new Promise((resolve, reject) => {
          if(resolve) {
            resolve(ready);
          } else {
            reject('GoodBye!');
          }
        });
      },
      /**
       * 布置作业
       */
      addHomeWork() {
        let {schedulingid, lessonid, starttime, endtime} = this;
        if (!this.schedulingid) {
          this.$Message.error('请选择课程');
          return;
        } else if (!this.lessonid) {
          this.$Message.error('请选择课时');
          return;
        } else if (!this.starttime || !endtime) {
          this.$Message.error('请选择作业开始结束时间');
          return;
        } else if (!this.homeWorkConent) {
          this.$Message.error('题库暂无数据');
          return;
        }
        Server.post(Path.arrangeHomework, {
          'schedulingid': schedulingid,
          'lessonsid': lessonid,
          'starttime': starttime,
          'endtime': endtime,
        }, (res) => {
          let {code, data, info} = res;
          if (code === 0) {
            this.$Message.success(info);
            this.$router.go(-1);//返回作业列表页
          } else {
            this.$Message.error(info);
          }
        });
      },
      /**
       * 选择作业开始结束时间
       * @param date [new Date(), new Date()]
       */
      selectDate(date) {
        if (date[0] == '') {
          return;
        }
        this.starttime = this.$Utils.formatDateStr(date[0], 'timestamp');
        this.endtime = this.$Utils.formatDateStr(date[1], 'timestamp');
      }
    },
  }
</script>
<style lang="less" scoped>

</style>



