<template>
  <div class="nm_content">
    <Layout>
      <Content>
        <router-view></router-view>
      </Content>
    </Layout>
  </div>
</template>

<script>
  import {Layout, Content} from 'iview'

  export default {
    name: "nm_content",
    components: {
      Layout,
      Content,
    }
  }
</script>

<style scoped lang="less">
  .nm_content {
    position: absolute;
    min-width: 1040px;
    top: 80px;
    left: 200px;
    right: 0;
    z-index: 1;
  }
</style>



