    Chapter1: {
      title: "Chapter 1 Product Overview",//第一章	产品概述
      section1: {
        title: "1.1 1.1Product Overview",//1.1 产品概述
        text1: "This document is mainly aimed to introduce the functions of registration, login, management of courses, students and orders for lecturers,so that the user can learn to use the platform of",
        code: "Course Website",
        text2: " in the shortest time. The ",
        text3: " has the final interpretation right to the document."
      },
      section2: {
        title: "1.2 Registration",//1.2	注册
        text1: "(1) You can register directly on the official website through the teacher login portal to enter the registration page, or through the invitation link when you receive it from the agency.",
        text2: "(2) (2)You need to enter your mobile number,the only identity,to register, each of which is only allowed to register once, supporting the numbers from mainland, overseas, Hong Kong, Macao and Taiwan."
      },
      section3: {
        title: "1.3 Login",//1.3 登录
        text: "After the registration is successful, you can log in with your mobile phone number and password when you log in again, retrieving the password on the login page."
      },
      section4: {
        title: "1.4 Binding unbinding agencies",//1.4 绑定机构与解绑机构
        sentence1:{
          KeyWord:"Binding agencies:",//绑定机构：
          text:"(1) Having not registered in the Course Website, you can register through the link or QR code sent by an agency and  become the teacher of the agency after successful registration."
        },
        text: "Teachers can only be bound to only one agency. On the condition that a teacher has an account of Course Website and does not work for any other agency, the agency can send an invitation link to the teacher, and the teacher can agree to join the agency.If the teacher work for another agency, there is a need to be untied to the former one before joining a new one. Except for individual teachers,  only can those who belong to a certain agency start classes and do other operations.",
        sentence2:{
          KeyWord:"Binding agencies:",//绑定机构：
          text:"If there is an agency's information on a teacher's personal homepage , the teacher can unbind the relations with the agency."
        },
      }
    },
    Chapter2: {
      title: "Chapter 2 Product Details",//第二章 产品详情
      section1: {
        title: "2.1 Personal Homepage",//2.1 个人主页
        text1: "On the personal homepage, you can view the edited information of your course, evaluation and students who have already been registered."
      },
      section2: {
        title: "2.2 My class schedule",//2.2 我的课表
        text1: "The teachers’ class schedule will be presented as a calendar, so you can clearly see your daily schedule and the details of those courses.",
        text2: "You can click on the class schedule to view the course details and enter the upcoming classes;",
        text3: "you can also view the course details and the student details for the courses that are not started and about to start, as well as add and modify the courseware."
      },
      section3: {
        title: "2.3 Material Management",//2.3 素材管理
        sentence:{
          text1:"You are allowed to create your own folders and upload courseware in the folder. To ensure the security of the courseware, an agency's administrator can delete all the courseware under the agency, while you can only delete the courseware that you uploaded.We support a variety of commonly used courseware, such as",
          KeyWord:"Excel、Word、PPT、PDF、TXT、MP3、MP4",
          text2:"and various types of images, to meet your needs."
        },
        text: "You can view all the courseware under the folder by clicking on the folder,and you can also upload,view and delete the courseware."
      },
      section4: {
        title: "2.4 My students",//2.4 我的学生
        text: "In Student Management, you can view student details who have purchased your course."
      },
      section5: {
        title: "2.5 My Orders",//2.5 我的订单
        text1: "You can view all student orders for your course.",
        text2: "Click 'View' to view the order details."
      },
      section6: {
        title: "2.6 My settings",//2.6 我的设置
        text1: "(1) You can set your own 1-on-1 appointment time in My settings. An agency can open a one-on-one course for you only when you have set the appointment time.",
        text2: "(2) You can edit your own details in My settings, and change the password and mobile phone number."
      },
      section7: {
        title: "2.7 My messages",//2.7 我的消息
        text: "You can check,view and delete all notification messages in My messages;"
      },
    },
    hint: "The above is the using guide for instructors of the Course Website and it is expected to have an assistance to you. If there is any problems when using the website, you can contact our customer service staff.",
    tel: "Contact number: 086-010-84918242",
    email: "Email: support@51menke.com"
