<template>
  <div>
    <panel-title :crumbs="crumbs"></panel-title>
    <div class="mytheme-container-mg-30">
      <Card class="mytheme-card-padding noradius-card mytheme-noborder" dis-hover>
        <template>
          <div class="mytheme-mg-top-20">
            <span class="mytheme-input-label">{{$t('Main.TaskManage.InputSearch') + $t('Main.Symbols.Colon')}}</span>
            <Input
              class="input-normal-width input-max-height"
              :placeholder="$t('Main.TaskManage.ExerciName')"
              v-model="periodname"
              @on-enter="getHomeWorkList(current, courseid, classid, periodname)"
            />
            <Button
              class="mytheme-mg-left-70"
              type="primary"
              @click="getHomeWorkList(current, courseid, classid, periodname)">搜索
            </Button>
          </div>
        </template>
        <template>
          <i-table
            class="mytheme-table noborder-table thead-max-height mytheme-mg-top-30 thead-max-height tbody-min-height"
            ref="selection"
            :disabled-hover="true"
            :stripe="true"
            :columns="columns"
            :data="data"
            :no-data-text="$t('Main.TaskManage.NoData')"
          ></i-table>
        </template>
        <template>
          <Pagination
            class="mytheme-mg-top-20"
            v-if="total > 0"
            :total="total"
            :current-page="current"
            :display="display"
            @pagechange="pagechange"
          ></Pagination>
        </template>
      </Card>
    </div>
    <template>
      <Modal class='mytheme-modal testModel' v-model="modalControl.isShowViewModal" width="800">
        <div class="modal-head" slot="header">{{reviewTitle}}</div>
        <div class="modal-body type-content">
          <TaskContentComponent :reviewData="reviewData" :isshowEdit="false" :isdisable="true"></TaskContentComponent>
        </div>
        <div slot="footer"></div>
      </Modal>
    </template>
  </div>
</template>

<script>
  import panelTitle from '@/components/panelTitle';
  import Pagination from '@/components/pagination';
  import TaskContentComponent from '@/container/TaskManage/TaskContent';
  import Server from '@/service/request';
  import Path from '@/service/Path';
  import {Card, Form, Input, Table, Row, Col, Checkbox, Modal, Button} from 'iview';

  export default {
    name: "TaskDetail",
    components: {
      panelTitle,
      Card,
      Form,
      Input,
      "i-table": Table,
      Row,
      Col,
      Checkbox,
      Modal,
      Pagination,
      Button,
      TaskContentComponent,
    },
    data() {
      return {
        crumbs: [
          {
            name: this.$t('Main.TaskManage.Title'),
            route: '/TaskManage'
          }, {
            name: this.$t('Main.TaskManage.TaskDetail'),
            route: '/TaskDetail'
          }
        ],      //面包屑
        total: 0,           //记录总条数
        display: 20,        //每页显示20条数据
        current: 1,         //当前页
        columns: [
          {
            title: this.$t('Main.TaskManage.Num'),
            align: "center",
            key: 'id'
          }, {
            title: this.$t('Main.TaskManage.ExerciName'),
            align: "center",
            key: 'periodname'
          }, {
            title: this.$t('Main.TaskManage.StartTime'),
            align: "center",
            render: (h, params) => {
              return h('div', this.$Utils.formatStampStr(params.row.starttime, 'yyyy-MM-dd HH:mm:ss'));
            }
          }, {
            title: this.$t('Main.TaskManage.TaskSta'),
            align: "center",
            key: 'type',
            render: (h, params) => {
              let that = this;

              function renderType(type) {
                if (type == 1) {
                  return that.$t('Main.TaskManage.ReadOver')
                } else if (type == 0) {
                  return that.$t('Main.TaskManage.NoFinished')
                }
              }

              return h('div', {}, renderType(params.row.reviewstatus));
            },
          }, {
            title: this.$t('Main.TaskManage.EndTime'),
            align: "center",
            render: (h, params) => {
              return h('div', this.$Utils.formatStampStr(params.row.endtime, 'yyyy-MM-dd HH:mm:ss'));
            }
          }, {
            title: this.$t('Main.TaskManage.RateNum'),
            align: "center",
            render: (h, params) => {
              return h('div', `${params.row.worknum}/${params.row.stunum}`);
            }
          }, {
            title: this.$t('Main.TaskManage.AverageScore'),
            align: "center",
            key: 'avgscore'
          }, {
            title: this.$t('Main.TaskManage.Operate'),
            align: "center",
            render: (h, params, index) => {
              return h('div', [
                h('div', {
                  on: {
                    'click': () => {
                      this.review(this.$route.query.classid, params.row.lessonid, params.row.periodid);
                      this.reviewTitle = params.row.periodname;
                      this.modalControl.isShowViewModal = true;
                    }
                  },
                  style: {
                    cursor: 'pointer',
                    color: '#f89240',
                    display: 'inline-block',
                    marginRight: '10px'
                  },
                  attrs: {
                    class: 'tablebtn blue'
                  }
                }, '预览'),
                h('a', {
                  style: {
                    cursor: 'pointer',
                    display: 'inline-block',
                    marginRight: '10px'
                  },
                  on: {
                    'click': () => {
                      if (params.row.reviewstatus == 1) {
                        this.$router.push({
                          path: '/TaskFinished',
                          query: {
                            parentName: this.crumbs[0].name,
                            parentRoute: this.crumbs[0].route,
                            name: this.$t('Main.TaskManage.TaskDetail'),
                            route: this.$route.fullPath,
                            curriculumid: params.row.courseid,
                            schedulingid: params.row.classid,
                            lessonsid: params.row.lessonid,
                          },
                        });
                      } else if (params.row.reviewstatus == 0) {
                        this.$router.push({
                          path: '/TaskDetailType',
                          query: {
                            ID: params.row.id,
                            parentName: this.crumbs[0].name,
                            parentRoute: this.crumbs[0].route,
                            name: this.$t('Main.TaskManage.TaskDetail'),
                            route: this.$route.fullPath,
                            curriculumid: params.row.courseid,
                            schedulingid: params.row.classid,
                            lessonsid: params.row.lessonid,
                            reviewstatus: params.row.reviewstatus,
                            periodname: params.row.periodname,
                          }
                        })
                      }
                    }
                  },
                  attrs: {
                    class: 'tablebtn blue'
                  }
                }, '进入')
              ])
            }
          }],     //表格表头定义
        data: [],           //表格填充数据
        modalControl: {
          isShowViewModal: false,
        },//弹窗
        periodname: '',     //课时名称搜索关键字
        courseid: this.$route.query.courseid,//课程id
        classid: this.$route.query.classid,  //班级id
        lessonid: this.$route.query.lessonid,//课时id
        reviewData: {},     //预览弹窗数据
        reviewTitle: '',
      }
    },
    created() {
      let {current, courseid, classid, periodname} = this;
      this.initCrumbs();
      this.getHomeWorkList(current, courseid, classid, periodname);
    },
    methods: {
      /**
       * 初始化面包屑
       */
      initCrumbs() {
        this.crumbs[0].name = this.$route.query.name;
        this.crumbs[0].route = this.$route.query.route;
        this.crumbs[1].route = this.$route.fullPath;
      },
      /**
       * 查询作业明细列表
       * @param page 目标页码
       * @param curriculumid 课程id
       * @param schedulingid 班级id
       * @param periodname   课时名称
       */
      getHomeWorkList(page = 1, curriculumid = '', schedulingid = '', periodname = '') {
        this.data = [];
        Server.post(Path.showLesssonsHomeList, {
          'pagenum': page,
          'curriculumid': curriculumid,
          'schedulingid': schedulingid,
          'periodname': periodname,
        }, (res) => {
          let {code, data} = res;
          if (code === 0) {
            this.data = data.data;
            this.total = data.pageinfo.total;
          }
        });
      },
      /**
       * 分页请求
       * @param current 目标页码
       */
      pagechange(current) {
        let {periodname, courseid, classid} = this;
        this.getHomeWorkList(current, courseid, classid, periodname);
        this.current = current;
      },
      /**
       * 查询预览数据
       * @param schedulingid 班级id
       * @param lessonid     课程id
       */
      review(schedulingid, lessonid, periodid) {
        Server.post(Path.getChoicelist, {
          'schedulingid': schedulingid,
          'lessonid': lessonid,
          'periodid': periodid,
        }, (res) => {
          let {code, data} = res;
          if (code === 0) {
            this.reviewData = data.exercise;
          }
        });
      },
    },
  }
</script>

<style lang="less" scoped>
  @import "~@/assets/less/variable";
</style>



