<template>
  <div>
    <panel-title :crumbs="crumbs"></panel-title>
    <div class="mytheme-container-mg-30">
      <Card class="mytheme-card mytheme-noborder noradius-card mytheme-content-minheight" dis-hover>
        <div class="tabCon">
            <Button class="edit-btn" type="primary" @click.native="isDisabled = false;" v-if="isDisabled">编辑</Button>
            <Form
              class="mytheme-form"
              ref="editData"
              :label-width="160"
              :model="editData"
              :rules="checkInfoValidata">
              <Row>
                <Col offset="6">
                  <!--老师头像-->
                  <FormItem :label="$t('Main.Setting.EditData.Avatar')+$t('Main.Symbols.Colon')">
                    <upload-picture
                      class="AvataVerticalAlign"
                      v-if="!isDisabled"
                      :tip="[3,1,1]"
                      @avatarChange="uploadSuccess"
                    >
                      <Avata
                        :avataImage="editData.imageurl"
                        :avataStyle="AvataStyle">
                      </Avata>
                    </upload-picture>
                    <Avata
                      class="AvataVerticalAlign"
                      v-else
                      :avataImage="editData.imageurl"
                      :avataStyle="AvataStyle">
                    </Avata>
                    <div v-if="!isDisabled" class="color-hui">*（图片格式支持jpg、png，需小于2M）</div>
                  </FormItem>
                </Col>
              </Row>
              <Row >
                <Col offset="6">
                  <!--昵称-->
                  <FormItem
                    class="form-confrol"
                    :label="$t('Main.Setting.EditData.NickName')+$t('Main.Symbols.Colon')"
                    prop="nickname">
                    <Input
                      v-if="!isDisabled"
                      class="input-max-width"
                      :maxlength="16"
                      v-model.trim="editData.nickname"
                      autocomplete="off"
                    />
                    <div class="label-val" v-else>{{editData.nickname}}</div>
                    <!-- 用户昵称长度不得大于30 -->
                    <!--<div v-if="!isDisabled" class="hui">{{$t('Main.Setting.EditData.NickNameTip')}}</div>-->
                  </FormItem>
                </Col>
              </Row>
              <Row>
                <Col offset="6">
                  <!--性别-->
                  <FormItem class="form-confrol" :label="$t('Main.Setting.EditData.Sex')+$t('Main.Symbols.Colon')">
                    <RadioGroup v-if="!isDisabled" v-model="editData.sex">
                      <Radio :label="1">
                        <span>{{$t('Main.Setting.EditData.Man')}}</span>
                      </Radio>
                      <Radio :label="2">
                        <span>{{$t('Main.Setting.EditData.Woman')}}</span>
                      </Radio>
                    </RadioGroup>
                    <div class="label-val" v-else>{{editData.sex | renderSex}}</div>
                  </FormItem>
                </Col>
              </Row>
              <Row>
                <Col offset="6">
                  <!-- 所在地 -->
                  <!-- 在更改国家菜单时，省级和城市列表的显示控制根据对应列表长度判断 -->
                  <FormItem class="form-confrol" :label="$t('Main.Setting.EditData.Location')+$t('Main.Symbols.Colon')">
                    <div v-if="!isDisabled">
                      <Select
                        style="max-width: 118px;"
                        v-model="countryId"
                        @on-change="getProvinceList(countryId);"
                      >
                        <Option
                          :value="item.id"
                          v-for="item in countryList"
                          :key="item.id"
                        >{{item.name}}
                        </Option>
                      </Select>
                      <Select
                        v-if="provinceList.length"
                        style="max-width: 118px;"
                        v-model="provinceId"
                        @on-change="getCityList(provinceId)"
                      >
                        <Option
                          :value="item.id"
                          v-for="item in provinceList"
                          :key="item.id"
                        >{{item.name}}
                        </Option>
                      </Select>
                      <Select
                        v-if="cityList.length"
                        style="max-width: 118px;"
                        v-model="cityId"
                      >
                        <Option
                          :value="item.id"
                          v-for="item in cityList"
                          :key="item.id"
                        >{{item.name}}
                        </Option>
                      </Select>
                    </div>
                    <div class="label-val" v-else>
                      <div v-if="getCity(countryId)">
                        <span>{{getCity(countryId)}}</span>
                        <span>{{getCity(provinceId)}}</span>
                        <span>{{getCity(cityId)}}</span>
                      </div>
                      <div v-else>
                        <span>未知</span>
                      </div>
                    </div>
                  </FormItem>
                </Col>
              </Row>
              <Row>
                <Col offset="6">
                  <!--生日-->
                  <FormItem
                    class="form-confrol mytheme"
                    :label="$t('Main.Setting.EditData.Birth')+$t('Main.Symbols.Colon')">
                    <DatePicker
                      class="input-max-width input-max-height"
                      v-if="!isDisabled" type="date" :placeholder="$t('Main.SelectTime')"
                      format="yyyy-MM-dd"
                      :value="editData.birth || `2010-01-01`"
                      @on-change="getBirth">
                    </DatePicker>
                    <div class="label-val" v-else>
                      {{editData.birth || `2010-01-01`}}
                    </div>
                  </FormItem>
                </Col>
              </Row>
              <Row>
                <Col offset="6">
                  <!--老师简介-->
                  <FormItem :label="$t('Main.Setting.EditData.Profile')+$t('Main.Symbols.Colon')">
                    <Input
                      class="input-max-width"
                      v-if="!isDisabled"
                      type="textarea"
                      :rows="5"
                      :maxlength="100"
                      v-model.trim="editData.profile"
                      autocomplete="off"
                    />
                    <div class="label-val" v-else>
                      <div v-if="editData.profile">{{editData.profile}}</div>
                      <div v-else>{{noprofile}}</div>
                    </div>
                    <div class="color-hui" style="margin-left: 240px" v-if="!isDisabled&&editData.profile!==null">{{editData.profile.length || 0}}/100</div>
                    <!--<div v-if="!isDisabled" style="color: #BDC2C8;font-size: 12px">-->
                      <!--{{$t('Main.Setting.EditData.ProfileTip')}}-->
                    <!--</div>-->
                  </FormItem>
                </Col>
              </Row>
              <Row v-if="!isDisabled">
                <Col class="mytheme-mg-bottom-40 mytheme-mg-top-20" offset="6" style="padding-left: 160px;">
                  <Button type="primary" @click="handleSubmitEdit('editData')">
                    {{$t('Main.Setting.EditData.SubmitBtn')}}
                  </Button>
                </Col>
              </Row>
            </Form>
        </div>
      </Card>
    </div>
  </div>
</template>

<script>
  import panelTitle from '@/components/panelTitle'
  import UploadPicture from '@/components/UploadPicture';
  import defaultAvatar from './assets/images/icon_default_avatar.png';
  import Avata from '@/components/Avata';
  import areaCode from "@/components/areaCode";
  import "@/assets/areaCode.css";
  import {
    Input,
    Form,
    FormItem,
    Radio,
    RadioGroup,
    Select,
    Option,
    DatePicker,
    Switch,
    Card,
    Row,
    Col,
    Button,
    Tabs,
    TabPane,
    Upload,
    Icon
  } from 'iview'

  import Server from '@/service/request';
  import requestPath from '@/service/Path';
  import lodash from 'lodash';
  import City from '@/Api/city';

  export default {
    name: "UserEdit",
    components: {
      Input,
      Form,
      FormItem,
      Radio,
      RadioGroup,
      Select,
      Option,
      DatePicker,
      'i-switch': Switch,
      Card,
      panelTitle,
      Row,
      Col,
      Button,
      Server,
      requestPath,
      Tabs,
      TabPane,
      Upload,
      Icon,
      UploadPicture,
      areaCode,
      Avata,
    },
    props: {},
    data() {
      const validatePassCheck = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(this.$t('Main.PleaseReEnterPwd')));
        } else if (value !== this.pwdData.password) {
          callback(new Error(this.$t('Main.PwdErrorTip') + this.$t('Main.Symbols.ExclamationMark')));
        } else {
          callback();
        }
      };
      return {
        AvataStyle: {
          margin: 0,
          width: '113px',
          height: '113px',
        },
        crumbs: [
          {name: this.$t('Main.Setting.EditData.Title'), route: ''},
        ],
        defaultAvatar,//默认头像
        tabList: [
          {name: this.$t('Main.Setting.EditData.Title')},
          {name: this.$t('Main.Setting.ChangePass.Title')},
          {name: this.$t('Main.Setting.ChangePhone.Title')}
        ],
        tabActiveIndex: 0,
        countryName: '',
        provinceName: '',
        cityName: '',
        //编辑资料
        editData: {},
        editLabel: {},
        countryList: [],
        provinceList: [],
        cityList: [],
        countryId: 0,
        provinceId: 0,
        cityId: 0,
        Path: '',
        organid: 0,
        userInfo: {},
        checkInfoValidata: {
          //昵称
          nickname: [
            {required: true, message: this.$t('Main.Setting.EditData.PleaseEnterNickName'), trigger: 'blur'}
          ],
        },
        isDisabled: true,
        noprofile: '该老师很懒，目前还没有留下任何信息...',
      }
    },
    created() {
      if (this.$Utils.isLogin()) {
        this.crumbs[0].route = this.$route.fullPath;
        this.getTeacherInfo();
        this.Path = requestPath.Upload_tencent;
      } else {
        this.$router.replace('/Login');
        return;
      }

    },
    mounted() {
      // console.log(City);
    },
    filters: {
      renderSex(val) {
        if (val == 1) {
          return '男';
        } else if (val == 2) {
          return '女';
        } else {
          return '未知';
        }
      }
    },
    methods: {
      getCity(id) {
        return City[id];
      },
      //获取区号
      selectValue(val) {
        this.pwdData.prphone = val;
      },
      back() {
        this.$router.go(-1);
      },
      //提交资料验证
      handleSubmitEdit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.submitEdit();
          } else {
            // this.$Message.error(this.$t('Main.Setting.PleaseCompolete'));
          }
        })

      },
      clickDebounce: lodash.debounce(function () {
        this.handleSubmitPwdC('pwdData');
      }, 1000),
      /**
       * 修改密码提交验证，新增函数防抖
       * @Author   qyh
       * @DateTime 2018-08-03
       * @version  2.0
       */
      handleSubmitPwdC: lodash.debounce(function () {
        this.$refs['pwdData'].validate((valid) => {
          if (valid) {
            this.submitPwd();
          } else {
            this.$Message.error(this.$t('Main.Setting.PleaseCompolete'));
          }
        })
      }, 500),
      /**
       * 修改手机号提交验证新增函数防抖
       * @Author   qyh
       * @DateTime 2018-08-03
       * @version  2.0
       */
      handleSubmitMobile: lodash.debounce(function () {
        this.$refs['mobileData'].validate((valid) => {
          if (valid) {
            this.submitMobile();
          } else {
            this.$Message.error(this.$t('Main.Setting.PleaseCompolete'));
          }
        })
      }, 500),
      getBirth(date) {
        this.editData.birth = date;
      },
      uploadSuccess(url) {
        this.editData.imageurl = url;
      },
      //  查询老师详情信息
      getTeacherInfo() {
        Server.post(requestPath.setting_teacherInfoEdit, {
          allaccountid: 1
        }, (res) => {
          if (res.code === 0) {
            this.editData = res.data;
            this.getCountryList();
            if (this.editData.country !== 0) {
              this.getProvinceList(this.editData.country);
            }
            if (this.editData.province !== 0) {
              this.getCityList(this.editData.province);
            }
          }
        }).catch(err => {
          throw err;
        });
      },
      // 查询国家
      getCountryList() {
        Server.post(requestPath.getCityList, {}, (res) => {
          res.data.forEach(item => {
            if (Number(item.id) === this.editData.country) {
              this.countryName = item.name;
              this.countryId = item.id; //绑定国家id，option :value 相等 默认显示
            }
          });
          this.countryList = res.data;
        }).catch(err => {
          throw err;
        });
      },
      // 查询地区
      getProvinceList(parentId) {
        /**
         * 更改国家菜单时，清空省级列表，清空省级id
         * 清空城市列表，清空城市id
         */
        this.provinceList = [];
        this.cityList = [];
        this.provinceId = 0;
        this.cityId = 0;
        Server.post(requestPath.getCityList, {
          parentid: parentId
        }, (res) => {
          if (res.data.length === 0) { //如果选择完国家省级列表返回为空数组，那么清空citylist
            this.cityList = [];
            this.cityId = 0;
          }
          for (let i = 0; i < res.data.length; i++) {
            if (Number(res.data[i].id) === this.editData.province) {//如果存在cityId，默认显示城市
              this.provinceName = res.data[i].name;
              this.provinceId = res.data[i].id;
              break;
            } else {//如果不存在，把下拉控件绑定的cityId清除
              this.provinceName = '';
              this.provinceId = 0;
            }
          }
          this.provinceList = res.data;
        }).catch(err => {
          throw err;
        });
      },
      // 查询城市
      getCityList(parentId) {
        Server.post(requestPath.getCityList, {
          parentid: parentId
        }, (res) => {
          for (let i = 0; i < res.data.length; i++) {
            if (Number(res.data[i].id) === this.editData.city) {//如果存在cityId，默认显示城市
              this.cityName = res.data[i].name;
              this.cityId = res.data[i].id;
              break;
            } else {//如果不存在，把下拉控件绑定的cityId清除
              this.cityName = '';
              this.cityId = 0;
            }
          }
          this.cityList = res.data;
        }).catch(err => {
          throw err;
        });
      },
      // 保存城市id
      saveCityId(id) {
        this.editData.cityId = id;
      },
      // 提交资料编辑
      submitEdit() {
        this.userInfo = JSON.parse(this.$loca.getItem('teacher_info'));
        let {imageurl, nickname, teachername, sex, country, province, city, birth, profile, accountstatus} = this.editData;
        let {countryId, provinceId, cityId} = this;
        if (this.countryList.length === 0) {
          this.countryId = 0;
        }
        if (this.provinceList.length === 0) {
          this.provinceId = 0;
        }
        if (this.cityList.length === 0) {
          this.cityId = 0;
        }
        Server.post(requestPath.setting_updateteacherMsg, {
          'imageurl': imageurl,
          'nickname': nickname,
          'sex': sex,
          'country': countryId,
          'province': provinceId,
          'city': cityId,
          'birth': birth || `2010-01-01`,
          'profile': profile || this.noprofile,
          'status': accountstatus,
          'organid': this.userInfo.organid,
          'teacherid': this.userInfo.uid
        }, (res) => {
          if (res.code === 0) {
            this.$store.store.commit('updateNickname', nickname);
            this.$store.store.commit('uploadHeadimg', imageurl);
            this.isDisabled = true;
            this.$Message.success(res.info);
          } else {
            this.$Message.error(res.info);
          }

        }).catch(err => {
          throw err;
        });
      },
    }
  }
</script>
<style>
  .form-confrol .ivu-select-selection {
    background-color: transparent !important;
  }

</style>
<style scoped>

  .tabCon {
    position: relative;
    padding-top: 70px;
  }

  .edit-btn {
    position: absolute;
    right: 0;
    top: 0;
    z-index: 2;
  }
  .AvataVerticalAlign {
    margin-top: -40px;
    display: inline-block;
    width: 113px;
    height: 113px;
  }

  .label-val {
    max-width: 300px;
    line-height: 36px;
    font-size: 16px;
    word-break: break-all;
    word-wrap: break-word;
  }

</style>



