<template>
  <div id="LessonDetails">
    <panel-title :crumbs="crumbs"></panel-title>
    <div class="mytheme-container-mg-30">
      <Tabs class="mytheme-tab" type="card" :animated="false">
        <TabPane label="课程信息">
          <CourseCover :data="lessonInfo.listPeriodinfo"></CourseCover>
        </TabPane>
        <TabPane label="课件作业">
          <CourseWare :ware="lessonInfo.ware" @addWareSuccess="getCourseInfo();"></CourseWare>
        </TabPane>
        <TabPane label="学生详情">
          <StudentList :data="lessonInfo" @addWareSuccess="getCourseInfo();"></StudentList>
        </TabPane>
        <TabPane label="课程回顾" v-if="getNowTime(endtime)">
          <PlayBack :playback="lessonInfoData" :lessonInfo="lessonInfo"></PlayBack>
        </TabPane>
      </Tabs>
    </div>
  </div>
</template>
<script>
  import panelTitle from "@/components/panelTitle";
  import CourseCover from "./CourseCover";
  import CourseWare from "./CourseWare";
  import StudentList from "./StudentList";
  import PlayBack from "./CoursePlayback";
  import StudentRating from "./StudentRating";
  import CourseTask from "./CourseTask";
  import {Card, Tabs, TabPane,} from "iview";
  import Server from "@/service/request";
  import requestPath from "@/service/Path";

  export default {
    name: "LessonDetails",
    components: {
      panelTitle,
      Card, Tabs, TabPane,
      CourseCover,
      CourseWare,
      StudentList,
      PlayBack,
      StudentRating,
      CourseTask,
    },
    created() {
      if (this.$Utils.isLogin()) {
        this.initCrumbs();
        this.getCourseInfo();
      } else {
        this.$router.replace('/Login');
        return;
      }
    },
    data() {
      return {
        crumbs: [
          {
            name: this.$t("Main.MyLessonList.Menu"), //我的课表
            route: "/CalendarSchedule"
          },
          {
            name: this.$t("Main.MyLessonList.ForaClass"), //今日课表
            route: ""
          },
          {
            name: this.$t("Main.MyLessonList.ClassHourDetails"), //课时详情
            route: "/"
          }
        ],
        valueCustomText: 4,
        lessonInfo: {
          listPeriodinfo: {
            coursename: null,
            courseimage: null,
            periodname: null,
            type: null,
            gradename: null,
            subhead: null,
            sum: 0
          },
          playback: {
            code: "30311",
            data: null,
            info: this.$t("Main.noData") //暂无数据
          },
          ware: []
        },
        starttime: this.$route.query.starttime,
        endtime: this.$route.query.endtime,
        commentList: [],
        lessonInfoData: "",
        theme3: "light",
        busy: false
      };
    },
    methods: {
      initCrumbs() {
        this.crumbs[0].route = this.$route.query.parentRoute;
        this.crumbs[1].name = this.$route.query.name;
        this.crumbs[1].route = this.$route.query.route;
        this.crumbs[2].route = this.$route.fullPath;
      },
      getNowTime(d2) {
        let date = new Date(),
          year = date.getFullYear(),
          month = date.getMonth() + 1,
          day = date.getDate(),
          hour = date.getHours(),
          minute = date.getMinutes();
        let dateStr = `${year}-${month}-${day} ${hour}:${minute}`;
        if (d2) {
          return (
            new Date(dateStr.replace(/-/g, "/")) > new Date(d2.replace(/-/g, "/"))
          );
        }
      },
      /**
       * 查询课时详情
       */
      getCourseInfo() {
        Server.post(
          requestPath.getPeriodinfo,
          {
            toteachtimeid: this.$route.query.toteachtimeid, //toteachtime表的主键id
            id: this.$route.query.id, //lessonsid
            starttime: this.$route.query.starttime, //开始时间
            endtime: this.$route.query.endtime, //结束时间
            date: this.$route.query.date, //当天日期
            type: this.$route.query.type //班型
          },
          res => {
            this.lessonInfo.listPeriodinfo = res.data.listPeriodinfo;
            this.lessonInfo.ware = res.data.ware.data;
            this.lessonInfoData = res.data.playback.data;
            this.lessonInfo.studentlists = res.data.studentlists;
          }
        ).catch(err => {
          throw err;
        });
      }
    }
  };
</script>
<style lang="less" scoped>
  .lessonMessage {
    width: 72px;
    font-size: 18px;
    font-family: PingFangSC-Medium;
    color: #387bff;
    line-height: 43px;
    font-weight: Medium;
    border-bottom: 2px solid #387bff;
  }

  .borderLine {
    width: 100%;
    height: 1px;
    background: #cfdce7;
    border-radius: 1px;
    margin-bottom: 40px;
  }
</style>



