<template>
  <div>
    <!--学生信息-->
    <template>
      <Card class='mytheme-mg-bottom-20 mytheme-card-padding noradius-card mytheme-noborder' dis-hover>
        <div class="mytheme-mg-top-20">
          <Button v-if="students && students.length && students.length > 0" type="primary" @click="sockectMsgModal=true">推送消息</Button>
          <!--<Button class="mg-left-44" type="primary">发送短信</Button>-->
        </div>
        <i-table
          class="mytheme-table noborder-table mytheme-mg-top-30 tbody-min-height thead-max-height"
          :columns="colData"
          :disabled-hover="true"
          :data="students"
          :stripe="true">
        </i-table>
        <pagination
          class="clearfloat mytheme-mg-top-20"
          v-if="students.length > 0"
          :total="total"
          :display="display"
          :current-page='current'
          @pageChange="pagechange"
        ></pagination>
      </Card>
    </template>

    <Modal class="mytheme-modal" width="600px" v-model="sockectMsgModal">
      <div class="modal-head" slot="header">App推送</div>
      <div class="modal-body">
        <Form class="modal-body-form" ref="formValidate" :model="formValidate" :label-width="110" :rules="ruleValidate">
          <FormItem label="推送标题：" prop="title">
            <Input class="input-max-width-l input-max-height" type="text" :maxlength="14" v-model="formValidate.title"/>
          </FormItem>
          <FormItem label="推送内容：" prop="content">
            <Input class="input-max-width-l" type="textarea" :maxlength="50" :rows="6" v-model="formValidate.content"/>
            <div>APP推送标题不能超过14个字，内容不能超过50个字。</div>
          </FormItem>
        </Form>
      </div>
      <div class="modal-foot" slot="footer">
        <Button class="modal-foot-btn mytheme-btn" type="primary" @click="handleSubmit('formValidate')">确定</Button>
        <Button class="modal-foot-btn mytheme-btn" type="default" @click="sockectMsgModal = false">取消</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
  import {
    Card,
    Input,
    Table,
    Button,
    Row,
    Col,
    Checkbox,
    Modal,
    Form,
    FormItem,
  } from "iview";
  import city from '@/Api/city';
  import pagination from "@/components/pagination";
  import Server from '@/service/request';
  import Path from '@/service/Path';

  export default {
    name: "StudentInfo",
    // props: ['students'],
    components: {
      Card,
      Input,
      "i-table": Table,
      Button,
      Row,
      Col,
      Checkbox,
      pagination,
      Modal,
      Form,
      FormItem,
    },
    data() {
      return {
        colData: [
          {key: 'id', title: '序号', align: 'center'},
          {key: 'nickname', title: '姓名', align: 'center'},
          {
            key: 'sex', title: '性别', align: 'center', render: (h, params) => {
              function renderSex(param) {
                //param: 0未知   1男   2女
                if (param == 0) {
                  return '----';
                } else if (param == 1) {
                  return '男';
                } else if (param == 2) {
                  return '女';
                }
              }

              return h('div', renderSex(params.row.sex));
            }
          },
          {key: 'mobile', title: '手机号', align: 'center'},
          {key: 'age', title: '年龄', align: 'center'},
          {
            title: '地区', align: 'center',
            render: (h, params) => {
              function renderArea(param) {
                if (!param) {
                  return '----';
                }
              }

              return h('div', [
                h('span', {
                  style: {
                    padding: '0 10px',
                  }
                }, city[params.row.country] || renderArea(params.row.country)),
                h('span', {
                  style: {
                    padding: '0 10px',
                    display: params.row.province ? 'ineline-block' : 'none',
                  }
                }, city[params.row.province]),
                h('span', {
                  style: {
                    padding: '0 10px',
                    display: params.row.city ? 'ineline-block' : 'none',
                  }
                }, city[params.row.city]),
              ]);
            }
          },
          {
            title: '个性签名', align: 'center', render: (h, params) => {
              function renderProfile(param) {
                if (param == '') {
                  return '这个人很懒，什么都没有留下...';
                }
              }

              return h('div', params.row.profile || renderProfile(params.row.profile));
            }
          },
        ],
        total: 0,
        display: 10,
        current: 1,
        students: [],
        sockectMsgModal: false,

        formValidate: {
          title: '',
          content: '',
        },

        ruleValidate: {
          title: [
            {required: true, message: '请填写标题', trigger: 'blur', type: 'string',}
          ],
          content: [
            {required: true, message: '请填写内容', trigger: 'blur', type: 'string',},
          ],
        },
      }
    },
    watch: {
      sockectMsgModal(val) {
        if(!val) {
          this.$refs.formValidate.resetFields();
        }
      },
    },
    created() {
      this.getCourseStudent(this.current);
    },
    methods: {
      /**
       * 获取学生数量
       * @return {number}
       */
      getStudentNum() {
        return this.students.length;
      },
      getCourseStudent(current) {
        Server.post(Path.periodStulist, {
          schedulingid: this.$route.query.claID,
          pagenum: current,
        }, (res) => {
          if (res.code === 0) {
            let data = res.data.data, arr = [];
            if(this.$Utils.isJson(res.data.data)) {
              for(let k in data) {
                arr.push(data[k]);
              }
              this.students = arr;
            } else if (this.$Utils.isArray(res.data.data)) {
              this.students = res.data.data;
            }
            this.total = res.data.pageinfo.total;
          }
        });
      },
      pagechange(current) {
        this.getCourseStudent(current);
      },
      getStudentIdArr(arr = []) {
        let tempArr = [], newArr = [];
        arr.forEach(el => {
          tempArr.push(el.id);
        });
        tempArr.forEach(el => {
          if(newArr.indexOf(el)==-1) {
            newArr.push(el);
          }
        })
        return newArr;
      },
      handleSubmit (name) {
        let that = this;
        this.$refs[name].validate((valid) => {
          if (valid) {
            that.groupSend();
          }
        })
      },
      /**
       * APP 消息推送
       */
      groupSend() {
        let studentarr = this.getStudentIdArr(this.students);
        Server.post(Path.noticeAll, {
          'studentarr': studentarr,
          'title': this.formValidate.title,
          'content': this.formValidate.content,
        }, (res) => {
          let {code, info} = res;
          if (code === 0) {
            this.$Message.success(info);
            this.sockectMsgModal = false;
          } else {
            this.$Message.error(info);
          }
        });
      },
    },
  }
</script>

<style lang="less" scoped>
  .title {
    color: #5e6266;
    font-size: 18px;
    font-weight: normal;
  }

  .mg-left-44 {
    margin-left: 44px;
  }
</style>



